@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.dashboard-wrap {
    .accom-wrap {
        .box-wrap {
            &.ranking {
                overflow: hidden;
                @media (max-width: 1100px) {
                    flex-direction: row !important;
                }
                @include mix.respond-to(terminal){
                    flex-direction: column !important;
                }
                &.type-2 {
                    .box {
                        &:first-of-type {
                            .tag-ranking {
                                color: #EA4F4F;
                            }
                        }
                    }
                    &.align-left {
                        .box {
                            text-align: left;
                        }
                    }
                }
                > .box {
                    &.round{
                        width: calc(33.33% - 1.2rem);
                        @include mix.respond-to(terminal){
                            width: 100%;
                        }
                    }
                    .tag-ranking {
                        padding: 0.8rem 1.4rem;
                        @include mix.txt-cap1;
                        color: #666;
                        font-weight: 700;
                        border-radius: 10rem;;
                        background-color: #F6F7FA;
                        &+.txt-wrap{
                            margin-top: 2.4rem;
                        }
                    }
                    .subject {
                        color: #999;
                        @include mix.text-overflow(1);
                        &+p {
                            margin-top: 1rem;
                        }
                    }
                    p {
                        min-height: 5.2rem;
                        @include mix.text-overflow(2);
                        @include mix.txt-body3;
                    }
                    &:first-of-type {
                        .tag-ranking{
                            color: #4E60FF;
                        }
                    }
                    .btn-purple{
                        width: 100%;
                        margin-bottom: 1rem;
                        color: #4E60FF;
                        border-radius: 5rem;
                        background-color: #E7EAFF;
                    }
                    .doughnut-wrap {
                        margin-top: 4rem;
                        height: 15rem;
                        overflow: hidden;
                        canvas {
                            transform: translateY(-25%);
                        }
                        .graph-label {
                            > em{margin-top: 1.5rem;}
                        }
                        +.btn-purple {
                            margin-top: 4rem;
                        }
                    }
                }
                &+.title {
                    margin-top: 14rem;
                }
            }
        }
        &.swiper-wrap {
            position: relative;
            .box-wrap__title {
                display:flex;
                align-items:center;
                gap:2rem;
                text-align: left;
                .btn-map {
                    display:flex;
                    align-items:center;
                    width:6.5rem;
                    height:3.8rem;
                    @include mix.txt-cap2;
                    color:#999999;
                    font-weight:500;
                    background:#FFFFFF;
                    border:.1rem solid #CCCCCC;
                    border-radius:.8rem;
                    justify-content: center;
                }
            }
            .swiper-btn-wrap {
                display: inline-block;
                position: absolute;
                right: 0;
                top: 0;
                padding: .7rem 1rem;
                background-color: #fff;
                border-radius: 10rem;
                @include mix.respond-to(mobile) {
                    display: none;
                }
                > div[class*="swiper-button"] {
                    display: inline-block;
                    width: 1.8rem;
                    height: 1.8rem;
                    left: initial;
                    right: initial;
                    top: initial;
                    bottom: initial;
                    position: static;
                    margin-top: initial;
                    vertical-align: middle;
                    &.swiper-button-disabled{
                        opacity: 0.3;
                    }
                }
                .swiper-button-prev {
                    &::after{
                        content: '';
                        width: 1.8rem;
                        height: 1.8rem;
                        display: inline-block;
                        background: url('../images/icon/ico_arr_prev.svg') 0 0 no-repeat;
                    }
                }
                .swiper-button-next {
                    &::after{
                        content: '';
                        width: 1.8rem;
                        height: 1.8rem;
                        display: inline-block;
                        background: url('../images/icon/ico_arr_next.svg') 0 0 no-repeat;
                    }
                }
                .swiper-pagination {
                    display: inline-block;
                    position: static;
                    width: auto;
                    margin: 0 .3rem;
                    @include mix.txt-cap1;
                    color: #999;
                    font-weight: 500;
                    .swiper-pagination-current {
                        color: #333;
                    }
                }
            }
        }
        &+h5.title {
            margin-top: 14rem;
            @include mix.respond-to(mobile) {
                margin-top: 10rem;
            }
        }
    }
}

.swiper {
    &.subject {
        .swiper-wrapper {
            @include mix.respond-to(mobile) {
                display: block;
            }
            .swiper-slide {
                display: flex;
                flex-wrap: wrap;
                align-items: flex-start;
                gap: 2.4rem;
                margin: 0;
                @include mix.respond-to(mobile) {
                    flex-direction: column;
                }
                .subject-wrap {
                    display: flex;
                    gap: 1.2rem;
                    flex-direction: column;
                    flex: 1;
                    position: relative;
                    margin: 0;
                    @include mix.respond-to(mobile) {
                        width: 100%;
                    }
                    &+.subject-wrap {
                        position: relative;
                        &::after {
                            content: '';
                            display: block;
                            position: absolute;
                            left: -2.4rem;
                            top: 2.4rem;
                            width: 2.4rem;
                            height: .1rem;
                            background-color: #ccc;

                        }
                        @include mix.respond-to(mobile) {
                            &::after {
                                display: none;
                            }
                        }
                    }
                    h6.tit {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 5rem;
                        padding: 0 1rem;
                        @include mix.txt-body4;
                        border-radius: .8rem;
                        background-color: #D7DAE2;
                        color: #666;
                        font-weight: 500;
                        text-align: center;
                        word-break:keep-all;
                        &+.box {
                            margin-top: 1.2rem;
                            &::before {
                                top: -2.4rem;
                                background-position: 50% -.3rem;
                            }
                        }
                    }
                    .box {
                        display: flex;
                        flex-direction: column;
                        position: relative;
                        padding: 2rem;
                        @include mix.txt-cap1;
                        &::before {
                            content: '';
                            position: absolute;
                            left: 0;
                            right: 0;
                            top: -1.2rem;
                            width: .2rem;
                            height: 100%;
                            margin: 0 auto;
                            background: url('../images/img/bg_dashed.png') 50% -5px repeat-y;
                            background-size: 2px auto;
                            z-index: -1;
                        }
                        p {
                            min-height: 4.4rem;
                            @include mix.text-overflow(2);
                            text-align: left;
                        }
                        & > span {
                            width: 100%;
                            margin-top: 1.8rem;
                            padding-top: 1.8rem;
                            border-top: 1px solid #eee;
                            text-align: left;
                            background: url('../images/icon/icon_aside.svg') 100% 100% no-repeat;
                            background-size: 1.8rem auto;
                            @include mix.txt-cap2;
                            &.very-excellent {
                                color: #9E40F1;
                            }
                            &.excellent {
                                color: #4D91E5;
                            }
                            &.normal {
                                color: #009F7A;
                            }
                            &.weak {
                                color: #FA7A16;
                            }
                            &.very-weak {
                                color: #FE675F;
                            }
                            &.none {
                                color: #999;
                            }
                        }
                    }
                }
                &+.swiper-slide {
                    @include mix.respond-to(mobile) {
                        margin-top: 2.4rem;
                    }
                }
            }
        }
    }
}