@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body {
    &.dark-mode {
        .voca-wrap, .classic-wrap {
            color:#FFFFFF;
            h4 {
                &.title {
                    &+.info-text {
                        color:#999999;
                    }
                    .round-box {
                        color:#FFFFFF;
                        background-color:#242424;
                        @include mix.respond-to(mobile) {
                            background:none;
                        }
                    }
                }
            }
            .tab-round {
                .tab-wrap {
                    border-color:#242424;
                    background-color:#242424;
                    li {
                        button {
                            &.active {
                                background-color:#444444;
                            }
                        }
                    }
                }
            }
            .btn {
                &.cart {
                    color:#FFFFFF;
                    border-color:#333333;
                    background-color:#333333;
                }
            }
            .bottom-wrap {
                .btn {
                    &.more {
                        color:#FFFFFF;
                        background-color:#333333;
                        border-color:#333333;
                    }
                }
            }
            .calendar-wrap {
                border-color:#242424;
                background-color:#242424;
                span {
                    color:#FFFFFF;
                }
            }
            .box {
                &.round {
                    .select-radio {
                        input {
                            &+label {
                                border-color:#333333;
                                background-color:#333333;
                            }
                            &:checked {
                                &+label {
                                    background-color:#13BE29;
                                }
                            }
                        }
                    }
                }
            }
            .box-wrap {
                .box {
                    border-color:#242424;
                    background-color:#242424;
                    .tit {
                        color:#FFFFFF;
                        &+.cont {
                            >span {
                                strong {
                                    color:#FFFFFF;
                                }
                            }
                            &:has(span + span) {
                                &::before {
                                    background-color:#666666;
                                }
                            }
                        }
                    }
                    &.round-bg {
                        .btn-wrap {
                            > .btn {
                                &.border {
                                    color:#FFFFFF;
                                    border-color:#333333;
                                    background-color:#333333;
                                }
                            }
                        }
                    }
                }
            }
            .edu-list {
                li {
                    a,
                    .complete {
                        color:#FFFFFF;
                        border-color:#242424;
                        background-color:#242424;
                        &::before {
                            background-color:#999999;
                        }
                        strong {
                            color:#FFFFFF;
                        }
                    }
                    .not-yet {
                        color:#666666;
                        border-color:#242424;
                        background-color:#242424;
                        &::before {
                            background-color:#666666;
                        }
                        strong {
                            color:#666666;
                        }
                        dl {
                            dt,
                            dd {
                                color:#666666;
                                &:before {
                                    background-color:#666666;
                                }
                            }
                        }
                    }
                }
            }
            .word-list {
                li {
                    > a {
                        color:#FFFFFF;
                        border-color:#242424;
                        background-color:#242424;
                        div {
                            color:#FFFFFF;
                        }
                        span {
                            color:#999999;
                        }
                    }
                }
            }
            .upper-area {
                .count-wrap {
                    dl {
                        dt {
                            color:#999999;
                        }
                    }
                }
                &.process {
                    .count-wrap {
                        dl {
                            background:#242424;
                            dt {
                                color:#FFFFFF;
                            }
                            dd {
                                color:#999999;
                            }
                        }
                    }
                }
                &.between {
                    .count-wrap {
                        dl {
                            background-color:#242424;
                            dt {
                                color:#FFFFFF;
                            }
                            dd {
                                color:#999999;
                                strong {
                                    color:#13BE29;
                                }
                            }
                        }
                    }
                }
                select {
                    color: #333;
                }
            }
            .input-box {
                .input-wrap {
                    background-color:transparent;
                }
            }
            .tab-content {
                .box {
                    &.round {
                        border-color:#242424;
                        background-color:#242424;
                        .select-wrap {
                            select {
                                color:#333333;
                            }
                        }
                    }
                }
            }
            .my-choice {
                background-color:#242424;
                .data {
                    li {
                        background-color:#333333;
                        color:#FFFFFF;
                    }
                }
            }
            .btn-aside {
                color:#999999;
            }
            .list-wrap {
                h5 {
                    &.sub-title {
                        color:#FFFFFF;
                    }
                }
            }
            .voca-list {
                > li {
                    border-color:#242424;
                    background-color:#242424;
                    .txt-wrap {
                        > strong {
                            > a {
                                &.hide {
                                    background-color:#333333;
                                }
                            }
                        }
                        .meaning {
                            div {
                                color: #fff;
                            }
                            &.hide {
                                &::before {
                                    background-color:#333333;
                                }
                            }
                        }
                    }
                    .level-wrap {
                        strong {
                            color: #999999;
                            background-color: #333333;
                        }
                    }
                    .ex-wrap {
                        .ex-list {
                            > li {
                                &.notice {
                                    background-color:#333333;
                                }
                                p {
                                    color:#999999;
                                }
                            }
                        }
                    }
                }
            }
            .exam-wrap {
                .no {
                    color:#FFFFFF;
                }
                .question {
                    border-color:#242424;
                    background-color:#242424;
                    .text {
                        .tit {
                            >span {
                                &.empty {
                                    border-color:#FFFFFF;
                                }
                            }
                        }
                    }
                }
                .answer {
                    ol {
                        &.multiple {
                            li {
                                input[type='radio']{
                                    &+label {
                                        color:#999999;
                                        border-color:#242424;
                                        background-color:#242424;
                                    }
                                    &:checked {
                                        &+label {
                                            color:#FFFFFF;
                                            border-color:#13BE29;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.ans {
                    &__wrong {
                        .answer {
                            ol {
                                &.multiple {
                                    li {
                                        input[type='radio']:checked{
                                            &+label {
                                                background:none;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .level-wrap {
                strong {
                    color: #999999;
                    background-color: #242424;
                }
            }
            .test-wrap {
                border-color:#242424;
                background-color:#242424;
                p {
                    color:#FFFFFF;
                }
            }
            .status-wrap {
                .round-box {
                    color:#999999;
                    background-color:#242424
                }
            }
            .table-cont {
                .btn {
                    background-color:transparent;
                    border:.1rem solid rgba(153,153,153,0.4);
                }
                .table {
                    table {
                        thead {
                            tr {
                                th {
                                    background-color:#242424;
                                    color:#999999;
                                }
                            }
                        }
                        tbody {
                            tr {
                                border-color:#333333;
                                td {
                                    color:#999999;
                                    &.subject {
                                        .tit {
                                            strong {
                                                color:#FFFFFF;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .viewer-wrap {
                .viewer {
                    &__body {
                        .btn-wrap {
                            .btn {
                                &.btn-next {
                                    border-color:#13BE29;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}