@use 'abstracts/variables' as var;
@use 'abstracts/variables' as mix;

.evaluation{
    width: 100%;
    &__exam{
        @include mix.txt-body4;
        font-weight: 600;
        color: #333;
        margin-bottom: 4.8rem;
        > span.num {
            margin-right: 0.8rem;
        }
        > em{
            color: #999;
            font-weight: 400;
        }
    }
    .example-wrap{
        width: 100%;
        margin-top: 2.8rem;
        > .row{
            width: 100%;
            display: flex;
            align-items: center;
            & + .row{
                margin-top: 1rem;
            }
            &:has(.list-add){
                > select{
                    flex: 1;
                }
            }
            &:has(.balloon-tit){
                align-items: flex-start;
            }
        }
        .list-add{
            width: 9rem;
            height: 6rem;
            border-radius: 0.8rem;
            background-color: #4E60FF;
            @include mix.txt-body4;
            color: #fff;
            margin-left: 1rem;
        }
        select{
            & + select{
                margin-left: 1rem;
            }
        }
        > .year{
            width: 100%;
            display: flex;
            align-items: center;
            margin-bottom: 2.8rem;
            > li{
                position: relative;
                button{
                    @include mix.txt-body2;
                    font-weight: 400;
                    color: #999;
                    &:disabled{
                        background-color: #eee;
                        color: #999;
                    }
                    &.active{
                        color: #333;
                        font-weight: 600;
                    }
                }
                & + li{
                    padding-left: 3rem;
                    margin-left: 3rem;
                    &::before{
                        content: '';
                        width: .1rem;
                        height: 1.4rem;
                        display: inline-block;
                        background: #ccc;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 0;
                    }
                }
            }
        }
        @include mix.respond-to(mobile){
            .row.select{
                flex-direction: column;
                > select{
                    width: 100%;
                    margin-left: 0;
                    margin-top: 1rem;
                }
            }
        }
    }
    .time-set{
        width: 100%;
        display: flex;
        align-items: center;
        margin-top: 1rem;
        > .input-basic{
            margin-right: 1rem;
            &:nth-of-type(2){
                margin-left: 1rem;
            }
        }
    }
    .count-wrap{
        width: 100%;
        display: flex;
        align-items: center;
        &:has(.count-tit){
            .input-basic{
                flex: 1;
            }
        }
        .count-tit{
            @include mix.txt-body2;
            font-weight: 600;
            color: #333;
            margin-right: 1rem;
            > em{
                font-weight: 400;
                color: #666;
            }
        }
        .conunt-btn{
            display: flex;
            align-items: center;
            margin-left: 1rem;
            > button{
                width: 6rem;
                height: 6rem;
                border-radius: 0.8rem;
                background-color: #4E60FF;
                position: relative;
                &::after{
                    content: '';
                    width: 2.4rem;
                    height: 2.4rem;
                    display: inline-block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    background-color:#FFFFFF;
                    mask-size:100% 100%;
                    mask-position:0 0;
                    mask-repeat:no-repeat;
                }
                &:disabled{
                    background-color: #eee;
                    color: #999;
                    cursor:default;
                    &::after {
                        background-color:#999999;
                    }
                }
                &.minus{
                    &::after{
                        mask-image: url('../images/icon/ico_count_minus.svg');
                    }
                }
                &.plus{
                    &::after{
                        mask-image: url('../images/icon/ico_count_plus.svg');
                    }
                }
                & + button{
                    margin-left: 1rem;
                }
            }
        }
        & + .count-wrap{
            margin-top: 1.8rem;
        }
        > .input-basic{
            > input{
                &[type="number"] {
                    padding-right:4rem !important;
                }
                &:read-only{
                    width: 100%;
                    @include mix.txt-body4;
                    font-weight: 400;
                    background-color: #fff;
                    padding: 2rem;
                }
            }
        }
    }
    .balloon-tit{
        width: 5rem;
        @include mix.txt-cap1;
        font-weight: 600;
        color: #999;
        text-align: right;
        padding: 1rem 1rem 0 0;
    }
    .balloon-wrap{
        width: calc(100% - 5rem);
        display: flex;
        flex-wrap: wrap;
        margin-top: -1rem;
        margin-left: -1rem;
        .balloon{
            width: auto;
            padding: 1.2rem 3.6rem 1.2rem 1.6rem;
            border-radius: 2.2rem;
            background: #F6F7FA;
            @include mix.txt-cap1;
            font-weight: 500;
            color: #666;
            margin-top: 1rem;
            margin-left: 1.2rem;
            position: relative;
            .delete{
                width: 1.6rem;
                height: 1.6rem;
                background: url('../images/icon/ico_balloon_delete.svg') 0 0 / 100% 100% no-repeat;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 1.6rem;
            }
        }
    }
}