@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.doughnut-wrap{
    width: fit-content;
    height: fit-content;
    position: relative;
    margin: 0 auto;
    .graph-label{
        position: absolute;
        top: 56%;
        left: 50%;
        transform: translate(-50%, -50%);
        > em{
            width: 100%;
            display: inline-block;
            @include mix.txt-body4;
            font-weight: 400;
            color: #999;
            text-align: center;
            margin-bottom: 0.8rem;
        }
        > strong{
            width: 100%;
            display: inline-block;
            @include mix.txt-tit3;
            font-weight: 700;
            color: #333;
            text-align: center;
        }
    }
}