@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body{
    &.dark-mode{
        .main{
            background-color: #111;
            &__visual{
                background: linear-gradient(180deg, rgba(0,0,0,1) -9.13%, rgba(0,27,91,1) 83.87%);
                &::before{
                    background: url('../images/img/bg_main_visual_center_dark.svg') 0 0 no-repeat;
                }
                @include mix.respond-to(mobile){
                    &::before{
                        background: url('../images/img/bg_main_visual_center_dark_mo.svg') 0 0 no-repeat;
                    }
                }
                .txt-wrap{
                    .sub-tit{
                        color: #EEE;
                    }
                    h2.tit{
                        color: #fff;
                    }
                    h3.tit{
                        color: var.$color-white;
                        > em{
                            color: #EEE;
                        }
                    }
                }
                .step-flow{
                    &::before{
                        background: url('../images/img/img_main_circle_pink.svg') 0 0 no-repeat;
                    }
                    &::after{
                        background: url('../images/img/img_main_circle_yellow.svg') 0 0 no-repeat;
                    }
                    .list{
                        background: linear-gradient(146deg, rgba(255, 255, 255, 0.18) 6.29%, rgba(255, 255, 255, 0.02) 90.14%);
                        .txt-box{
                            em{
                                color: #0d1238;
                            }
                            p{
                                color: var.$color-white;
                            }
                            > span{
                                color: var.$color-white;
                                background-color: linear-gradient(146deg, rgba(255, 255, 255, 0.18) 6.29%, rgba(255, 255, 255, 0.02) 90.14%);
                                backdrop-filter: blur(5px);
                            }
                        }
                        &:nth-child(1){
                            .txt-box{
                                em{
                                    background-color: #89c6f4;;
                                }
                            }
                        }
                        &:nth-child(2){
                            .txt-box{
                                em{
                                    background-color: #8F9CFF;;
                                }
                            }
                        }
                        &:nth-child(3){
                            .txt-box{
                                em{
                                    background-color: #b880f0;
                                }
                            }
                        }
                        &:nth-child(4){
                            .txt-box{
                                em{
                                    background-color: #F590DA;;
                                }
                            }
                        }
                    }
                }
                .scroll{
                    > em{
                        color: rgba(255, 255, 255, 0.5);
                    }
                    .circle{
                        .mouse{
                            border: 0.25rem solid rgba(255, 255, 255, 0.3);
                            .wheel{
                                background-color: rgba(255, 255, 255, 0.3);
                            }
                        }
                    }
                }
                .wave{
                    .wave-line{
                        background: url("../images/img/bg_main_wave_line.png") repeat-x;
                    }
                    .wave-page{
                        background: url("../images/img/bg_main_wave_page.png") repeat-x;
                    }
                }
            }
            &__cont{
                background-color: #111;
                .single{
                    &__txt{
                        .badge{
                            background-color: #3B1DFF;
                            color: var.$color-white;
                            > em{
                                color: #FFED2A;
                            }
                            &::after{
                                background: url('../images/icon/ico_badge_bottom_dark.svg') 0 0 no-repeat;
                            }
                        }
                        .sub-tit{
                            color: var.$color-white;
                        }
                        h3.tit{
                            color: var.$color-white;
                        }
                        .info-txt{
                            color: #eee;
                        }
                    }
                    &__link{
                        > button{
                            background-color: #242424;
                            &::after{
                                background-color: var.$color-white;
                            }
                            .link-tit{
                                color: #fff;
                                .badge{
                                    background-color: #494949;
                                    color: #fff;
                                }
                            }
                            .info-txt{
                                color: #ccc;
                            }
                            &:hover{
                                &::after{
                                    background-color: #fff;
                                }
                                .link-tit, .info-txt{
                                    color: #fff;
                                    .badge{
                                        background-color: #fff;
                                        color: #5b41ff
                                    }
                                }
                                &:nth-of-type(1){
                                    background: url('../images/img/bg_link_literature_hover.svg') 0 0 no-repeat;
                                }
                                &:nth-of-type(2){
                                    background: url('../images/img/bg_link_voca_hover.svg') 0 0 no-repeat;
                                }
                                &:nth-of-type(3){
                                    background: url('../images/img/bg_link_math_hover.svg') 0 0 no-repeat;
                                }
                            }
                        }
                    }
                }
                .danchoo{
                    .tit-box{
                        .sub-tit{
                            color: #EEE;
                        }
                        > h3{
                            color: var.$color-white;
                            em{
                                color: var.$color-white;
                            }
                        }
                    }
                    &__link{
                        > button{
                            background-color: #242424;
                            > h4{
                                color: #fff;
                                em{
                                    color: #aaa;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .card-sparkle {
        position: relative;
        &::after{
            content: "";
            position: absolute;
            top: -100%;
            left: -100%;
            width: 400%;
            height: 400%;
            background: linear-gradient(
                95deg,
                rgba(255, 255, 255, 0) 0%,
                rgba(255, 255, 255, 0.1) 40%,
                rgba(255, 255, 255, 0.2) 45%, /*   */
                rgba(255, 255, 255, 0.4) 50%, /*   */
                rgba(255, 255, 255, 0.2) 55%, /*   */
                rgba(255, 255, 255, 0.1) 60%, /*   */
                rgba(255, 255, 255, 0) 100%
            );
            transform: rotate(0deg);
            animation: sparkle-glow 7s ease-in-out infinite;
        }
    }
}