@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body{
    &.dark-mode {
        .profile-wrap {
            background-color: #111;
            .box-wrap {
                .box {
                    border-color: #242424;
                    background-color: #242424;
                }
            }
            h4.title {
                color: #fff;
            }
            h5.sub-title {
                color: #fff;
            }
            h6.box-title {
                color: #fff;
            }
            .section {
                &.basic-info {
                    .box-wrap {
                        .box {
                            &.round {
                                .txt-wrap {
                                    .name {
                                        color: #fff;
                                    }
                                }
                            }
                            + .box {
                                .input-box {
                                    .title {
                                        color: #999;
                                    }
                                    > .input-wrap {
                                        background-color: transparent;
                                        &.text {
                                            background-color: transparent;
                                            > strong {
                                                color: #fff;
                                            }
                                        }
                                    }
                                    + .input-box {
                                        &::before {
                                            background-color: #333;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.subject-interest {
                    .box-wrap {
                        .box {
                            .ranking-wrap {
                                dl {
                                    dd {
                                        color: #fff;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .grade-wrap {
                .title-wrap {
                    .tag {
                        color: #fff;
                        background-color: #3d3d3d;
                    }
                }
                ul.select-grade {
                    li {
                        .title {
                            color: #999;
                        }
                    }
                }
            }
            .table {
                &.grade {
                    table {
                        tbody {
                            tr {
                                td {
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
                &.ranking {
                    table {
                        tbody {
                            tr {
                                td {
                                    color: #999;
                                    &.light {
                                        color: #fff;
                                    }
                                }
                            }
                        }
                    }
                }
                table {
                    thead {
                        tr {
                            th {
                                color: #999999;
                                background-color: #333;
                            }
                        }
                    }
                    tbody {
                        tr {
                            border-bottom: 1px solid #333;
                        }
                    }
                }
            }
            .btn {
                &.type-2 {
                    border-color:#333333;
                    background-color: #333333;
                    color:#FFFFFF;
                }
                &.type-3 {
                    border-color:rgba(153,153,153,0.4);
                }
            }
            .modal {
                .table {
                    table {
                        thead {
                            tr {
                                th {
                                    background-color: #F6F7FA;
                                }
                            }
                        }
                        tbody {
                            tr {
                                border-bottom: 1px solid #eee;
                            }
                        }
                    }
                }
            }
        }
    }
}