@keyframes waveMoveLine {
    0% {
        background-position: 100% 0;
    }
    100% {
        background-position: 0 0;
    }
}
@keyframes waveMovePage {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 100% 0;
    }
}
@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(5rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
.fade-in {
    opacity: 0;
    transform: translateY(5rem);
    animation: fadeInUp 1s ease-out forwards;
}
@keyframes bounceDonuts {
    0% {
        transform: translate(0, 0);
    }
    25% {
        transform: translate(2.5rem, 0) translateY(-2.5rem);
    }
    50% {
        transform: translate(0, 0) translateY(-5rem);
    }
    75% {
        transform: translate(-2.5rem, 0) translateY(-2.5rem);
    }
    100% {
        transform: translate(0, 0);
    }
}
@keyframes bounceRibbon {
    0% {
        transform: translate(0, 0);
    }
    25% {
        transform: translate(2.5rem, 0) translateY(-2.5rem);
    }
    50% {
        transform: translate(0, 0) translateY(-5rem);
    }
    75% {
        transform: translate(-2.5rem, 0) translateY(-2.5rem);
    }
    100% {
        transform: translate(0, 0);
    }
}
@keyframes bounce {
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(0.5rem);
    }
    100% {
        transform: translateY(0);
    }
}
@keyframes wheel {
    0%{
        transform: translateY(0);
        opacity: 0;
    }
    40%{
        opacity: 1;
    }
    80%{
        transform: translateY(2.6rem);
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@keyframes sparkle-glow {
    0% {
        transform: translate(-100%, -100%) rotate(45deg);
    }
    100% {
        transform: translate(100%, 100%) rotate(45deg);
    }
}
@keyframes pop-in {
    0% {
        opacity: 0;
        transform: scale(0.8);
    }
    40% {
        opacity: 1;
        transform: scale(1.15);
    }
    60% {
        transform: scale(0.95);
    }
    80% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}
.card-pop {
    animation: pop-in 0.8s cubic-bezier(0.25, 0.8, 0.5, 1.4) forwards;
}
@keyframes sway {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(6.5deg);
    }
    50% {
        transform: rotate(0deg);
    }
    75% {
        transform: rotate(-5deg);
    }
    100% {
        transform: rotate(0deg);
    }
}