@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body {
    &.dark-mode {
        .dashboard-wrap, .classic-wrap {
            color:#FFFFFF;
            background:#111111;
            select {
                color:#333333;
            }
            .box-wrap {
                .box {
                    background-color:#242424;
                    .desc {
                        color:#999999;
                    }
                }
                &:has(.box + .box) {
                    &:has(.info) {
                        &:has(button.btn) {
                            .btn {
                                color:#FFFFFF;
                                background:#444444;
                            }
                        }
                    }
                    @include mix.respond-to(terminal){
                        &:has(.info) {
                            &:has(button.btn) {
                                display:block;
                                .box {
                                    &+.box {
                                        margin-top:2.4rem;
                                    }
                                }
                            }
                        }
                    }
                    @include mix.respond-to(mobile){
                        &:has(.info) {
                            &:has(button.btn) {
                                .box {
                                    display:block;
                                    text-align:center;
                                    .btn {
                                        margin-top:2rem;
                                    }
                                }
                            }
                        }

                    }
                }
            }
            .container {
                &__top {
                    .box-wrap {
                        .box {
                            .info {
                                .grade {
                                    color:#FFFFFF;
                                    background-color:#444444;
                                }
                            }
                            button {
                                &.func {
                                    color:#999999;
                                }
                            }
                        }
                    }
                }
                &__body {
                    .tab-round {
                        .days {
                            background:#242424;
                        }
                        .tab-wrap {
                            background:#242424;
                            li {
                                .btn {
                                    &.active {
                                        background:#4E60FF;
                                    }
                                }
                            }
                        }
                    }
                    .calendar-wrap {
                        background:#242424;
                        select {
                            color:#FFFFFF;
                        }
                        button {
                            &.btn-calendar {
                                &::after {
                                    background-color:#FFFFFF;
                                }
                            }
                            &[class^='btn__'] {
                                background-color:#FFFFFF;
                            }
                            &.btn-off {
                                background-color:#666666;
                            }
                        }
                        .btn-calendar {
                            .datepicker {
                                color:#FFFFFF;
                            }
                        }
                    }
                    .box-wrap {
                        .box {
                            &.dash-map {
                                dl:not(:last-child) {
                                    &::before {
                                        background-color:#333333;
                                    }
                                }
                            }
                            .btn-grey,
                            .btn-purple {
                                color: #FFFFFF;
                                background: #444444;
                            }
                        }
                    }
                    .swiper-wrap {
                        &.accom-wrap {
                            .swiper-btn-wrap {
                                background-color:#242424;
                                .swiper-pagination {
                                    .swiper-pagination-current {
                                        color:#FFFFFF;
                                    }
                                }
                            }
                        }
                    }
                    .swiper {
                        &.subject {
                            .swiper-wrapper {
                                .swiper-slide {
                                    .subject-wrap  {
                                        h6 {
                                            &.tit {
                                                color:#FFFFFF;
                                                background-color:#333333;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .learning-status {
                li {
                    border-color:#333333;
                    &::before {
                        background-color:#333333;
                    }
                    &::after {
                        background-color:#333333;
                    }
                    .info {
                        strong {
                            color:#FFFFFF;
                        }
                    }
                    &.learned {
                        &::before {
                            background-image:url('../images/icon/ico_learned_dark.svg');
                        }
                    }
                    &.learned-time {
                        &::before {
                            background-image:url('../images/icon/ico_learned_time_dark.svg')
                        }
                    }
                    &.test {
                        &::before {
                            background-image:url('../images/icon/ico_test_dark.svg')
                        }
                    }
                    &.test-time {
                        &::before {
                            background-image:url('../images/icon/ico_test_time_dark.svg')
                        }
                    }
                    &.my-rate {
                        &::before {
                            background-image:url('../images/icon/ico_my_rate_dark.svg')
                        }
                    }
                }
            }
            .learning-state {
                > .calendar {
                    table {
                        tbody {
                            tr {
                                border-color:#666666;
                                td {
                                    color:#999999;
                                    &[class*='bg__grade'] {
                                        color:#FFFFFF;
                                    }
                                    &.today {
                                        span {
                                            &::after {
                                                background:#FFFFFF;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .learning-info {
                .learning-state {
                    ul.info {
                        li {
                            strong {
                                color:#FFFFFF;
                            }
                        }
                    }
                    &+.learning-state {
                        border-color:#333333;
                        &::before {
                            background-color:#333333;
                        }
                    }
                }
            }
            .calendar-week {
                .day {
                    color:#999999;
                    border-color:#666666;
                    &[class*='bg__grade'] {
                        color:#FFFFFF;
                    }
                }
            }
            .text-info {
                background-color:#333333;
                button {
                    background-color:#242424;
                    border-color:#242424;
                }
            }
            .chart-wrap {
                &__title {
                    color:#FFFFFF;
                }
            }
        }
        .doughnut-wrap {
            .graph-label {
                > strong {
                    color: #fff;
                }
            }
        }
        .swiper {
            &.subject {
                .swiper-wrapper {
                    .swiper-slide {
                        .subject-wrap {
                            .box {
                                > span {
                                    border-top: 1px solid #333;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}