@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.voca-wrap, .classic-wrap {
    padding:6rem 0 14rem;
    color:#333333;
    @include mix.txt-body4;
    font-weight:500;
    @include mix.respond-to(terminal) {
        padding: 8.5rem 0 7rem;
    }
    .inner {
        position: relative;
        max-width: 128rem;
        margin: 0 auto;
        padding: 0 2rem;
    }
    h4.title {
        display:flex;
        align-items:center;
        @include mix.txt-tit1;
        font-weight:700;
        @include mix.respond-to(mobile){
            @include mix.txt-tit3;
        }
        .btn-arrow {
            width: 2.8rem;
            height: 2.8rem;
            margin-left: 1rem;
            background: url('../images/icon/ico_arr_down3.svg') 50% 50% no-repeat;
            background-size: 2.8rem auto;
            @media (max-width: 360px) {
                margin-left: 0.5rem;
            }
        }
        & + .info-text{
            margin-top: 1.6rem;
            color: #666;
            font-weight: 400;
            & + .upper-area {
                margin-top: 4.8rem;
                @media (max-width: 420px) {
                    margin-top:1.6rem;
                    &.between {
                        .count-wrap {
                            & + .btn-aside {
                                width:100%;
                                margin: 1.6rem 0 0 0;
                                text-align:left;
                            }
                        }
                    }
                }
            }
        }
        & + .btn-aside {
            position: absolute;
            right: 2rem;
            top: .5rem;
            @include mix.respond-to(mobile) {
                position:static;
                margin-top:1.2rem;
            }
        }
        .round-box {
            padding:1.4rem 1.8rem;
            @include mix.txt-body4;
            font-weight:500;
            margin-left: 2rem;
            > span {
                display:flex;
                align-items:center;
                margin-left:.5rem;
                @include mix.txt-cap2;
                font-weight:600;
                > em {
                    display:block;
                    margin-left:.5rem;
                    @include mix.txt-body4;
                }
                > strong {
                    display:block;
                    margin-right:.5rem;
                    color:#4E60FF;
                    @include mix.txt-body4;
                }
            }
            &:has(span + span) {
                > span {
                    &+span {
                        display:flex;
                        align-items:center;
                        strong {
                            color:#666666;
                            font-weight:500;
                        }
                        &::before {
                            content:'';
                            width:.1rem;
                            height:1.4rem;
                            margin:0 1.2rem;
                            background-color:#CCCCCC;
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                position:absolute;
                bottom:0;
                right:2rem;
                padding:0;
                background:none;
                padding:0;
                background:none;
            }
        }
        &:has(.round-box span+span) {
            @media (max-width: 420px) {
                display:block;
                .round-box {
                    margin:1.2rem 0 0 0;
                    @media (max-width: 420px) {
                        position:static;
                    }
                }
            }
        }
    }
    .level-wrap {
        display: flex;
        gap: 1.2rem;
        strong {
            @include mix.txt-cap1;
            padding: 1.2rem 1.6rem;
            color:#666666;
            background-color: #F6F7FA;
            border-radius: 2.2rem;
        }
        & + .test-wrap {
            margin-top: 4.8rem;
       }
    }
    .test-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 3rem;
        border-radius: 1.6rem;
        border: 0.1rem solid #eee;
        @include mix.respond-to(mobile){
            flex-direction: column;
        }
        p {
            width: 100%;
            color: #666;
            @media (min-width: 721px) {
                max-width: calc(calc(100% - 19.3rem));
            }
        }
        button {
            @include mix.respond-to(mobile){
                margin-top: 3rem;
            }
        }
        + .tab-mini {
            margin-top: 14rem;
            padding: 0;
            @include mix.respond-to(mobile) {
                margin-top: 10rem;
            }
        }
    }
    .tab__panels {
        position: static;
        border: 0 none;
        .tab__panels__box {
            padding: 0;
        }
    }
    .tab-round {
        display: block;
        margin-top: 5rem;
        padding: 0;
        .days {
            padding:1.3rem 1.4rem;
            color:#999999;
            @include mix.txt-cap1;
            background:#FFFFFF;
            border-radius:5rem;
            em {
                margin-left:.5rem;
                color:#4E60FF;
            }
        }
        .tab-wrap {
            display:inline-flex;
            align-items:center;
            padding:.4rem;
            background:#FFFFFF;
            border: .1rem solid #eee;
            border-radius:5rem;
            @media (max-width: 420px) {
                width: 100%;
            }
            li {
                font-weight: 400;
                &::before {
                    display: none;
                }
                button {
                    display:block;
                    padding:.8rem 2rem;
                    color:#999999;
                    @include mix.txt-body4;
                    font-weight:400;
                    &.active {
                        color:#FFFFFF;
                        background:#4E60FF;
                        font-weight:600;
                        border-radius:5rem;
                    }
                }
            }
        }
        @media (max-width: 420px) {
            display:block;
            .days {
                margin-top:1rem;
                text-align:center;
            }
            .tab-wrap {
                justify-content: center;
            }
        }
    }
    .tab-mini {
        font-weight:400;
        &:not(.tab-round) {
            margin-top: 14rem;
            padding: 0;
            @include mix.respond-to(mobile) {
                margin-top: 10rem;
            }
        }
        & + .tab-content {
            margin-top: 2.4rem;
        }
    }
    .tab-content {
        .box {
            &.round {
                display: flex;
                flex-wrap: wrap;
                gap: 4rem;
                padding: 4rem;
                border-radius: 1.6rem;
                border: 0.1rem solid #eee;
                @include mix.respond-to(terminal){
                    flex-direction: column;
                }
                .input-box {
                    width: calc((100% - 4rem) / 2);
                    @include mix.respond-to(terminal) {
                        width: 100%;
                    }
                    & + .input-box {
                        margin-top: 0;
                        &:nth-of-type(2) {
                            .title {
                                span {
                                    display: inline-block;
                                    margin-left: 0.5rem;
                                    font-weight: 400;
                                    color: #999;
                                    @media (max-width: 420px) {
                                        margin-top: 0.5rem;
                                        display: block;
                                        margin-left: 0;
                                    }
                                }
                            }
                            .input-wrap {
                                border: 0 none;
                                @media (max-width: 370px) {
                                    flex-direction: column;
                                    height: auto;
                                }
                            }
                        }
                    }
                }
                .select-wrap {
                    select {
                        width: 100%;
                    }
                }
            }
        }
        .paging {
            margin-top: 4rem;
            @include mix.respond-to(mobile){
                display: none;
            }
        }
    }
    .state-wrap {
        margin-top: -4.8rem;
        text-align: center;
        @include mix.respond-to(terminal) {
            margin-top: 5rem;
        }
        @media (max-width: 420px) {
            margin-top: 3rem;
        }
    }
    .calendar-wrap {
        min-width: 18rem;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        gap: .6rem;
        padding: 1.2rem 2.4rem;
        border: .1rem solid #eee;
        border-radius: 10rem;
        @media (max-width: 420px) {
            min-width: auto;
            width: 100%;
        }
        span {
            color: #666;
            @include mix.txt-body4;
        }
    }
    .status-wrap {
        display:flex;
        align-items:center;
        flex-wrap:wrap;
        gap:1.2rem;
        margin-top:2.4rem;
        @include mix.respond-to(mobile){
            margin-top:3.2rem;
        }
        .round-box {
            flex:0 0 auto;
            padding:1.2rem 1.6rem;
            @include mix.txt-cap1;
            font-weight:500;
            &::before {
                content:'#';
                margin-right:.3rem;
            }
        }
    }
    .box-wrap {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-top: 2.4rem;
        @include mix.txt-body4;
        gap: 2.4rem;
        .box {
            flex:1;
            padding:3.4rem 3rem;
            border:0.1rem solid #EEEEEE;
            @include mix.respond-to(terminal2){
                flex: 0 1 auto;
                width: calc((100% - 2.4rem) / 2);
            }
            @include mix.respond-to(terminal){
                width: 100%;
            }
            &.round {
                border-radius:1.6rem;
                &-bg {
                    flex-direction:column;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;
                    padding:4rem 3rem;
                    border-radius: 2rem;
                    background-color: #F6F7FA;
                    border:0;
                    h5 {
                        @include mix.txt-body1;
                        font-weight:700;
                    }
                    > span {
                        margin-top: 1.2rem;
                        color:#666666;
                        @include mix.txt-body4;
                        font-weight:500;
                    }
                    .btn-wrap {
                        width:auto;
                        margin-top: 2.8rem;
                        > .btn {
                            display:block;
                            width: 16rem;
                            padding:2rem 0;
                            color:#FFFFFF;
                            @include mix.txt-body4;
                            background-color:#4E60FF;
                            border-radius:5rem;
                            border:.1rem solid #4E60FF;
                            text-align:center;
                            &.border {
                                color:#4E60FF;
                                @include mix.txt-body4;
                                font-weight:600;
                                background:transparent;
                                border:.1rem solid #4E60FF;
                            }
                        }
                        &:has(.btn + .btn) {
                            .btn {
                                width:21rem;
                                &+.btn {
                                    margin-left:1.6rem;
                                }
                            }
                        }
                        @include mix.respond-to(mobile){
                            display:block;
                            width: 100%;
                            &:has(.btn + .btn) {
                                .btn {
                                    width:100%;
                                    &+.btn {
                                        margin:1.2rem 0 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .cont {
                position:relative;
                display: flex;
                justify-content: space-between;
                > span {
                    padding-left:7.6rem;
                    color:#666666;
                    font-weight:500;
                    line-height:1.6rem;
                    strong {
                        display:block;
                        color:#333333;
                        @include mix.txt-body3;
                        font-weight:600;
                        > em{
                            @include mix.txt-body2;
                            font-weight: 600;
                            color: #999;
                        }
                        &[class^='ico-'] {
                            display: flex;
                            align-items: center;
                            cursor: pointer;
                            &::after {
                                content: '';
                                width: 2.8rem;
                                height: 2.8rem;
                                display: inline-block;
                                margin-left: 0.8rem;
                                background-size:100% 100%;
                                background-repeat:no-repeat;
                                @include mix.respond-to(mobile){
                                    width:2.4rem;
                                    height:2.4rem;
                                }
                            }
                        }
                        &.ico-mark{
                            position: relative;
                            &::after{
                                background-image: url('../images/icon/ico_mark.svg');
                            }
                            @include mix.respond-to(mobile){
                                .tooltip-box{
                                    min-width: auto;
                                    width: 22rem;
                                }
                            }
                        }
                    }
                    br {
                        display: none;
                    }
                    &::before {
                        content:'';
                        display:block;
                        position:absolute;
                        top:50%;
                        left:0;
                        width:5.6rem;
                        height:5.6rem;
                        background:#F6F7FA url('../images/icon/ico_book2.svg') center center no-repeat;
                        border-radius:2.8rem;
                        background-size:3.2rem 3.2rem;
                        transform: translateY(-50%);
                    }
                }
                .btn {
                    padding:1.4rem 2rem;
                    color:#FFFFFF;
                    @include mix.txt-cap1;
                    background-color:#4E60FF;
                    border-radius:5rem;
                    font-weight:600;
                    border:0.1rem solid #4E60FF;
                    &.border {
                        color:#4E60FF;
                        @include mix.txt-cap1;
                        font-weight:600;
                        background:transparent;
                        border:0.1rem solid #4E60FF;
                    }
                    & + .btn {
                        margin-left:1.2rem;
                    }
                }
                @include mix.respond-to(mobile){
                    flex-direction: column;
                    > span {
                        padding:0;
                        line-height:2.4rem;
                        text-align:center;
                        font-weight:400;
                        &::before {
                            display:none;
                        }
                        br {
                            display:block;
                        }
                    }
                    .btn {
                        margin-top: 2.4rem;
                        padding:1.6rem 2.4rem;
                        @include mix.txt-body4;
                    }
                }
            }
            .tit {
                display:flex;
                align-items:center;
                color:#666666;
                font-weight:600;
                line-height:1.9rem;
                .info {
                    position: relative;
                    width: 1.6rem;
                    height: 1.6rem;
                    background: url('../images/icon/ico_mark.svg') 0 0 /100% 100% no-repeat;
                    margin-left: 0.4rem;
                    cursor: pointer;
                    > .tooltip-box{
                        width: 100%;
                        top: auto;
                        bottom:3.8rem;
                        left: auto;
                        right: -2.4rem;
                        @include mix.respond-to(terminal){
                            min-width: 0;
                            width: 19rem;
                            left: -1rem;
                            right: auto;
                        }
                        @media (max-width: 420px) {
                            width: 16rem;
                        }
                        @media (max-width: 370px) {
                            width: 14rem;
                        }
                        &::before{
                            left: auto;
                            right: 2.6rem;
                            transform: rotate(-180deg);
                            top: 100%;
                            @include mix.respond-to(terminal){
                                left: 1.2rem;
                                right: auto;
                            }
                        }
                        p {
                            br {
                                display: none;
                            }
                        }
                    }
                }
                & + .cont {
                    margin-top:3.2rem;
                    &:has(span+span) {
                        &::before {
                            content:'';
                            display:block;
                            position:absolute;
                            top:0;
                            left:50%;
                            width:.1rem;
                            height:100%;
                            background:#EEEEEE;
                            transform: translateX(-50%);
                        }

                    }
                    > span {
                        flex:1;
                        padding:0 2rem 0 0;
                        color:#999999;
                        text-align: left;
                        &::before {
                            display:none;
                        }
                        strong {
                            margin:1rem 0 0 0;
                            @include mix.txt-tit5;
                            font-weight:600;
                        }
                        & + span {
                            padding:0 0 0 2rem;
                        }
                        @include mix.respond-to(terminal2){
                            @include mix.txt-cap1;
                            strong {
                                @include mix.txt-body1;
                            }
                            & + span {
                                padding:0 0 0 1rem;
                            }
                        }
                    }
                }
                &::before {
                    content:'';
                    display:block;
                    width:2.4rem;
                    height:2.4rem;
                    margin-right:.8rem;
                    background-repeat:no-repeat;
                    background-size: 100% 100%;
                    background-position: 50% 50%;
                }
                &.ico-recommend {
                    &::before {
                        background-image:url('../images/icon/ico_recommend.svg');
                    }
                }
                &.ico-book {
                    &::before {
                        background-image:url('../images/icon/ico_book.svg');
                    }
                }
                &.ico-file {
                    &::before {
                        background-image:url('../images/icon/ico_file3.svg');
                    }
                }
                &.ico-clock {
                    &::before {
                        background-image:url('../images/icon/ico_clock2.svg');
                    }
                }
                &.ico-correct {
                    &::before {
                        background-image:url('../images/icon/ico_correct.svg');
                    }
                }
                @include mix.respond-to(mobile){
                    & + .cont {
                        flex-direction: row;
                        > span {
                            text-align:left;
                            line-height:1.671rem;
                            font-weight:500;
                        }
                    }
                }
            }
            &:has(.tit) {
                padding: 3rem;
            }
        }
    }
    .edu-list {
        display: flex;
        gap: 2.4rem;
        flex-wrap: wrap;
        &.days {
            li {
                width: calc((100% - 7.2rem) / 4);
                @include mix.respond-to(terminal){
                    width: calc((100% - 4.8rem) / 3);
                }
                @include mix.respond-to(mobile){
                    width: calc((100% - 2.4rem) / 2);
                }
                @media (max-width: 430px) {
                    width: 100%;
                }
            }
        }
        li {
            width: calc((100% - 4.8rem) / 3);
            @include mix.respond-to(terminal){
                width: 100%;
            }
            > a {
                display: block;
                position:relative;
                width: 100%;
                padding: 3rem;
                border-radius: 1.6rem;
                @include mix.txt-body3;
                color: #333;
                font-weight: 500;
                text-align: left;
                border: 0.1rem solid #eee;
                &::before {
                    content:'';
                    position:absolute;
                    right:3rem;
                    top:50%;
                    width:1.6rem;
                    height:1.6rem;
                    background-color:#999999;
                    mask-image:url('../images/icon/ico_arr_right3.svg');
                    mask-position: center center;
                    mask-repeat:no-repeat;
                    mask-size: 100% 100%;
                    transform:translateY(-50%);
                }
                strong {
                    @include mix.txt-body3;
                    color: #333;
                    & + dl {
                        margin-top: 0.8rem;
                    }
                }
                dl {
                    display: flex;
                    dt {
                        color: #999;
                        font-weight:400;
                        & + dd {
                            &::before {
                                content: '';
                                display: inline-block;
                                margin: 0 0.8rem;
                                width: 0.1rem;
                                height: 1.2rem;
                                vertical-align: 0;
                                font-size: 0;
                                background-color: #ccc;
                            }
                        }
                    }
                    dd {
                        font-weight: 500;
                    }
                }
                &.complete {
                    dl {
                        dd {
                            color: #4E60FF;
                        }
                    }
                }
                &.not-yet {
                    strong {
                        color: #999;
                    }
                    dl {
                        dt {
                            color: #999;
                        }
                        dd {
                            color: #999;
                        }
                    }
                }
            }
        }
        & + .upper-area {
            margin-top: 9rem;
            @include mix.respond-to(mobile) {
                margin-top: 6rem;
            }
        }
        & + .bottom-wrap {
            margin-top: 6rem;
            @include mix.respond-to(mobile) {
                margin-top: 4rem;
            }
        }
    }
    .round-box {
        display:flex;
        align-items:center;
        color:#666666;
        background-color:#F6F7FA;
        border-radius:2.4rem;
    }
    .upper-area {
        display: flex;
        align-items: center;
        @include mix.respond-to(mobile){
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &.between {
            justify-content: space-between;
            .func {
                margin-top:0;
            }
            .count-wrap {
                dl {
                    gap: 0.5rem;
                    padding: 1.4rem 1.8rem;
                    background-color: #F6F7FA;
                    border-radius: 5rem;
                    dd {
                        color: #666;
                        strong {
                            color: #4E60FF;
                        }
                    }
                }
                @media (max-width: 420px) {
                    width: 100%;
                    dl {
                        display: flex;
                        justify-content: center;
                        flex: 1;
                    }
                }
            }
            .sort-wrap {
                display: flex;
                gap: 1rem;
                align-items: center;
                padding-top: .8rem;
                @include mix.respond-to(mobile){
                    flex-wrap: wrap;
                    margin-top: 3rem;
                }
                @media (max-width: 540px) {
                    width: 100%;
                    flex-direction: column;
                    align-items: flex-start;
                }
                .func {
                    margin-top: 0;
                    padding-left: 0;
                    & + select {
                        margin-left: 2.2rem;
                        @media (max-width: 540px) {
                            margin-left: 0;
                        }
                    }
                    @include mix.respond-to(mobile){
                        padding-right: 0;
                    }
                }
                select {
                    min-width: 15rem;
                    font-weight:400;
                    @media (max-width: 540px) {
                        width: 100%;
                        min-width: auto;
                        margin-left: 0;
                    }
                }
            }
            @include mix.respond-to(mobile){
                &:has(.sort-wrap) {
                    flex-direction: column;
                    align-items: flex-start;
                }
            }
            @media (max-width: 420px) {
                display:block;
                .count-wrap {
                    & + .btn-aside {
                        margin-top: 2rem;
                    }
                }
                .func {
                    margin-top:1.6rem;
                }
            }
        }
        &:not(.process) {
            .count-wrap {
                @media (max-width: 360px) {
                    dl {
                        & + dl {
                            &::before {
                                display: none;
                            }
                        }
                    }
                }
            }
            .btn-aside {
                margin-left: auto;
            }
        }
        h5.sub-title {
            @include mix.txt-body2;
            font-weight: 600;
            & + .count-wrap {
                margin-left: 1.6rem;
                @include mix.respond-to(mobile){
                    margin-left: 0;
                }

            }
        }
        .count-wrap {
            display: flex;
            dl {
                display: flex;
                dt {
                    color: #666;
                }
                dd {
                    padding-left: 0.3rem;
                    color: #4E60FF;
                    strong {
                        font-weight: 600;
                    }
                }
                & + dl {
                    &::before {
                        content: '/';
                        margin: 0 .6rem;
                    }
                }
            }
        }

        & + .word-list {
            margin-top: 2.4rem;
        }
        & + .edu-list {
            margin-top: 2.4rem;
            li {
                > .complete,
                > .not-yet {
                    background-position: calc(100% - 3rem) calc(100% - 3.5rem);
                }
            }
        }
        .all-list {
            display: flex;
            color: #666;
            strong {
                margin-left: .8rem;
                color: #4E60FF;
                font-weight: 600;
            }
            li {
                display: flex;
                color: #666;
                align-items: center;
                & + li {
                    &::before {
                        content: '';
                        display: inline-block;
                        width: 0.1rem;
                        height: 1.2rem;
                        margin: 0 1.2rem;
                        background-color: #ccc;
                        font-size: 0;
                    }
                }
            }
        }
    }
    .btn-aside {
        padding-left: 2.6rem;
        color:#666666;
        font-weight:400;
        &.setting {
            background: url('../images/icon/ico_setting2.svg') 0 50% no-repeat;
            background-size: 2rem auto;
        }
        &.reset {
            background: url('../images/icon/ico_reset.svg') 0 50% no-repeat;
            background-size: 2rem auto;
        }
        & + .level-wrap {
            margin-top: 2.4rem;
        }
        & + .tab-mini {
            margin-top: 6rem;
        }
    }
    .word-list {
        display: flex;
        flex-wrap: wrap;
        gap: 2.4rem;
        li {
            width: calc((100% - 7.2rem) / 4);
            @include mix.respond-to(terminal){
                width: calc((100% - 4.8rem) / 3);
            }
            @include mix.respond-to(mobile){
                width: calc((100% - 2.4rem) / 2);
            }
            @media (max-width: 430px) {
                width: 100%;
            }
            > a {
                display: block;
                padding: 3rem;
                border-radius: 1.6rem;
                border: 0.1rem solid #eee;
                div {
                    @include mix.txt-body3;
                    color: #333;
                    @include mix.text-overflow(1);
                    & + span {
                        margin-top: 0.8rem;
                    }
                }
                span {
                    @include mix.text-overflow(1);
                    color: #666;
                }
            }
        }
        & + .bottom-wrap {
            margin-top: 6rem;
            @include mix.respond-to(mobile) {
                margin-top: 4rem;
            }
        }
    }
    .bottom-wrap {
        margin-top: 6rem;
        text-align: center;
        .btn {
            &.more {
                min-width: 16rem;
                padding: 2rem 2.4rem;
                color: #4E60FF;
                border-radius: 5rem;
                border: 0.1rem solid #4E60FF;
                text-align: center;
            }
        }
    }
    .count-btn{
        display: flex;
        align-items: center;
        margin-left: 1rem;
        @media (max-width: 370px) {
            margin-top: 1rem;
            margin-left: 0;
        }
        > button{
            width: 6rem;
            height: 6rem;
            border-radius: 0.8rem;
            background-color: #4E60FF;
            position: relative;
            &::after{
                content: '';
                width: 2.4rem;
                height: 2.4rem;
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-color:#FFFFFF;
                mask-size:100% 100%;
                mask-position:0 0;
                mask-repeat:no-repeat;
            }
            &:disabled{
                background-color: #eee;
                color: #999;
                cursor:default;
                &::after {
                    background-color:#999999;
                }
            }
            &.minus{
                &::after{
                    mask-image:url('../images/icon/ico_count_minus.svg');
                }
            }
            &.plus{
                &::after{
                    mask-image: url('../images/icon/ico_count_plus.svg');
                }
            }
            & + button{
                margin-left: 1rem;
            }
        }
    }
    .box {
        &.round {
            .input-wrap {
                .input-round {
                    display: flex;
                    width: 14.7rem;
                    height: 100%;
                    align-items: center;
                    position: relative;
                    margin: 0;
                    padding: 0 1.6rem;
                    border: 0.1rem solid #eee;
                    border-radius: 0.8rem;
                    background-color: #fff;
                    @media (max-width: 370px) {
                        width: 100%;
                        height: 6rem;
                    }
                    .input-field {
                        width: 100%;
                        border: 0 none;
                        padding: 0;
                        &:read-only {
                            background-color: transparent;
                        }
                    }
                    .text {
                        position: absolute;
                        right: 1.6rem;
                        top: 50%;
                        transform: translateY(-50%);
                        display: inline-block;
                        font-size: 1.2rem;
                        line-height: 1.8rem;
                        letter-spacing: -0.024rem;
                        color: #999;
                    }
                }
            }
            .select-radio {
                display: flex;
                gap: 1rem;
                @include mix.respond-to(mobile){
                    flex-direction: column;
                }
                input {
                    &[type="radio"] {
                        display: none;
                    }
                    &:checked {
                        & + label {
                            color: #fff;
                            background-color: #4E60FF;
                            border: 0.1rem solid transparent;
                        }
                    }
                    & + label {
                        flex: 1;
                        color: #999;
                        cursor: pointer;
                        padding: 1.6rem 1rem;
                        background-color: transparent;
                        border: 0.1rem solid #ccc;
                        text-align: center;
                        border-radius: 0.8rem;
                    }
                }
            }
            & + .my-choice {
                margin-top: 2rem;
            }
        }
    }
    .my-choice {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 3.2rem 4rem;
        border-radius: 1.6rem;
        background-color: #F6F7FA;
        @include mix.respond-to(terminal) {
            flex-direction: column;
            .btn {
                &.quiz {
                    margin-top: 3rem;
                }
            }
        }
        .data {
            display: flex;
            gap: 1rem;
            flex-wrap: wrap;
            width: calc(100% - 19.2rem);
            @include mix.respond-to(terminal) {
                width: 100%;
            }
            @include mix.respond-to(mobile) {
                justify-content: center;
            }
            li {
                padding: 1.2rem 1.6rem;
                @include mix.txt-cap1;
                color: #666;
                border-radius: 2.2rem;
                background-color: #fff;
            }
        }
        .btn {
            &.quiz {
                width: 13.2rem;
                padding: 1.4rem 2rem;
                border-radius: 10rem;
                @include mix.txt-cap1;
                color: #fff;
                background-color: #4E60FF;
            }
        }
    }
    .list-wrap {
        h5.sub-title {
            margin-top: 4rem;
            color: #333;
            font-weight: 600;
            & + .voca-list {
                > li {
                    &:nth-child(1) {
                        margin-top: 1.4rem;
                    }
                }
            }
        }
    }
    .voca-list {
        > li {
            position: relative;
            margin-top: 2.4rem;
            padding: 4rem 4rem 4rem;
            border: .1rem solid #eee;
            border-radius: 1.6rem;
            &:has(.ex-wrap) {
                @media (min-width: 721px) {
                    .btn-wrap {
                        top: 4.7rem;
                    }
                    .level-wrap {
                        & + .txt-wrap {
                            & + .ex-wrap {
                                & + .btn-wrap {
                                    top: 7.6rem;
                                }
                            }
                        }
                    }
                }
                &:has(.ex) {
                    @include mix.respond-to(mobile){
                        padding-bottom: 6rem;
                    }
                }
            }
            &:has(.ex-wrap.hide) {
                @media (min-width: 721px) {
                    .btn-wrap {
                        top: 0;
                        transform: none;
                        margin-top: 4.7rem;
                    }
                }
            }
            .level-wrap {
                flex-wrap: wrap;
                gap:  1rem;
                strong {
                    padding: .4rem 1.2rem;
                    background-color: #E7EAFF;
                    border-radius: .8rem;
                    color: #4E60FF;
                }
                & + .txt-wrap {
                    margin-top: 3.2rem;
                }
            }
            .txt-wrap {
                @media (min-width: 721px) {
                    width: 100%;
                    max-width: calc(100% - 26.2rem);
                }
                @include mix.respond-to(mobile){
                    max-width: none;
                }
                > strong {
                    position: relative;
                    @include mix.txt-body2;
                    > a {
                        &.hide{
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 12rem;
                            height: 100%;
                            border-radius: .4rem;
                            background: #F6F7FA url('../images/icon/ico_view.svg') 50% 50% no-repeat;
                            background-size: 2rem auto;
                            font-size: 0;
                            & + .btn {
                                margin-left: 12.4rem;
                            }
                        }
                    }
                    .btn {
                        &.play {
                            margin:-.2rem 0 0 .5rem;
                            vertical-align:middle;
                        }
                    }
                }
                .meaning {
                    display: flex;
                    margin-top: 1.2rem;
                    @include mix.txt-body4;
                    &.hide {
                        position: relative;
                        width: 12rem;
                        font-size: 0;
                        &::before {
                            content: '';
                            display: block;
                            position: absolute;
                            width: 12rem;
                            height: 100%;
                            border-radius: .4rem;
                            background: #F6F7FA url('../images/icon/ico_view.svg') 50% 50% no-repeat;
                            background-size: 2rem auto;

                        }
                    }
                    span {
                        color: #4E60FF;
                        & + div {
                            &::before {
                                content: '';
                                display: inline-block;
                                margin: 0 0.8rem;
                                width: 0.1rem;
                                height: 1.2rem;
                                vertical-align: 0;
                                font-size: 0;
                                background-color: #ccc;
                            }
                        }
                    }
                    div {
                        color: #333;
                    }
                }
            }
            .btn-wrap {
                position: absolute;
                right: 4rem;
                top: 4.7rem;
                display: flex;
                gap: 1.2rem;
                width: auto;
                transition: all .3s ease;
                .btn {
                    min-width: 11.5rem;
                    @include mix.txt-cap1;
                }
                @include mix.respond-to(mobile){
                    position:static;
                    margin-top:2.4rem;
                    transform: none;
                    .btn {
                        width:50%;
                        text-align:center;
                    }
                }
            }
            .ex-wrap {
                position: relative;
                margin-top: 2.4rem;
                text-align: center;
                @media (min-width: 721px) {
                    margin-bottom: -1rem;
                }
                &.hide {
                    position: relative;
                    .ex-list {
                        height: 6rem;
                        overflow: hidden;
                        border-radius: .8rem;
                    }
                    @include mix.respond-to(mobile){
                        position:static;
                    }
                }
                @include mix.respond-to(mobile){
                    position:static;
                }
                .ex-list {
                    position: relative;
                    margin-bottom: 2.4rem;
                    display: none;
                    text-align: left;
                    @include mix.respond-to(mobile){
                        max-width: none;
                        margin-bottom: 0;
                    }

                    > li {
                        p {
                            color: #666;
                            font-weight: 400;
                            &.en {
                                .btn {
                                    margin-left: 0.4rem;
                                    vertical-align: -0.4rem;
                                }
                            }
                        }
                        & + li {
                            margin-top: 1.2rem;
                        }
                        &.notice {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            position: absolute;
                            left: 0;
                            top: 0;
                            width: 100%;
                            height: 100%;
                            margin: 0;
                            padding: 2rem;
                            color: #999;
                            font-weight: 400;
                            border-radius: .8rem;
                            background-color: #F6F7FA;
                            cursor:pointer;
                        }
                    }
                }
                .btn {
                    &.ex {
                        @include mix.txt-cap1;
                        color: #999;
                        transition: all .3s ease;
                        @include mix.respond-to(mobile){
                            position:absolute;
                            bottom:2rem;
                            left:50%;
                            transform: translateX(-50%);
                        }
                        &::after{
                            content: '';
                            display: inline-block;
                            width: 1.6rem;
                            height: 1.6rem;
                            margin-left: .4rem;
                            background: url('../images/icon/ico_arr_down2.svg') 50% 50% no-repeat;
                            background-size: 1.6rem auto;
                            vertical-align: middle;
                            transition: all .3s ease;
                        }
                        &.open {
                            &::after {
                                transform: rotate(180deg);
                            }
                        }
                    }
                }
            }
        }
    }
    .func {
        position:relative;
        display: flex;
        align-items:center;
        justify-content: space-between;
        margin-top:4.8rem;
        z-index:1;
        > span {
            display: flex;
        }
        @include mix.respond-to(mobile){
            margin-top:3.2rem;
            .desc {
                display:none;
            }
        }
    }
    .btn {
        &.word,
        &.exam {
             padding: 1.4rem 2rem;
             border-radius: 10rem;
             background-color: #4E60FF;
             color: #fff;
        }
        &.cart {
            padding: 1.4rem 2rem;
            border-radius: 10rem;
            border: .1rem solid #4E60FF;
            color: #4E60FF;
       }
        &.into{
            padding-right: 2rem;
            background: url('../images/icon/ico_into.svg') 100% 50% no-repeat;
            background-size: 1.6rem auto;
        }
        &.play {
            width: 2rem;
            height: 2rem;
            background: url('../images/icon/ico_play.svg') 50% 50% no-repeat;
            background-size: 2rem auto;
            vertical-align: -.3rem;
            &.active {
                background-image:url('../images/icon/ico_play_on.svg');
            }
        }
    }
    .viewer-wrap {
        .viewer {
            &__top {
                @include mix.respond-to(mobile) {
                    padding:0;
                }
            }
        }
        .status-wrap {
            margin-top:0;
            @include mix.respond-to(mobile) {
                padding:0;
            }
        }
    }
    .table-cont {
        position:relative;
        margin-top:2.4rem;
        .btn {
            flex: 0 0 auto;
            padding:1rem 1.6rem;
            color:#999999;
            font-weight:500;
            line-height:1.9rem;
            border-radius:.8rem;
            &.btn-grey {
                background:#F6F7FA;
            }
            &.btn-primary {
                color:#A034FF;
                background:#F9ECFF;
            }
            &.btn-primary2 {
                color:#4E60FF;
                background:#E7EAFF;
            }
        }
        .table {
            table {
                tbody {
                    tr {
                        td {
                            strong {
                                display:block;
                                font-weight:600;
                            }
                            &.subject {
                                display:flex;
                                align-items:center;
                                padding:2rem 3rem;
                                &:has(.btn){
                                    justify-content: space-between;
                                    .tit {
                                        padding-right:2rem;
                                        text-align:left;
                                    }
                                }
                                .tit {
                                    text-align:center;
                                    strong {
                                        margin-top:.6rem;
                                        color:#333333;
                                        @include mix.txt-body3;
                                    }
                                }
                            }
                            &.grade {
                                color:#EA4F4F;
                                font-weight:600;
                                line-height:1.9rem;
                                &.o {
                                    color:#4E60FF;
                                }
                            }
                            &.scoring {
                                color:#333333;
                            }
                        }
                    }
                }
            }
        }
        .desc {
            position:absolute;
            display:flex;
            align-items:center;
            justify-content: flex-end;
            top:-4.7rem;
            right:0;
            width: 100%;
            color:#999999;
            font-weight:400;
            &::before {
                content:'';
                display:block;
                width:.4rem;
                height:.4rem;
                margin-right:.6rem;
                background:#999999;
                border-radius:.2rem;
            }
        }
        @include mix.respond-to(mobile){
            position:static;
            .table {
                padding:0;
            }
            .desc {
                position:static;
                align-items:flex-start;
                justify-content: flex-start;
                @include mix.txt-cap1;
                &::before {
                    margin-top:.8rem;
                }
            }
        }
    }
    .scoring-list {
        margin-top:14rem;
        .tit-wrap {
            @include mix.respond-to(mobile) {
                position:relative;
            }
        }
        .table {
            table {
                tbody {
                    tr {
                        td {
                            &.scoring {
                                padding-left:2rem;
                                color:#333333;
                                text-align:left;
                                font-weight:300;
                                strong {
                                    @include mix.txt-body3;
                                    font-weight:600;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .result-wrap {
        .box-wrap {
            .box {
                &.round-bg {
                    .btn-wrap {
                        @include mix.respond-to(mobile){
                            width:auto;
                        }
                        .btn {
                            width: auto;
                            padding:2rem;
                        }
                    }
                }
            }
        }
    }
    .viewer-wrap {
        .viewer {
            &__body {
                .btn-wrap {
                    .btn {
                        &.btn-next {
                            color:#FFFFFF;
                            background-color:#4E60FF;
                            &::after {
                                background-color:#FFFFFF;
                            }
                        }
                    }
                }
            }
        }
    }
    .exam-wrap {
        .no {
            padding:0 0 1.2rem 3rem;
            color:#333333;
            @include mix.txt-body3;
            font-weight:600;
        }
        .question {
            position:relative;
            height:40rem;
            margin-top:1.2rem;
            border:.1rem solid #EEEEEE;
            border-radius:1.6rem;
            .text {
                position:absolute;
                top:50%;
                left:50%;
                width:100%;
                transform:translate(-50%,-50%);
                text-align:center;
                .tit {
                    @include mix.txt-h3;
                    font-weight:700;
                    span {
                        &.empty {
                            display:inline-block;
                            width:20rem;
                            margin-left:.8rem;
                            border-bottom:.4rem solid #000000;
                        }
                    }
                }
                .sub {
                    margin-top:2rem;
                    color:#999999;
                    @include mix.txt-body3;
                    font-weight:400;
                }
                @include mix.respond-to(mobile){
                    width:90%;
                }
                .right {
                    display:none;
                }
            }
        }
        .answer {
            margin-top:7rem;
            ol {
                &.multiple {
                    display:flex;
                    align-items:stretch;
                    flex-wrap:wrap;
                    gap:2.4rem;
                    li {
                        display:flex;
                        align-items:stretch;
                        width:calc(50% - 1.2rem);
                        input {
                            &[type='radio'] {
                                display:none;
                                &+label {
                                    display:block;
                                    width:100%;
                                    padding:3.2rem 2rem;
                                    color:#999999;
                                    @include mix.txt-body3;
                                    border:.1rem solid #EEEEEE;
                                    border-radius:1.6rem;
                                    text-align:center;
                                    cursor:pointer;
                                    font-weight:400;
                                }
                                &:checked {
                                    &+label {
                                        color:#4E60FF;
                                        border:.2rem solid #4E60FF;
                                        font-weight:600;
                                    }
                                }
                            }
                        }
                    }

                }
            }
            @include mix.respond-to(mobile){
                ol {
                    li {
                        width:100%;
                    }
                }
            }
            .short {
                text-align:center;
                input {
                    &[type="text"] {
                        padding-bottom:2.8rem;
                        @include mix.txt-h3;
                        border-bottom:.4rem solid #666666;
                        font-weight:700;
                        text-align:center;
                        @include mix.respond-to(terminal){
                            width:90%;
                        }
                    }
                }
            }
        }
        &.ans {
            &__right {
                .answer {
                    ol {
                        &.multiple {
                            li {
                                input {
                                    &[type='radio']:checked {
                                        &+label {
                                            color:#FFFFFF;
                                            border-color:#4E60FF;
                                            background-color:#4E60FF;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .short {
                        input {
                            color:#4E60FF;
                        }
                    }
                }
            }
            &__wrong {
                .question {
                    .text {
                        .right {
                            display:block;
                            margin-top:2rem;
                            color:#4E60FF;
                            @include mix.txt-h3;
                            font-weight:700;
                        }
                    }
                }
                .answer {
                    ol {
                        &.multiple {
                            li {
                                input {
                                    &[type='radio']:checked {
                                        &+label {
                                            color:#EA4F4F;
                                            border:.1rem solid #EA4F4F;
                                        }
                                    }
                                }
                                &.right {
                                    input {
                                        &[type='radio'] {
                                            &+label {
                                                color:#FFFFFF;
                                                border-color:#4E60FF;
                                                background-color:#4E60FF;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .short {
                        color:#EA4F4F;
                    }
                }
            }
        }
