@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.classic-wrap{
    padding:6rem 0 14rem;
    color:#333333;
    background-color: #fff;
    @include mix.txt-body4;
    font-weight:500;
    @include mix.respond-to(terminal) {
        padding: 8.5rem 0 7rem;
    }
    .inner {
        position: relative;
        max-width: 128rem;
        margin: 0 auto;
        padding: 0 2rem;
        > .lecture-wrap{
            .curriculum{
                &__item{
                    .category-wrap{
                        .category{
                            em{
                                font-weight: 400;
                            }
                        }
                    }
                    .curriculum-tit{
                        h5{
                            font-family: 'YetHangul';
                            font-weight: 600;
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                h4.title{
                    padding-left: 2rem;
                }
            }
        }
        > .tab-round{
            margin-top: 4.8rem;
            .tab-wrap{
                border: 1px solid #EEE;
            }
        }
        .classic-search{
            margin-top: 14rem;
            @include mix.respond-to(mobile){
                margin-top: 10rem;
            }
        }
        .literary-search{
            width: 100%;
            display: flex;
            align-items: center;
            > h5{
                @include mix.txt-body1;
                margin-right: 4rem;
            }
            > select{
                width: 18rem;
                height: 5.6rem;
                flex: none;
                padding: 1.6rem 3.2rem 1.6rem 1.6rem;
                margin-right: 1rem;
            }
            > .input-basic{
                flex: 1;
                height: 5.6rem;
            }
            > .search{
                min-width: 9rem;
                @include mix.button_color(1.4rem 2rem, #4E60FF, 1.4rem, #fff, 0.8rem);
                margin-left: 1rem;
            }
            @include mix.respond-to(mobile){
                flex-direction: column;
                > select, > .input-basic, > .search{
                    width: 100%;
                    margin-left: 0;
                    margin-right: 0;
                }
                > .input-basic, > .search{
                    margin-top: 1rem;
                }
            }
        }
        .classic-status{
            margin-top: 14rem;
            &__top{
                margin-top: 4.8rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                > select{
                    width: 17rem;
                    flex: none;
                    padding: 1.6rem 3.2rem 1.6rem 1.6rem;
                }
                > .tab-mini{
                    margin-top: 0;
                }
            }
            &__cont{
                margin-top: 2.4rem;
                .progress{
                    display: flex;
                    align-items: center;
                    progress {
                        width: 65%;
                        display:inline-block;
                        height:1rem;
                        margin:0 2rem;
                        border:0;
                        border-radius:.5rem;
                        overflow:hidden;
                        transition:width .5s;
                        &::-webkit-progress-bar {
                            background: #EDEFF7;
                        }
                        &::-webkit-progress-value {
                            background: linear-gradient(90deg, #51C5FF 0%, #4E60FF 100%);
                            border-radius:.5rem;
                            transition:width .5s;
                        }
                    }
                }
                .classic-tit{
                    @include mix.txt-body3;
                    text-align: left;
                    font-weight: 600;
                    color: #333;
                }
            }
            @include mix.respond-to(mobile){
                margin-top: 10rem;
                &__top{
                    padding: 0 2rem;
                }
            }
        }
        > .grade-list{
            .tit-wrap{
                .func{
                    > em{
                        @include mix.txt-body4;
                        color: #999;
                    }
                }
            }
        }
        .btn{
            flex:0 0 auto;
            padding:1rem 1.6rem;
            color:#999999;
            font-weight:600;
            line-height:1.9rem;
            border-radius:.8rem;
            &.btn-primary {
                color: #A034FF;
                background: #F9ECFF;
            }
            &.btn-primary2 {
                color: #4E60FF;
                background: #E7EAFF;
            }
            &.btn-grey {
                background: #F6F7FA;
            }
        }
    }
    h4.title {
        @include mix.txt-tit1;
        font-weight:700;
        @include mix.respond-to(mobile){
            @include mix.txt-tit3;
        }
    }
    &.keyword{
        button.title{
            @include mix.txt-tit1;
            font-weight:700;
            display: flex;
            align-items: center;
            &::before{
                content: '';
                width: 2.8rem;
                height: 2.8rem;
                background: url('../images/icon/ico_tit_arr_left.svg') 0 0 no-repeat;
                display: inline-block;
                margin-right: 1rem;
            }
            @include mix.respond-to(mobile){
                @include mix.txt-tit3;
            }
        }
        .classic-search{
            margin-top: 0;
            &__top{
                width: 100%;
                display: flex;
                justify-content: space-between;
                .list-all{
                    @include mix.txt-cap2;
                    color: #999;
                    border: 1px solid #ccc;
                    border-radius: 0.8rem;
                    padding: 1rem;
                }
                @include mix.respond-to(mobile){
                    padding: 0 2rem;
                }
            }
            > .box-wrap{
                margin-top: 4.8rem;
            }
            .keyword-cont{
                margin-top: 2.4rem;
                .classic-tit{
                    @include mix.txt-body3;
                    text-align: left;
                    font-weight: 600;
                    color: #333;
                }
            }
            @include mix.respond-to(mobile){
                .keyword-top{
                    padding: 0 2rem;
                }
                .keyword-cont{
                    padding: 0 2rem;
                }
            }
        }
    }
    .box-wrap {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-top: 2.4rem;
        @include mix.txt-body4;
        gap: 2.4rem;
        .box {
            flex:1;
            padding:3.4rem 3rem;
            border:0.1rem solid #EEEEEE;
            @include mix.respond-to(terminal2){
                flex: 0 1 auto;
                width: calc((100% - 2.4rem) / 2);
            }
            @include mix.respond-to(terminal){
                width: 100%;
            }
            &.round {
                border-radius:1.6rem;
                &-bg {
                    flex-direction:column;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;
                    padding:4rem 3rem;
                    border-radius: 2rem;
                    background-color: #F6F7FA;
                    border:0;
                    h5 {
                        @include mix.txt-body1;
                        font-weight:700;
                    }
                    > span {
                        margin-top: 1.2rem;
                        color:#666666;
                        @include mix.txt-body4;
                        font-weight:500;
                    }
                    .btn-wrap {
                        width:auto;
                        margin-top: 2.8rem;
                        gap:1.2rem;
                        > .btn {
                            display:block;
                            width: 16rem;
                            padding:2rem 0;
                            color:#FFFFFF;
                            @include mix.txt-body4;
                            background-color:#13BE29;
                            border-radius:5rem;
                            border:.1rem solid #13BE29;
                            text-align:center;
                            &.border {
                                color:#13BE29;
                                @include mix.txt-body4;
                                font-weight:600;
                                background:transparent;
                                border:.1rem solid #13BE29;
                            }
                        }
                        &:has(.btn + .btn) {
                            .btn {
                                width:21rem;
                                &+.btn {
                                    margin-left:1.6rem;
                                }
                            }
                        }
                        @include mix.respond-to(mobile){
                            display:block;
                            width: 100%;
                            &:has(.btn + .btn) {
                                .btn {
                                    width:100%;
                                    &+.btn {
                                        margin:1.2rem 0 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .cont {
                position:relative;
                display: flex;
                justify-content: space-between;
                > span {
                    padding-left:7.6rem;
                    color:#666666;
                    font-weight:500;
                    line-height:1.6rem;
                    strong {
                        display:block;
                        color:#333333;
                        @include mix.txt-body3;
                        font-weight:600;
                        > em{
                            @include mix.txt-body2;
                            font-weight: 600;
                            color: #999;
                        }
                        &[class^='ico-'] {
                            display: flex;
                            align-items: center;
                            cursor: pointer;
                            &::after {
                                content: '';
                                width: 2.8rem;
                                height: 2.8rem;
                                display: inline-block;
                                margin-left: 0.8rem;
                                background-size:100% 100%;
                                background-repeat:no-repeat;
                                @include mix.respond-to(mobile){
                                    width:2.4rem;
                                    height:2.4rem;
                                }
                            }
                        }
                        &.ico-mark{
                            position: relative;
                            &::after{
                                background-image: url('../images/icon/ico_mark.svg');
                            }
                            @include mix.respond-to(mobile){
                                .tooltip-box{
                                    min-width: auto;
                                    width: 22rem;
                                }
                            }
                        }
                    }
                    br {
                        display: none;
                    }
                    &::before {
                        content:'';
                        display:block;
                        position:absolute;
                        top:50%;
                        left:0;
                        width:5.6rem;
                        height:5.6rem;
                        background:#F6F7FA url('../images/icon/ico_book2.svg') center center no-repeat;
                        border-radius:2.8rem;
                        background-size:3.2rem 3.2rem;
                        transform: translateY(-50%);
                    }
                }
                .btn {
                    padding:1.4rem 2rem;
                    color:#FFFFFF;
                    @include mix.txt-cap1;
                    background-color:#13BE29;
                    border-radius:5rem;
                    font-weight:600;
                    border:0.1rem solid #13BE29;
                    &.border {
                        color:#13BE29;
                        @include mix.txt-cap1;
                        font-weight:600;
                        background:transparent;
                        border:0.1rem solid #13BE29;
                    }
                    & + .btn {
                        margin-left:1.2rem;
                    }
                }
                @include mix.respond-to(mobile){
                    flex-direction: column;
                    > span {
                        padding:0;
                        line-height:2.4rem;
                        text-align:center;
                        font-weight:400;
                        &::before {
                            display:none;
                        }
                        br {
                            display:block;
                        }
                    }
                    .btn {
                        margin-top: 2.4rem;
                        padding:1.6rem 2.4rem;
                        @include mix.txt-body4;
                    }
                }
            }
            .tit {
                display:flex;
                align-items:center;
                color:#666666;
                font-weight:600;
                line-height:1.9rem;
                .info {
                    position: relative;
                    width: 1.6rem;
                    height: 1.6rem;
                    background: url('../images/icon/ico_mark.svg') 0 0 /100% 100% no-repeat;
                    margin-left: 0.4rem;
                    cursor: pointer;
                    > .tooltip-box{
                        width: 100%;
                        top: auto;
                        bottom:3.8rem;
                        left: auto;
                        right: -2.4rem;
                        @include mix.respond-to(terminal){
                            min-width: 0;
                            width: 19rem;
                            left: -1rem;
                            right: auto;
                        }
                        @media (max-width: 400px) {
                            width: 16rem;
                        }
                        @media (max-width: 370px) {
                            width: 14rem;
                        }
                        &::before{
                            left: auto;
                            right: 2.6rem;
                            transform: rotate(-180deg);
                            top: 100%;
                            @include mix.respond-to(terminal){
                                left: 1.2rem;
                                right: auto;
                            }
                        }
                        p {
                            br {
                                display: none;
                            }
                        }
                    }
                }
                & + .cont {
                    margin-top:3.2rem;
                    &:has(span+span) {
                        &::before {
                            content:'';
                            display:block;
                            position:absolute;
                            top:0;
                            left:50%;
                            width:.1rem;
                            height:100%;
                            background:#EEEEEE;
                            transform: translateX(-50%);
                        }

                    }
                    > span {
                        flex:1;
                        padding:0 2rem 0 0;
                        color:#999999;
                        text-align: left;
                        &::before {
                            display:none;
                        }
                        strong {
                            margin:1rem 0 0 0;
                            @include mix.txt-tit5;
                            font-weight:600;
                        }
                        & + span {
                            padding:0 0 0 2rem;
                        }
                        @include mix.respond-to(terminal2){
                            @include mix.txt-cap1;
                            strong {
                                @include mix.txt-body1;
                            }
                            & + span {
                                padding:0 0 0 1rem;
                            }
                        }
                    }
                }
                &::before {
                    content:'';
                    display:block;
                    width:2.4rem;
                    height:2.4rem;
                    margin-right:.8rem;
                    background-repeat:no-repeat;
                    background-size: 100% 100%;
                    background-position: 50% 50%;
                }
                &.ico-recommend {
                    &::before {
                        background-image:url('../images/icon/ico_recommend.svg');
                    }
                }
                &.ico-book {
                    &::before {
                        background-image:url('../images/icon/ico_book.svg');
                    }
                }
                &.ico-file {
                    &::before {
                        background-image:url('../images/icon/ico_file3.svg');
                    }
                }
                &.ico-clock {
                    &::before {
                        background-image:url('../images/icon/ico_clock2.svg');
                    }
                }
                &.ico-correct {
                    &::before {
                        background-image:url('../images/icon/ico_correct.svg');
                    }
                }
                @include mix.respond-to(mobile){
                    & + .cont {
                        flex-direction: row;
                        > span {
                            text-align:left;
                            line-height:1.671rem;
                            font-weight:500;
                        }
                    }
                }
            }
            &:has(.tit) {
                padding: 3rem;
            }
        }
        &.solve-test {
            margin-top:9rem;
            .box {
                &.round {
                    &-bg {
                        flex-direction: row;
                        justify-content: space-between;
                        align-items:center;
                        strong {
                            @include mix.txt-body3;
                            font-weight:600;
                        }
                        .btn {
                            margin-top:0;
                            padding:1.4rem 2rem;
                            @include mix.txt-cap1;
                        }
                        @media (max-width: 768px) {
                            display:block;
                            strong {
                                width:100%;
                            }
                            .btn {
                                width:100%;
                                margin-top:2rem;
                            }
                        }
                    }
                }
            }
        }
    }
    .mt12{
        margin-top: 1.2rem;
    }
    .comm-bot{
        width: 100%;
        background-color: #F6F7FA;
        padding: 3rem;
        border-radius: 1.6rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 9rem;
        > p{
            @include mix.txt-body3;
            color: #333;
            font-weight: 600;
        }
        > button{
            min-width: 14rem;
            @include mix.button_color(1.4rem 2rem, #4E60FF, 1.4rem, #fff, 10rem);
        }
    }
    &__study{
        width: 100%;
        margin-top: 4.8rem;
        .box-gray{
            width: 100%;
            background-color: #F6F7FA;
            border-radius: 1.6rem;
            padding: 3rem;
            & + .box-line{
                margin-top: 2.4rem;
            }
        }
        .study-info{
            @include mix.txt-body4;
            line-height: normal;
            color: #666;
            display: flex;
            align-items: center;
            > dt{
                min-width: 5.1rem;
                font-weight: 400;
            }
            > dd{
                font-weight: 500;
            }
            & + dl{
                margin-top: 1.2rem;
            }
        }
        .box-line{
            width: 100%;
            border: 1px solid #EEE;
            border-radius: 1.6rem;
            background-color: #fff;
            padding: 3rem;
            > .title{
                @include mix.txt-body2;
                color: #333;
                font-weight: 700;
                margin-bottom: 1.6rem;
            }
            > .cont{
                @include mix.txt-body4;
                color: #666;
                font-weight: 400;
                font-family: 'YetHangul', YetHangul;
            }
            & + .box-line{
                margin-top: 2.4rem;
            }
        }
        @include mix.respond-to(mobile){
            padding: 0 2rem;
            .box-line{
                > .table{
                    padding: 0;
                }
            }
        }
    }
    .study-tit{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 3rem;
        color: #333;
    }
    .study-sum{
        @include mix.txt-body4;
        color: #666;
        display: flex;
        align-items: center;
        min-width: 9rem;
        margin-left:2rem;
        font-weight: 400;
        &::after{
            content: '';
            width: 1.6rem;
            height: 1.6rem;
            display: inline-block;
            mask-image: url('../images/icon/ico_exam-sum.svg');
            background-color: #aaa;
            margin-left: 0.4rem;
        }
    }
}