@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.main{
    width: 100%;
    @include mix.respond-to(mobile){
        overflow: hidden;
    }
    .inner{
        width: 100%;
        max-width: 128rem;
        height: 100%;
        margin: 0 auto;
        position: relative;
        @include mix.respond-to(terminal){
            overflow: hidden;
        }
    }
    &__visual{
        width: 100%;
        background: linear-gradient(180deg, #FFF 30.4%, #98C8FF 100%);
        border-radius: 0 0 5rem 5rem;
        padding: 12.1rem 2rem 27rem 2rem;
        position: relative;
        z-index: 3;
        transition: padding 0.3s ease;
        @include mix.respond-to(tablet){
            padding: 14rem 0 27rem 0;
        }
        &::before{
            content: '';
            width: 27rem;
            height: 9.2rem;
            display: inline-block;
            background: url('../images/img/bg_main_visual_center.svg') 0 0 no-repeat;
            position: absolute;
            bottom: -3.4rem;
            left: 50%;
            transform: translateX(-50%);
        }
        @include mix.respond-to(mobile){
            padding: 14rem 0 16rem 0;
            &::before{
                width: 16.1rem;
                height: 4.9rem;
                background: url('../images/img/bg_main_visual_center_mo.svg') 0 0 no-repeat;
                bottom: -1.8rem;
            }
        }
        .txt-wrap{
            width: 100%;
            margin-bottom: 12rem;
            .sub-tit{
                @include mix.txt-tit3;
                color: #666;
                font-weight: 600;
                margin-bottom: 2.8rem;
                @include mix.respond-to(terminal){
                    display: block;
                    margin-bottom: 0;
                }
            }
            h2.tit{
                @include mix.txt-h1;
                font-weight:700;
                color: #333;
                display: flex;
                align-items: center;
                padding: 2.4rem 0 1.4rem 0;
                @include mix.respond-to(terminal){
                    display: inline-block;
                }
                em.en{
                    font-size: 8.9rem;
                    font-weight: 500;
                    padding-right: 0.8rem;
                }
            }
            .sub-text{
                @include mix.txt-body3;
                font-weight: 400;
                color: #666;
            }
            @include mix.respond-to(tablet){
                margin-bottom: 10.2rem;
                padding: 0 2rem;
            }
            @include mix.respond-to(mobile){
                padding: 0 2rem;
                margin-bottom: 0;
                padding-left: 12%;
                .sub-tit{
                    @include mix.txt-body2;
                    font-weight: 500;
                }
                h2.tit{
                    font-size: 5.6rem;
                    line-height: 6.3rem;
                    em.en{
                        font-size: 5.6rem;
                    }
                }
                h3.tit{
                    font-size: 4.6rem;
                    flex-wrap: wrap;
                    > em.en{
                        font-size: 5.6rem;
                        line-height: normal;
                    }
                    > em{
                        display: inline-block;
                        text-align: left;
                        flex: none;
                        padding: 0;
                        margin-top: 1rem;
                        @include mix.txt-cap1;
                    }
                }
            }
        }
        .step-flow{
            width: 100%;
            position: relative;
            .object-ribbon{
                position: absolute;
                bottom: 2.3rem;
                left: -9.2rem;
                animation: fadeInUp 1s ease-in-out forwards, bounceRibbon ease-in-out infinite 7s;
                @include mix.respond-to(mobile){
                    width: 8rem;
                    height: 8rem;
                    left: -2.2rem;
                    > img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            .object-donuts{
                width: 24.2rem;
                height: 20.6rem;
                position: absolute;
                top: -10rem;
                right: 13.7rem;
                animation: fadeInUp 2s ease-in-out forwards, bounceDonuts ease-in-out infinite 5s;
                > img{
                    width: 100%;
                    height: 100%;
                }
                @include mix.respond-to(mobile){
                    width: 13rem;
                    height: 13rem;
                    right: 0.7rem;
                    top: 4rem;
                }
            }
            .item{
                width: 100%;
                display: flex;
                align-items: flex-end;
                justify-content: space-between;
                transition: transform 0.3s ease-out;
                &.in-view{
                    transform: translateY(-10rem);
                    @include mix.respond-to(mobile){
                        transform: translateY(-5rem);
                    }
                }
                @media (min-width: 320px) and (max-width: 980px) {
                    padding-left: 2rem;
                    padding-top: 10rem;
                }
            }
            .list{
                width: calc(25% - 2rem);
                border-radius: 3rem;
                position: relative;
                overflow: hidden;
                background: linear-gradient(146deg, rgba(255, 255, 255, 0.60) 6.29%, rgba(255, 255, 255, 0.05) 90.14%);
                backdrop-filter: blur(5px);
                opacity: 0;
                transform: scale(0.8);
                transition: transform 0.3s ease-out, opacity 0.3s ease-out;
                &.visible{
                    opacity: 1;
                    transform: scale(1);
                }
                &::before{
                    content: '';
                    width: 100%;
                    height: 100%;
                    display: inline-block;
                    border: 1px solid transparent;
                    border-radius: 3rem;
                    background: linear-gradient(146deg, rgba(255, 255, 255, 0.5),rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5)) border-box;
                    mask:
                        linear-gradient(#fff 0 0) padding-box,
                        linear-gradient(#fff 0 0);
                    mask-composite: exclude;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                .txt-box{
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    em{
                        width: 12rem;
                        border-radius: 5rem;
                        display: inline-block;
                        padding: 1.2rem 0;
                        @include mix.txt-body3;
                        font-weight: 600;
                        text-align: center;
                        color: #fff;
                        margin-top: 6rem;
                    }
                    p{
                        @include mix.txt-body2;
                        font-weight: 400;
                        color: #666;
                        padding-top: 2rem;
                        text-align: center;
                        > strong{
                            @include mix.txt-tit5;
                            font-weight: 700;
                        }
                    }
                    > span{
                        display:flex;
                        align-items:center;
                        justify-content: center;
                        @include mix.txt-body3;
                        width: 100%;
                        height: 6rem;
                        font-weight: 600;
                        text-align: center;
                        background-color: linear-gradient(146deg, rgba(255, 255, 255, 0.18) 6.29%, rgba(255, 255, 255, 0.02) 90.14%);
                        backdrop-filter: blur(5px);
                        position: absolute;
                        bottom: 0;
                        left: 0;
                    }
                }
                &:nth-child(1){
                    height: 33.2rem;
                    margin-bottom: 9rem;
                    .txt-box{
                        em{
                            background-color: #58aef0;;
                        }
                        > span{
                            color: #58AEF0;
                        }
                    }
                }
                &:nth-child(2){
                    height: 30rem;
                    .txt-box{
                        em{
                            background-color: #808EF7 ;
                        }
                        > span{
                            color: #7685F6;
                        }
                    }
                }
                &:nth-child(3){
                    height: 33.2rem;
                    margin-bottom: 9rem;
                    .txt-box{
                        em{
                            background-color: #AD6DED;
                        }
                        > span{
                            color: #58AEF0;
                        }
                    }
                }
                &:nth-child(4){
                    height: 30rem;
                    .txt-box{
                        em{
                            background-color: #F590DA;
                        }
                        > span{
                            color: #EC7ACE;
                        }
                    }
                }
                @media (min-width: 320px) and (max-width: 980px) {
                    &:nth-child(-n+4){
                        height: 28.6rem;
                    }
                }
                @include mix.respond-to(mobile){
                    &:nth-child(2n+1){
                        margin-bottom: 7rem;
                    }
                    .txt-box{
                        em{
                            margin-top: 5rem;
                            @include mix.txt-cap1;
                            padding: 1rem 0;
                        }
                        p{
                            padding-top: 1.8em;
                            @include mix.txt-body4;
                            strong{
                                @include mix.txt-body4;
                            }
                        }
                        > span{
                            @include mix.txt-body4;
                        }
                    }
                }
            }
            .ai-danchoo{
                width: 24.2rem;
                height: 23.2rem;
                position: absolute;
                top: -7.9rem;
                left: 26.3rem;
                z-index: 1;
                animation: sway 3.5s infinite ease-in-out;
                transform-origin: center bottom;
                opacity: 0;
                transform: translateY(10px);
                transition: opacity 1s ease, transform 1s ease;
                > img{
                    width: 100%;
                }
                &.visible{
                    opacity: 1;
                    transform: translateY(0);
                }
            }
        }
        .scroll{
            position: absolute;
            bottom: -2rem;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            flex-direction: column;
            align-items: center;
            > em{
                @include mix.txt-cap1;
                color: #447EC3;
                margin-bottom: 1.4rem;
            }
            .circle{
                width: 5.8rem;
                height: 5.8rem;
                border-radius: 50%;
                background-color: rgba(255, 255, 255, 0.2);
                position: relative;
                .mouse{
                    width: 2rem;
                    height: 3.2rem;
                    display: inline-block;
                    border-radius: 1.6rem;
                    border: 0.25rem solid #67A0E4;
                    position: absolute;
                    top:50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    overflow: hidden;
                    .wheel{
                        width: 0.4rem;
                        height: 0.8rem;
                        border-radius: 5rem;
                        background-color: #67A0E4;
                        position: absolute;
                        left: 0.6rem;
                        top: 0;
                        animation: wheel 2s cubic-bezier(0.25, 1, 0.5, 0.7) infinite;
                    }
                }
            }
            @include mix.respond-to(terminal){
                display: none;
            }
        }
        .wave{
            width: 100%;
            height: 61.6rem;
            overflow: hidden;
            position: absolute;
            bottom: 5.5rem;
            left: 0;
            .wave-line{
                width: 1000%;
                height: 55.3rem;
                background: url("../images/img/bg_main_wave_line.png") repeat-x;
                animation: waveMoveLine 300s linear infinite;
                position: absolute;
                top: 0;
                left: 0;
            }
            .wave-page{
                width: 1000%;
                height: 55.3rem;
                background: url("../images/img/bg_main_wave_page.png") repeat-x;
                animation: waveMovePage 250s linear infinite;
                position: absolute;
                top: 15rem;
                left: 0;
            }
        }
        &.in-view{
            padding: 12.1rem 2rem 12rem 2rem;
            @include mix.respond-to(tablet){
                padding: 12rem 0;
            }
            @include mix.respond-to(mobile){
                padding: 14rem 0 3.5rem 0;
            }
            .inner{
                .step-flow{
                    .ai-danchoo{
                        bottom: 0;
                    }
                }
            }
        }
    }
    &__cont{
        width: 100%;
        background-color: var.$color-white;
        position: relative;
        padding: 15.4rem 2rem 16rem 2rem;
        &::before{
            content: '';
            width: 46.2rem;
            height: 76.4rem;
            background: url('../images/img/bg_main_cont_violet.svg') 0 0 no-repeat;
            position: absolute;
            top: -13rem;
            left: 0;
        }
        &::after{
            content: '';
            width: 47.6rem;
            height: 33.5rem;
            background: url('../images/img/bg_main_cont_blue.svg') 0 0 no-repeat;
            position: absolute;
            bottom: -10.8rem;
            right: 0;
        }
        @include mix.respond-to(terminal){
            > .inner{
                overflow: visible;
            }
        }
        @include mix.respond-to(tablet){
            padding: 9.3rem 2rem 16rem 2rem;
        }
        @include mix.respond-to(mobile){
            padding: 9.3rem 2rem 10rem 2rem;
            &::before{
                width: 32.5rem;
                height: 32.6rem;
                background-size: 100% 100%;
            }
            &::after{
                width: 27.9rem;
                height: 17.1rem;
                background-size: 100% 100%;
                bottom: -4.3rem;
            }
        }
        .single{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 16rem;
            &__txt{
                width: 50%;
                .badge{
                    width: 8.9rem;
                    height: 3.8rem;
                    display: inline-block;
                    background-color: #222;
                    border-radius: 8rem;
                    @include mix.txt-body2;
                    color: var.$color-white;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    animation: bounce ease-in-out infinite 1s;
                    position: absolute;
                    top: -5rem;
                    left: 0;
                    > em{
                        color: #FFED2A;
                        margin-right: 0.5rem;
                    }
                    &::after{
                        content: '';
                        width: 1.95rem;
                        height: 1.15rem;
                        display: inline-block;
                        background: url('../images/icon/ico_badge_bottom.svg') 0 0 no-repeat;
                        position: absolute;
                        bottom: -0.9rem;
                        left: 1.2rem;
                    }
                }
                .sub-tit{
                    @include mix.txt-h3;
                    font-weight:600;
                    color: #474747;
                    margin-top: 1rem;
                    position: relative;
                }
                h3.tit{
                    @include mix.txt-h2;
                    font-weight:700;
                    color: #3B1DFF;
                    display: flex;
                    align-items: center;
                    > em{
                        font-size: 7rem;
                        font-weight: 600;
                        line-height: 8.4rem;
                        letter-spacing: -0.28rem;
                        margin-right: 0.8rem;
                    }
                }
                .info-txt{
                    display: inline-block;
                    @include mix.txt-body2;
                    font-weight: 500;
                    color: #666;
                    padding-top: 4.9rem;
                }
            }
            &__link{
                width: 50%;
                > button{
                    width: 100%;
                    height: 13rem;
                    border-radius: 10rem;
                    background-color: #F2F3F5;
                    position: relative;
                    padding: 3rem 8rem;
                    overflow: hidden;
                    &::after{
                        content: '';
                        width: 4rem;
                        height: 4rem;
                        display: inline-block;
                        @include mix.svg('../images/icon/ico_single-link_arr_right.svg', 4rem, 4rem, #444);
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 6rem;
                    }
                    &:nth-of-type(1), &:nth-of-type(2){
                        .link-tit{
                            padding-left: 10.9rem;
                        }
                    }
                    .badge{
                        display:flex;
                        align-items:center;
                        justify-content: center;
                        min-width: 7rem;
                        height: 3rem;
                        padding: 0 1.2rem;
                        border-radius: 0.6rem;
                        background-color: #E2E7F1;
                        @include mix.txt-body4;
                        line-height: normal;
                        font-weight: 600;
                        text-transform: uppercase;
                        text-align: center;
                        color: #768090;
                        margin-right: 1.2rem;
                        position: absolute;
                        top: 3rem;
                        left: 8rem;
                    }
                    .link-tit{
                        @include mix.txt-tit3;
                        line-height: 3rem;
                        font-weight: 700;
                        color: var.$color-black;
                        text-align: left;
                        padding-left: 8.2rem;
                    }
                    .info-txt{
                        width: 100%;
                        display: inline-block;
                        text-align: left;
                        @include mix.txt-body3;
                        font-weight: 300;
                        color: #666;
                        margin-top: 1.6rem;
                        strong{
                            font-weight: 300;
                        }
                        @media (max-width: 1078px) {
                            @include mix.txt-body4;
                        }
                        @include mix.respond-to(mobile){
                            strong{
                                font-weight: 300;
                            }
                        }
                    }
                    & + button{
                        margin-top: 2rem;
                    }
                    &:hover{
                        &::after{
                            background-color: #fff;
                        }
                        .link-tit, .info-txt{
                            color: #fff;
                            > strong{
                                font-weight: 700;
                            }
                        }
                        .badge{
                            background-color: #fff;
                            color: #5b41ff
                        }
                        background: linear-gradient(90deg, #6031FF 0%, #4E60FF 50%, #3B96FF 100%);
                        // &:nth-of-type(1){
                        //     background: url('../images/img/bg_link_literature_hover.svg') center 95% / cover no-repeat;
                        // }
                        // &:nth-of-type(2){
                        //     background: url('../images/img/bg_link_voca_hover.svg') center 95% / cover no-repeat;
                        // }
                        // &:nth-of-type(3){
                        //     background: url('../images/img/bg_link_math_hover.svg') center 95% / cover no-repeat;
                        // }
                    }
                }
            }
            @include mix.respond-to(terminal){
                flex-direction: column;
                &__txt{
                    width: fit-content;
                    .badge{
                        left: 6.2rem;
                    }
                    .sub-tit{
                        width: auto;
                        margin-top: 1.4rem;
                        text-align: center;
                    }
                    h3{
                        justify-content: center;
                    }
                    .info-txt{
                        width: 100%;
                        text-align: center;
                        padding-top: 1.8rem;
                    }
                }
                &__link{
                    width: 70%;
                    margin-top: 4.2rem;
                    > button{
                        background: linear-gradient(90deg, #6031FF 0%, #4E60FF 50%, #3B96FF 100%);
                        &::after{
                            background-color: #fff;
                        }
                        .link-tit, .info-txt{
                            color: #fff;
                        }
                        .badge{
                            width: 7.1rem;
                            padding: 0;
                            font-size: 1.2rem;
                            background-color: #fff;
                            color: #5b41ff
                        }
                        &:nth-of-type(1), &:nth-of-type(2){
                            .link-tit{
                                padding-left: 8.2rem;
                            }
                        }
                        // &:nth-of-type(1){
                        //     background: url('../images/img/bg_link_literature_hover.svg') center 95% / cover no-repeat;
                        // }
                        // &:nth-of-type(2){
                        //     background: url('../images/img/bg_link_voca_hover.svg') center 95% / cover no-repeat;
                        // }
                        // &:nth-of-type(3){
                        //     background: url('../images/img/bg_link_math_hover.svg') center 95% / cover no-repeat;
                        // }
                        & + button{
                            margin-top: 1rem;
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                padding-bottom: 10rem;
                &__txt{
                    .badge{
                        width: 7.4rem;
                        height: 3.1rem;
                        @include mix.txt-body4;
                        &::after{
                            width: 1.35rem;
                            height: 0.9rem;
                            bottom: -0.7rem;
                        }
                    }
                    .sub-tit{
                        font-size: 3.4rem;
                        line-height: 4.4rem;
                    }
                    h3.tit{
                        font-size: 3.4rem;
                        line-height: 4.4rem;
                        > em{
                            font-size: 3.4rem;
                            line-height: 4.4rem;
                        }
                    }
                    .info-txt{
                        width: 65%;
                        @include mix.txt-body3;
                        padding-top: 1.8rem;
                        margin: 0 auto;
                        display: block;
                    }
                }
                &__link{
                    width: 100%;
                    > button{
                        height: 9.1rem;
                        padding: 1.4rem 3rem;
                        background-position: center right !important;
                        &::after{
                            width: 2.4rem;
                            height: 2.4rem;
                            mask-size: 2.4rem, 2.4rem;
                            right: 3rem;
                        }
                        .badge{
                            width: 5rem;
                            height: 3rem;
                            @include mix.txt-cap2;
                            left: 3rem;
                            top: 50%;
                            transform: translateY(-50%);
                            background-color: #fff;
                            color: #5B41FF;
                        }
                        .link-tit{
                            @include mix.txt-body4;
                            padding-left: 8.2rem;
                        }
                        .info-txt{
                            @include mix.txt-cap2;
                            margin-top: 0.4rem;
                            padding: 0 2rem 0 8.2rem;
                        }
                    }
                }
            }
        }
        .danchoo{
            width: 100%;
            position: relative;
            z-index: 10;
            .tit-box{
                width: 100%;
                text-align: center;
                .sub-tit{
                    @include mix.txt-body2;
                    font-weight: 500;
                    color: #666;
                }
                > h3{
                    font-size: 5.2rem;
                    font-weight: 700;
                    line-height: 7rem;
                    color: var.$color-black;
                    padding-top: 1.2rem;
                    > em{
                        font-size: 5.2rem;
                        color: var.$color-black;
                    }
                }
            }
            &__link{
                width: 100%;
                margin-top: 6.4rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                > button{
                    width: 33.33%;
                    padding: 3.5rem 3.4rem;
                    border-radius: 1.8rem;
                    background-color: #F2F3F5;
                    display: flex;
                    & + button{
                        margin-left: 2.4rem;
                    }
                    .icon{
                        width: 5.6rem;
                        height: 5.6rem;
                        display: inline-block;
                    }
                    > h4{
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        @include mix.txt-tit5;
                        font-weight: 600;
                        color: #333;
                        padding-left: 2rem;
                        flex: 1;
                        em{
                            @include mix.txt-body3;
                            font-weight: 500;
                            color: #666;
                            margin-top: 0.8rem;
                        }
                    }
                }
                @media (min-width: 895px) and (max-width: 1143px) {
                    > button{
                        padding: 3.5rem 1.4rem;
                        &:nth-of-type(1){
                            > h4{
                                @include mix.txt-body1;
                                letter-spacing: -0.07rem;
                            }
                        }
                    }
                }
                @media (min-width: 720px) and (max-width: 895px) {
                    > button{
                        padding: 3.5rem 1.4rem;
                        > h4{
                            padding-left: 0.5rem;
                        }
                        .icon{
                            width: 5rem;
                            height: 5rem;
                        }
                        &:nth-of-type(1){
                            > h4{
                                @include mix.txt-body2;
                                letter-spacing: -0.07rem;
                            }
                        }
                    }
                }
                @media (min-width: 721px) and (max-width: 745px) {
                    > button{
                        h4{
                            @include mix.txt-body2;
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                .tit-box{
                    .sub-tit{
                        @include mix.txt-body3;
                    }
                    > h3{
                        font-size: 3.4rem;
                        line-height: 4.4rem;
                        letter-spacing: 0.068rem;
                        padding-top: 1.8rem;
                        > em{
                            font-size: 3.4rem;
                            line-height: 4.4rem;
                            letter-spacing: 0.068rem;
                            display: inline-block;
                        }
                    }
                }
                &__link{
                    flex-direction: column;
                    margin-top: 4.2rem;
                    > button{
                        width: 100%;
                        padding: 2.3rem 3.4rem;
                        & + button{
                            margin-left: 0;
                            margin-top: 1rem;
                        }
                    }
                }
            }
        }
    }
}
.card-sparkle {
    position: relative;
    &::after{
        content: "";
        position: absolute;
        top: -100%;
        left: -100%;
        width: 400%;
        height: 400%;
        background: linear-gradient(
            95deg,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0.5) 40%,
            rgba(255, 255, 255, 0.9) 45%, /*   */
            rgba(255, 255, 255, 1) 50%, /*   */
            rgba(255, 255, 255, 0.9) 55%, /*   */
            rgba(255, 255, 255, 0.5) 60%, /*   */
            rgba(255, 255, 255, 0) 100%
        );
        transform: rotate(0deg);
        animation: sparkle-glow 7s ease-in-out infinite;
    }
}
.mo-show{
    display: none;
}
@media (min-width: 320px) and (max-width: 980px) {
    .swiper-container{
        width: 100%;
        min-height: 36rem;
        height: 100%;
        overflow: hidden;
    }
}