@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body{
    &.dark-mode {
        .onestop, .classic-wrap {
            .step-learn {
                background: linear-gradient(180deg, #000000 22.56%, #001B5B 100%);
                &::before{
                    background: url('../images/img/bg_onestop_main_left_dark.svg') 0 0 no-repeat;
                }
                &::after{
                    background: url('../images/img/bg_onestop_main_right_dark.svg') 0 0 no-repeat;
                }
                .txt-wrap {
                    h3 {
                        color:#FFFFFF;
                    }
                    > span {
                        color:#FFFFFF;
                    }
                }
                .selection {
                    border: 1px solid;
                    background: linear-gradient(92.88deg, rgba(255, 255, 255, 0.15) 9.96%, rgba(255, 255, 255, 0.03) 90.52%);
                    backdrop-filter: blur(10px);
                    &__year {
                        background:none;
                        border-bottom: 1px solid;
                        .item {
                            color:#999999;
                            .active {
                                color:#FFFFFF;
                            }
                        }
                    }
                    &__subject {
                        .title {
                            color:#FFFFFF;
                        }
                    }
                }
            }
            .status {
                &__graph {
                    background-color:#222222;
                    border:0;
                    > h4 {
                        color:#FFFFFF;
                    }
                    &.first {
                        &::after {
                            background-image:url('../images/img/img_status-graph_first_dark.svg');
                        }
                    }
                }
                &__banner {
                    background: #002750;
                }
            }
            .latest-learn {
                border-color:#222222;
                background-color:#222222;
                > h4 {
                    color:#FFFFFF;
                    background: linear-gradient(-270deg, #222 81.27%, rgba(34, 34, 34, 0.00) 100%);
                    &::before{
                        background: #222;
                    }
                }
                .curriculum {
                    &__item {
                        border-color:#2C2C2C;
                        background-color:#2C2C2C;
                        .category-wrap{
                            .open-sum{
                                color: #fff;
                                &::after{
                                    background-color: #fff;
                                }
                            }
                        }
                        .category {
                            color:#FFFFFF;
                            background-color:#3D3D3D;
                            > em {
                                color:#FFFFFF;
                            }
                        }
                        .curriculum-tit {
                            h5,
                            .sub-tit {
                                color:#FFFFFF;
                            }
                        }
                        .go-study {
                            color:#FFFFFF;
                            background-color:#444444;
                        }
                        .teacher-wrap {
                            em {
                                color:#FFFFFF;
                                &+em {
                                    color:#FFFFFF;
                                }
                            }
                        }
                        &:hover {
                            background-color:#4E60FF;
                            .category {
                                color:#4E60FF;
                                background-color:#F6F7FA;
                                > em {
                                    color:#4E60FF;
                                    &::before {
                                        background-color:#CBD1FF;
                                    }
                                }
                            }
                            .go-study {
                                color:#FFFFFF;
                                background-color:#384BEC
                            }
                        }
                    }
                    &::after {
                        background: linear-gradient(270deg, #222222 0%, rgba(34, 34, 34, 0) 100%);
                    }
                    &::before {
                        background: #222;
                    }
                }
                .swiper-btn-wrap {
                    [class^="swiper-button-"] {
                        background-color:#333333;
                    }
                }
                @include mix.respond-to(mobile){
                    background:transparent;
                    .curriculum {
                        margin-top: 3.2rem;
                    }
                }
            }
            .learning {
                &__top {
                    h4 {
                        color:#FFFFFF;
                    }
                    .round-box {
                        > span {
                            color:#999999;
                            > em {
                                color:#999999;
                            }
                        }
                        @include mix.respond-to(mobile){
                            background:transparent;
                        }
                    }
                }
                &__subject {
                    background-color:#242424;
                    border-color:#242424;
                    .item {
                        > button {
                            color:#666666;
                            &.active {
                                color:#FFFFFF;
                            }
                        }
                        & + li {
                            &::after {
                                background-color:#666666;
                            }
                        }
                    }
                }
                .unit-list {
                    border-color:#242424;
                    background-color:#242424;
                    > .table-cont {
                        .table {
                            table {
                                thead {
                                    tr {
                                        th {
                                            background-color:#2C2C2C;
                                        }
                                    }
                                }
                            }
                        }
                        .func {
                            .tag {
                                .item {
                                    > button {
                                        border-color:#333333;
                                        background-color:#333333;
                                        &.active {
                                            background-color: #4E60FF;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            ol.step {
                li {
                    color:#666666;
                    &::before {
                        color:#242424;
                        background-color:#666666;
                    }
                    &::after {
                        border-color:#666666;
                    }
                    &.completed,
                    &.active {
                        color:#4E60FF;
                        &::before {
                            color:#FFFFFF;
                        }
                    }
                    &.completed {
                        &::before {
                            background-color:transparent;
                        }
                        &::after {
                            border-color:#4E60FF;
                        }
                    }
                    &.active {
                        &::before {
                            background-color:#4E60FF;
                        }
                    }
                }
            }
            h4.title {
                color:#FFFFFF;
            }
            h5.title {
                color:#FFFFFF;
            }
            .box-wrap {
                .box {
                    border-color:#242424;
                    background-color:#242424;
                    .tit {
                        color:#999999;
                        &.ico-book {
                            &::before {
                                background-image:url('../images/icon/ico_book_dark.svg');
                            }
                        }
                        &.ico-file {
                            &::before {
                                background-image:url('../images/icon/ico_file2_dark.svg');
                            }
                        }
                        &+.cont {
                            &:has(span + span) {
                                &::before {
                                    background-color:#333333;
                                }
                            }
                        }
                    }
                    .cont {
                        > span {
                            color:#999999;
                            strong {
                                color:#FFFFFF;
                            }
                            &::before {
                                background-color:#333333;
                            }
                        }
                    }
                    &.round-bg {
                        > h5 {
                            color:#FFFFFF;
                        }
                        > span {
                            color:#999999;
                        }
                    }
                    &.banner {
                        > h4 {
                            color:#FFFFFF;
                        }
                        > span {
                            color:#999999;
                        }
                    }
                    &.round {
                        .table-info {
                            > h5 {
                                color:#FFFFFF;
                            }
                            em {
                                color:#999999;
                            }
                        }
                    }
                }
            }
            .round-box {
                color:#999999;
                background-color:#242424;
            }
            input[type="checkbox"] {
                &+label {
                    color:#999999;
                }
            }
            .table-cont {
                .table {
                    table {
                        thead {
                            background:none;
                            tr {
                                th {
                                    color:#999999;
                                    background-color:#242424;
                                }
                            }
                        }
                        tbody {
                            tr {
                                border-color:#333333;
                                td {
                                    color:#999999;
                                    &.subject {
                                        .tit {
                                            strong {
                                                color:#FFFFFF;
                                            }
                                        }
                                    }
                                    &.grade {
                                        color:#EA4F4F;
                                        &.o {
                                            color:#4E60FF;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    &.evaluate {
                        table {
                            thead {
                                tr {
                                    th {
                                        background-color:#333333;
                                        &.font-primary {
                                            color:#4E60FF;
                                        }
                                    }
                                }
                            }
                            tbody {
                                td {
                                    &:nth-of-type(2) {
                                        color:#FFFFFF;
                                    }
                                }
                            }
                        }
                    }
                }
                .btn {
                    background-color:transparent;
                    border: 1px solid rgba(153,153,153,0.4);
                    &.btn-primary {
                         border-color:rgba(160, 52, 255, 0.4);
                    }
                    &.btn-primary2 {
                        border-color:rgba(78, 96, 255, 0.4);
                    }
                }
                @include mix.respond-to(mobile) {
                    .func {
                        .round-box {
                            background-color:transparent;
                        }
                    }
                }
            }
            .lecture-wrap {
                .latest-learn {
                    background-color:transparent;
                }
            }
            .current-wrap {
                .episode {
                    >h5 {
                        color:#FFFFFF;
                    }
                }
            }
        }
        .state {
            border-color:#3D3D3D;
            background: #3D3D3D;
            &.complete {
                color:#00C089;
            }
            &.studying {
                color:#FF9549;
            }
        }
        .episode{
            > h5{
                color: #fff;
            }
        }
    }
}