@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.dashboard-wrap, .classic-wrap {
    padding:6rem 0 14rem;
    color:#333333;
    @include mix.txt-body4;
    background:#F6F7FA;
    font-weight:500;
    .tab-mini {
        .tab-wrap {
            li {
                font-weight:400;
            }
        }
    }
    @include mix.respond-to(terminal){
        padding-bottom:5rem;
        .tab-mini {
            padding:0;
        }
    }
    .inner {


        max-width:128rem;
        margin:0 auto;
        padding:0 2rem;
    }
    h5.title {
        display:flex;
        align-items:center;
        @include mix.txt-tit3;
        font-weight:700;
        @include mix.respond-to(mobile){
            @include mix.txt-body2;
        }
        &+.desc {
            margin-top:1.6rem;
            color:#666666;
            @include mix.txt-body4;
        }
    }
    .calendar-week {
        display:flex;
        align-items:center;
        gap:.2rem;
        margin-top:1rem;
        progress {
           display:block;
            width:100%;
            height:1.6rem;
            border:0;
            border-radius:1rem;
            overflow:hidden;
            transition:width .5s;
            &::-webkit-progress-bar {
                height:100%;
            }
            &::-webkit-progress-value {
                border-radius:1rem;
                transition:width .5s;
            }
        }
        .count {
            margin-left:1rem;
            @include mix.txt-body4;
            font-weight:700;
        }
        .day {
            flex:1 0 auto;
            height:3.2rem;
            padding-top:.5rem;
            color:#333333;
            @include mix.txt-cap2;
            border:.1rem solid #EEEEEE;
            border-radius:.4rem;
            font-weight:400;
            text-align:center;
            &[class*='bg__'] {
                border:0;
                color:#FFFFFF;
            }
        }
        .bg__grade2 {
            background-color:#AAC3FD;
        }
        .bg__grade3 {
            background-color:#5A7CE7;
        }
        .bg__grade4 {
            background-color:#405CC1;
        }
        &.color-blue {
            .progress {
                &::-webkit-progress-bar {
                    background: #E7EAFF;
                }
                &::-webkit-progress-value {
                    background: #5A7CE7;
                }
            }
            .count {
                color:#4E60FF;
            }
            .bg__grade2 {
                background-color:#AAC3FD;
            }
            .bg__grade3 {
                background-color:#5A7CE7;
            }
            .bg__grade4 {
                background-color:#405CC1;
            }
        }
        &.color-pink {
            .progress {
                &::-webkit-progress-bar {
                    background: #FFE8EE;
                }
                &::-webkit-progress-value {
                    background: #F96891;
                }
            }
            .count {
                color:#F96891;
            }
            .bg__grade2 {
                background-color:#FFB6CB;
            }
            .bg__grade3 {
                background-color:#F96891;
            }
            .bg__grade4 {
                background-color:#D92E5F;
            }
        }
    }
    .box-wrap {
        display:flex;
        align-items:center;
        &__title{
            @include mix.txt-tit5;
            font-weight: 700;
            margin: 6rem 0 3.6rem 0;
        }
        .box {
            width:100%;
            padding:4rem 3rem;
            border-radius:1.6rem;
            background-color:#FFFFFF;
            box-shadow: 0 .1rem .3rem 0 #606C8014;
            text-align:center;
            .title {
                @include mix.txt-body1;
                font-weight:600;
                .ico-info {
                    display:flex;
                    gap:.4rem;
                    align-items:center;
                    margin-left: 0.4rem;
                    &::after {
                        display:block;
                        content:'';
                        width:2rem;
                        height:2rem;
                        background:url('../images/icon/ico_question.svg') 0 0 / 100% 100% no-repeat;
                    }
                }
            }
            .desc {
                margin-top:1.2rem;
                color:#666666;
                line-height:2.4rem;
                font-weight:400;
            }
            .btn {
                margin-top:2.8rem;
                padding:2rem;
                color:#FFFFFF;
                background:#4E60FF;
                font-weight:600;
                border-radius:3rem;
                @include mix.respond-to(mobile){
                    width: 100%;
                }
            }
        }
        &:has(.box + .box) {
            gap:2.4rem;
            margin-top:2.4rem;
            &:has(.info) {
                &:has(button.btn) {
                    .box {
                        text-align:left;
                        display:flex;
                        align-items:center;
                        justify-content: space-between;
                    }
                    .info {
                        .title {
                            display:block;
                            @include mix.txt-tit5;
                            font-weight:600;
                        }
                        .desc {
                            display:block;
                            margin-top:1.6rem;
                        }
                    }
                    .btn {
                        margin-top:0;
                        padding:1.4rem 2rem;
                        color:#4E60FF;
                        @include mix.txt-cap1;
                        background:#E7EAFF;
                    }
                }
            }
            &:has(.calendar.calendar-week) {
                align-items:flex-start;
            }
            @include mix.respond-to(terminal){
                &:has(.info) {
                    &:has(button.btn) {
                        display:block;
                        .box {
                            &+.box {
                                margin-top:2.4rem;
                            }
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                &:has(.info) {
                    &:has(button.btn) {
                        .box {
                            display:block;
                            text-align:center;
                            .btn {
                                margin-top:2rem;
                            }
                        }
                    }
                }

            }
        }
        &+.box-wrap {
          margin-top:2.4rem;
            @include mix.respond-to(terminal){
                margin-top:2rem;
            }
        }
    }
    .container {
        &__top {
            .box-wrap {
                margin-top:2.4rem;
                .box {
                    display:flex;
                    align-items:center;
                    justify-content: space-between;
                    padding:2rem 3rem;
                    .info {
                        display:flex;
                        align-items:center;
                        .grade {
                            margin-left:.8rem;
                            padding:.4rem 1rem;
                            color:#4E60FF;
                            @include mix.txt-cap2;
                            background-color:#E7EAFF;
                            font-weight:600;
                            border-radius: 2.4rem;
                        }
                    }
                    button {
                        &.func {
                            display:flex;
                            align-items:center;
                            color:#666666;
                            font-weight:400;
                            &::after {
                                content:'';
                                display:block;
                                width:2rem;
                                height:2rem;
                                margin-left:.8rem;
                                background:url('../images/icon/ico_setting.svg') center center no-repeat;
                                background-size: 100% 100%;
                            }
                        }
                    }
                }
            }
            @include mix.respond-to(terminal){
                margin-top:3rem;
            }
        }
        &__body {
            margin-top:9rem;
            @include mix.respond-to(terminal){
                margin-top:4rem;
            }
            .box-wrap {
                .box {
                    &.round{
                        &.dash-map {
                            display: flex;
                            padding: 2.4rem 0;
                            gap: 1.1rem;
                            @include mix.respond-to(mobile) {
                                flex-wrap: wrap;
                                padding: 0;
                            }
                            dl {
                                position: relative;
                                width: 100%;
                                padding: 0 2.4rem;
                                &:not(:last-child)::before {
                                    content: "";
                                    display: block;
                                    position: absolute;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    width: 0.1rem;
                                    right: -0.6rem;
                                    height: 5.8rem;
                                    background: #EEEEEE;
                                }
                                @include mix.respond-to(mobile) {
                                    display: flex;
                                    align-items: center;
                                    justify-content: space-between;
                                    padding: 2.4rem;
                                    &:not(:last-child)::before {
                                        content: none;
                                    }
                                }
                                + dl {
                                    @include mix.respond-to(mobile) {
                                        padding-top: 0;
                                    }
                                }

                                dt {
                                    position: relative;
                                    text-align: left;
                                    line-height: normal;
                                    white-space: nowrap;
                                    &::before {
                                        content: "";
                                        display: inline-block;
                                        margin-right: 0.8rem;
                                        width: 0.6rem;
                                        height: 0.6rem;
                                        transform: translateY(-50%);
                                        border-radius: 50%;
                                    }
                                    &.none{
                                        color: #999;
                                        &::before{
                                            background: #999;
                                        }
                                    }
                                    &.very-weak{
                                        color: #FE675F;
                                        &::before{
                                            background: #FE675F;
                                        }
                                    }
                                    &.weak{
                                        color: #FA7A16;
                                        &::before{
                                            background: #FA7A16;
                                        }
                                    }
                                    &.normal{
                                        color: #009F7A;
                                        &::before {
                                            background: #009F7A;
                                        }
                                    }
                                    &.excellent{
                                        color: #4D91E5;
                                        &::before{
                                            background: #4D91E5;
                                        }
                                    }
                                    &.very-excellent{
                                        color: #9E40F1;
                                        &::before{
                                            background: #9E40F1;
                                        }
                                    }
                                }
                                dd {
                                    margin-top: 2rem;
                                    font-size:2.8rem;
                                    line-height:2.4rem;
                                    text-align: right;
                                    @include mix.respond-to(mobile) {
                                        margin-top: 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .accom-wrap {
                @include mix.respond-to(mobile) {
                    margin-top: 3.6rem;
                }
            }
        }
    }
    .chart-wrap {
        width: 100%;
        overflow: auto;
        &__title{
            margin-bottom:1rem;
            @include mix.txt-body2;
            font-weight: 600;
            color: #333;
            text-align: left;
        }
    }
    .tab-round {
        display:flex;
        align-items:center;
        justify-content: space-between;
        .days {
            padding:1.3rem 1.4rem;
            color:#999999;
            @include mix.txt-cap1;
            background:#FFFFFF;
            border-radius:5rem;
            em {
                margin-left:.5rem;
                color:#4E60FF;
            }
        }
        .tab-wrap {
            display:flex;
            align-items:center;
            padding:.4rem;
            background:#FFFFFF;
            border-radius:5rem;
            li {
                .btn {
                    display:block;
                    padding:.8rem 2rem;
                    color:#999999;
                    font-weight:400;
                    &.active {
                        color:#FFFFFF;
                        background:#4E60FF;
                        font-weight:600;
                        border-radius:5rem;
                    }
                }
            }
        }
        @media (max-width: 400px) {
            display:block;
            .days {
                margin-top:1rem;
                text-align:center;
            }
            .tab-wrap {
                justify-content: center;
            }
        }
    }
    .calendar-contents {
        text-align:center;
        &:has(.calendar-wrap) {
            margin-top:-4.8rem;
            @include mix.respond-to(mobile){
                margin-top:1rem;
            }
        }
        span {
            color: #666;
            @include mix.txt-body4;
        }
        @include mix.respond-to(mobile){
            margin-top:1rem;
            .calendar-wrap {
                width:100%;
                margin-top:2rem;
                .btn-calendar {
                    width:100%;
                    justify-content: center;
                }
            }
        }
        @media (max-width: 360px) {
            .calendar-wrap {
                padding:1.2rem 3rem;
                button {
                    &.btn__prev {
                        left:1rem;
                    }
                    &.btn__nect {
                        right:1rem;
                    }
                }
            }
        }
        .chart-wrap {
            .select-wrap {
                margin-top:3rem;
                text-align:left;
                select {
                    margin-right:1rem;
                    padding:1.9rem 4.4rem 1.9rem 1.6rem;
                    line-height:1.9rem;
                }
                em {
                    margin:0 .8rem;
                    color:#4E60FF;
                    font-weight:600;
                }
            }
        }
    }
    .chart-contents {
        margin-top:14rem;
    }
    .calendar-wrap {
        display:inline-block;
        position:relative;
        padding:1.2rem 4.6rem 1.2rem 5rem;
        background:#FFFFFF;
        border-radius:5rem;
        > div {
            display:inline-block;
        }
        select {
            padding:0 2.4rem 0 0;
            color:#666666;
            border:0;
            background:url('../images/icon/ico_select_down.svg') no-repeat;
            background-size: 1rem .5rem;
            background-position:right .65rem top 50%;
            border-radius:0;
            text-align:center;
            &+select {
                margin-left:.8rem;
            }
        }
        .btn-calendar {
            display:flex;
            align-items:center;
            gap:.6rem;
            .datepicker {
                width:17.5rem;
                text-align:left;
                cursor:pointer;
                color:#666666;
                background:url('../images/icon/ico_calendar2.svg') center right no-repeat;
                &.week{
                    width: 25rem;
                    @media (max-width: 360px) {
                        width:20rem;
                        font-size:1.2rem;
                    }
                }
            }
        }
        button {
            &[class^='btn__'] {
                position:absolute;
                top:1.45rem;
                width:2rem;
                height:2rem;
                text-indent:-9999em;
                overflow:hidden;
                mask-repeat:no-repeat;
                mask-position:center center;
                mask-size:100% 100%;
                background-color:#666666;
            }
            &.btn__prev {
                left:1.4rem;
                mask-image:url('../images/icon/ico_arr_left2.svg');
            }
            &.btn__next {
                right:1.4rem;
                mask-image:url('../images/icon/ico_arr_right2.svg');
            }
            &.btn-off {
                background-color:#CCCCCC;
                cursor:default;
            }
        }
    }
    .learning-wrap {
        margin-top:2.4rem;
        text-align:left;
        .box-wrap {
            .box {
                padding:3rem;
                > .select-wrap{
                    > p{
                        width: fit-content;
                        display: inline-block;
                        @include mix.respond-to(mobile){
                            width: 100%;
                            margin-top: 1rem
                        }
                    }
                }
            }
            &:has(.box + .box) {
                @media (max-width: 1100px) {
                    flex-direction: column;
                    .box {
                        width:100%;
                        min-width:auto;
                        &.learning-status {
                            ul {
                                li {
                                    width:50%;
                                }
                            }
                        }
                    }
                }
                @include mix.respond-to(mobile){
                    .box {
                        &.learning-status {
                            ul {
                                margin-top:2rem;
                                li {
                                    width:100%;
                                    margin-top:0;
                                }
                            }
                        }
                    }

                }
            }
        }
    }
    .check-wrap {
        display:flex;
        align-items:center;
        gap:2.4rem;
        min-height:4.8rem;
        margin-top:4.8rem;
        .checkbox {
            display:flex;
        }
        @media (max-width: 400px) {
            display:block;
            .checkbox {
                &+.checkbox {
                    margin-top:1rem;
                }
            }
        }
        &+.calendar-contents {
            @media (max-width: 1150px) {
                margin-top:1rem;
                .calendar-wrap {
                    width:100%;
                    .btn-calendar {
                        width:100%;
                        justify-content: center;
                    }
                }
            }
            .calendar-contents  {
                margin-top:-4.8rem;
                text-align:center;
                @include mix.respond-to(mobile){
                    margin-top:1rem;
                    .calendar-wrap {
                        width:100%;
                        .btn-calendar {
                            width:100%;
                            justify-content: center;
                        }
                    }
                }
            }
            .calendar-wrap {
                display:inline-block;
                position:relative;
                padding:1.2rem 4.6rem 1.2rem 5rem;
                background:#FFFFFF;
                border-radius:2.4rem;
                select {
                    padding:0 1.6rem 0 0;
                    border:0;
                    background:url('../images/icon/ico_select_down.svg') no-repeat;
                    background-size:.5rem 1rem;
                    background-position:right .65rem top 50%;
                    border-radius:0;
                    &+select {
                        margin-left:.8rem;
                        padding-right:1.8rem;
                    }
                }
                button {
                    &.btn-calendar {
                        display:flex;
                        align-items:center;
                        gap:.6rem;
                        &::after {
                            content:'';
                            width:1.6rem;
                            height:1.6rem;
                            background-color:#666666;
                            mask-image:url('../images/icon/ico_calendar2.svg');
                            mask-repeat:no-repeat;
                            mask-position:center center;
                            mask-size:100% 100%;
                        }
                    }
                    &[class^='btn__'] {
                        position:absolute;
                        top:1.4rem;
                        width:2rem;
                        height:2rem;
                        text-indent:-9999em;
                        overflow:hidden;
                        mask-repeat:no-repeat;
                        mask-position:center center;
                        mask-size:100% 100%;
                        background-color:#666666;
                    }
                    &.btn__prev {
                        left:1.4rem;
                        mask-image:url('../images/icon/ico_arr_left2.svg');
                    }
                    &.btn__next {
                        right:1.4rem;
                        mask-image:url('../images/icon/ico_arr_right2.svg');
                    }
                    &.btn-off {
                        background-color:#CCCCCC;
                        cursor:default;
                    }
                }
            }
            .learning-wrap {
                margin-top:2.4rem;
            }
        }
        @include mix.respond-to(mobile){
            min-height:auto;
            margin-top:3.6rem;
        }
    }
    .chart-contents {
        &:not(:has(.check-wrap)) {
            .calendar-contents {
                margin-top:4.8rem;
            }
        }

    }
    .learning-status {
        .title {
            display:none;
        }
        ul {
            display:flex;
            align-items:center;
            flex-wrap:wrap;
        }
        li {
            position:relative;
            flex:1 0 auto;
            display:flex;
            align-items:center;
            gap:2rem;
            margin-right:3rem;
            padding-right:2rem;
            .info {
                text-align:left;
                em {
                    display:block;
                    color:#999999;
                    @include mix.txt-body4;
                }
                strong {
                    display:block;
                    color:#333333;
                    @include mix.txt-body2;
                    font-weight:600;
                }
            }
            &::before {
                display:block;
                content:'';
                width:5.6rem;
                height:5.6rem;
                background-repeat:no-repeat;
                background-color:#F6F7FA;
                border-radius:2.8rem;
                background-position:center center;
                background-size:3.2rem 3.2rem;
            }
            &::after {
                display:block;
                position:absolute;
                top:0;
                right:0;
                content:'';
                width:.1rem;
                height:5.8rem;
                background:#eeeeee;
            }
            &:last-child {
                margin-right:0;
                &::after {
                    display:none;
                }
            }
            &.learned {
                &::before {
                    background-image:url('../images/icon/ico_learned.svg')
                }
            }
            &.learned-time {
                &::before {
                    background-image:url('../images/icon/ico_learned_time.svg')
                }
            }
            &.my-rate {
                &::before {
                    background-image:url('../images/icon/ico_my_rate.svg')
                }
            }
            &.test {
                &::before {
                    background-image:url('../images/icon/ico_test.svg')
                }
            }
            &.test-time {
                &::before {
                    background-image:url('../images/icon/ico_test_time.svg')
                }
            }
            @media (max-width: 1280px) {
                &:nth-child(4) {
                    &::after {
                        display:none;
                    }
                }
                &:nth-of-type(5) {
                    margin-top:2.4rem;
                }
            }
        }
        @media (max-width: 1150px) {
            li {
                width:calc(50% - 3rem);
                margin-top:2.4rem;
                padding:0;
                &:first-of-type,
                &:nth-of-type(2) {
                    margin-top:0;
                }
                .info {
                    em {
                        @include mix.txt-cap1;
                    }
                    strong {
                        @include mix.txt-body3;
                    }
                }
                &:nth-child(2n) {
                    &::after {
                        display:none;
                    }
                }
            }
            &:not(:has(li:nth-of-type(5))) {
                li {
                    width:calc(50% - 1.5rem);
                    margin:3rem 0 0;
                    &:first-of-type,
                    &:nth-of-type(2) {
                        margin-top:0;
                    }
                    &:nth-of-type(odd) {
                        margin-right:3rem;
                    }
                }
            }
        }
        @include mix.respond-to(mobile){
            display:block;
            li,
            &:not(:has(li:nth-of-type(5))) li {
                width:100%;
                margin-top:0;
                padding:2.4rem 0;
                border-top:.1rem solid #EEEEEE;
                &:first-of-type {
                    margin-top:0;
                    padding-top:0;
                    border:0;
                }
                &:last-of-type {
                    padding-bottom:0;
                }
                &::after {
                    display:none;
                }
            }
        }
        &:not(:has(.my-rate)) {
            width:33.5%;
            min-width:30rem;
            height:53.2rem;
            &+.box {
                height:53.2rem;
            }
            .title {
                display:block;
                @include mix.txt-body2;
                font-weight:600;
                text-align:left;
            }
            ul {
                margin-top:4.8rem;
                li {
                    margin-top:5.8rem;
                    &::before {
                        width:4rem;
                        height:4rem;
                        background-size:2.286rem 2.286rem;
                    }
                    &:first-child {
                        margin-top:0;
                    }
                    &::after {
                        display:none;
                    }
                }
            }
            @media (max-width: 1100px) {
                height: auto;
                &+.box {
                    height: auto;
                }
                ul {
                    margin-top:0;
                    li {
                        width: 100%;
                        margin-top:4rem;
                    }
                }
            }
        }
    }
    .learning-state {
        position:relative;
        .title{
            display: flex;
        }
        > .toggle-wrap {
            position:absolute;
            top:0;
            right:0;
            display:flex;
            align-items:center;
            .toggle-box {
                display:flex;
                margin:0 .8rem;
                .toggle {
                    width:5rem;
                    height:2.6rem;
                    .toggle-slider {
                        &::before {
                            left:.4rem;
                            width:1.8rem;
                            height:1.8rem;
                        }
                    }
                    input:checked {
                        &+.toggle-slider {
                            &::before {
                                transform:translateX(2.4rem);
                            }
                        }
                    }
                }
            }
            span {
                color:#999999;
                @include mix.txt-cap1;
                font-weight:400;
            }
            &:not(:has(.toggle-box input[type="checkbox"]:checked)) {
                .toggle-box {
                    .toggle {
                        background-color:#5A7CE7;
                    }
                }
                span {
                    &.time {
                        color:#5A7CE7;
                        font-weight:600;
                    }
                }
                &+.calendar-week {
                    .bg__grade2 {
                        background-color:#AAC3FD;
                    }
                    .bg__grade3 {
                        background-color:#5A7CE7;
                    }
                    .bg__grade4 {
                        background-color:#405CC1;
                    }
                }
            }
            &:has(.toggle-box input[type="checkbox"]:checked) {
                .toggle-box {
                    .toggle {
                        background-color:#FB5D8A;
                    }
                }
                span {
                    &.test {
                        color:#F96891;
                        font-weight:600;
                    }
                }
                &+.calendar-week {
                    .bg__grade2 {
                        background-color:#FFB6CB;
                    }
                    .bg__grade3 {
                        background-color:#F96891;
                    }
                    .bg__grade4 {
                        background-color:#D92E5F;
                    }
                }
            }
            @media (max-width: 475px) {
                position:static;
                margin-top:1.2rem;
            }
        }
        > .calendar {
            margin-top:2.8rem;
            table {
                width:100%;
                thead {
                    tr {
                        th {
                            padding:0 0 1.2rem;
                            color:#999999;
                            @include mix.txt-cap2;
                            font-weight:600;
                        }
                    }
                }
                tbody {
                    tr {
                        &:not(:empty) {
                            border-top:.1rem solid #EEEEEE;
                        }
                        td {
                            padding:.4rem;
                            color:#333333;
                            @include mix.txt-cap2;
                            font-weight:400;
                            span {
                                display:block;
                                padding:.6rem 0 3.2rem;
                                border-radius:1.2rem;
                                text-align:center;
                                &[class*='bg__grade'] {
                                    color:#FFFFFF;
                                }
                            }
                            &.day-off {
                                span {
                                    color:#AAAAAA;
                                    background:none !important;
                                }
                            }
                            &.today {
                                span {
                                    position:relative;
                                    font-weight:700;
                                    &::after {
                                        content:'';
                                        display:block;
                                        position:absolute;
                                        top:50%;
                                        left:50%;
                                        width:.6rem;
                                        height:.6rem;
                                        margin:.6rem auto 0;
                                        border-radius:.3rem;
                                        background-color:#333333;
                                        transform:translate(-50%,-50%);
                                    }
                                    &[class*='bg__grade'] {
                                        &::after {
                                            background-color:#FFFFFF;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .learning-info {
        display:flex;
        .learning-state {
            width:calc(72% - 2.8rem);
            padding-right:2.3%;
            .title {
                display:flex;
                @include mix.txt-body2;
            }
            ul.info {
                display:flex;
                align-items:center;
                justify-content: space-between;
                gap:4rem;
                margin-top:3.2rem;
                li {
                    width:50%;
                    text-align:left;
                    em {
                        display:block;
                        color:#999999;
                        @include mix.txt-body4;
                    }
                    strong {
                        display:block;
                        margin-top: 1rem;
                        color:#333333;
                        @include mix.txt-body3;
                        font-weight:600;
                    }
                }
            }
            &+.learning-state {
                width:28%;
                position:relative;
                margin-left:1.4rem;
                padding-left:calc(2.3% + 1.3rem);
                &::before {
                    content:'';
                    position:absolute;
                    top:50%;
                    left:0;
                    width:.1rem;
                    height:100%;
                    transform:translateY(-50%);
                    background-color:#EEEEEE;
                }
                ul.info {
                    gap:0;
                }
            }
        }
        @include mix.respond-to(terminal){
            flex-wrap:wrap;
            .learning-state {
                width:100%;
                padding-right:0;
                ul.info {
                    margin-top:2.4rem;
                    li {
                        strong {
                            margin-top:.8rem;
                        }
                    }
                }
                @include mix.respond-to(mobile){
                    ul.info {
                        display:block;
                        li {
                            width:100%;
                            &+li {
                                margin-top:2rem;
                            }
                        }
                    }
                }
                &+.learning-state {
                    width:100%;
                    margin:3.2rem 0 0 0;
                    padding:3.2rem 0 0;
                    border-top:.1rem solid #EEEEEE;
                    &::before {
                        display:none;
                    }
                    @include mix.respond-to(mobile){
                        .title {
                            @include mix.txt-body3;
                        }
                        ul.info {
                            display:flex;
                            li {
                                margin-top:0;
                                em {
                                    @include mix.txt-cap1;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .text-info {
        display:flex;
        align-items:center;
        justify-content: space-between;
        flex-wrap:wrap;
        margin-top:3rem;
        padding:3rem;
        background-color:#F6F7FA;
        border-radius:1.2rem;
        .text {
            p {
                display:flex;
                gap:.6rem;
                color:#999999;
                font-weight:400;
                text-align:left;
                &::before {
                    position:relative;
                    display:block;
                    content:'';
                    top:.9rem;
                    width:.4rem;
                    height:.4rem;
                    background-color:#999999;
                    border-radius:.2rem;
                }
                &+p {
                    margin-top:1.2rem;
                }
            }
        }
        button {
            margin:1.2rem 0;
            padding:1rem 1.2rem;
            color:#999999;
            @include mix.txt-cap2;
            background-color:#FFFFFF;
            border:0.1rem solid #EEEEEE;
            border-radius:.8rem;
        }
        @include mix.respond-to(terminal){
            button {
                margin:1.2rem auto 0;
            }
        }
        @include mix.respond-to(mobile){
            p {
                @include mix.txt-cap1;
            }
        }
    }
    .accom-wrap{
        margin-top: 4.8rem;
        text-align: center;
    }
    .ui-widget.ui-widget-content{
        /*   */
        border:0;
        box-shadow: .5rem .5rem 1rem #ccc;
        padding:0;
    }
    .ui-widget {
        width:30.6rem;
    }
}
