@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.profile-wrap {
    width: 100%;
    position: relative;
    padding:9rem 0 14rem;
    background:#F6F7FA;
    @include mix.txt-body4;
    font-weight: 500;
    @include mix.respond-to(terminal) {
        padding: 10.6rem 0 7rem;
    }
    h4.title {
        @include mix.txt-tit3;
        font-weight:700;
        @include mix.respond-to(mobile){
            @include mix.txt-tit3;
        }
    }
    h5.sub-title {
        padding: 6rem 0 3.6rem;
        @include mix.txt-tit5;
        font-weight:700;
        @include mix.respond-to(mobile) {
            padding: 4rem 0 2.4rem;
        }
    }
    h6.box-title {
        @include mix.txt-body2;
        color: #333;
        font-weight: 500;
    }
    p.sub-title {
        margin-bottom: 2.4rem;
        text-align: left;
    }
    > .inner {
        position: relative;
        max-width: 128rem;
        margin: 0 auto;
        padding: 0 2rem;
    }
    .box-wrap {
        display: flex;
        align-items: center; 
        gap: 2.4rem;
        &.table-wrap {
            width: 100%;
            .box {
                width: 100%;
            }
            +.table-wrap {
                margin-top: 2.4rem;
            }
        }
        .box {
            display: flex;
            flex-wrap: wrap;
            padding: 3rem;
            box-shadow: 0rem 0.1rem 0.3rem 0rem rgba(96, 108, 128, 0.08); 
            background-color: #fff;
            &.round {
                border-radius:  1.6rem;
            }
        }
    }
    .table {
        @include mix.respond-to(mobile){
            padding-left: 0;
            padding-right: 0;
        }
        &.grade {
            table {
                tbody {
                    tr {
                        td {
                            @include mix.txt-body3;
                            color: #333;
                        }
                    }
                }
            }
        }
        &.ranking {
            table {
                tbody {
                    tr {
                        td {
                            &.light {
                                color: #999;
                            }
                        }
                        td {
                            color: #333;
                        }
                    }
                }
            }
        }
        &.select-grade {
            width: calc(100% + 2.8rem);
            margin-left: -1.4rem;
            margin-right: -1.4rem;
            thead {
                tr {
                    th {
                        padding-top: 0;
                        padding-left: 1.4rem;
                        padding-right: 1.4rem;
                        color: #333;
                        text-align: left;
                        font-weight: 500;
                        background-color: #fff;
                    }
                }
            }
            tbody {
                tr {
                    border-bottom: 0 none;
                    td {
                        padding: 0 1.4rem;
                        > select {
                            width: 100%;
                        }
                    }
                }
            }
        }
        &.search {
            dl {
                line-height: 1.9rem;
                font-weight: 500;
                font-size: 1.6rem;
                border-bottom: 1px solid #EEE;
                &:first-child {
                    display: flex;
                    align-items: center;
                    gap: 0.8rem;
                    padding: 1.8rem 1.6rem;
                    text-align: center;
                    background-color: #F6F7FA;
                    border-radius: 0.8rem;
                    border-bottom: none;
                    dt,
                    dd {
                        font-weight: 500;
                        color: #999999;
                    }
                }
                &.active {
                    background-color: #F6F7FA;
                    dt,
                    dd {
                        color: #4E60FF;
                    }
                }
            }
            a {
                display: flex;
                align-items: center;
                gap: 0.8rem;
                padding: 1.8rem 1.6rem;
                text-align: center;
            }
            dt {
                width: 19.23%;
                color: #999;
            }
            dd {
                width: 40.38%;
                color: #666;
                @include mix.text-overflow(1);
            }
        }
    }
    .grade-wrap {
        width: 100%;
        .title-wrap {
            display: flex;
            align-items: center;
            gap: 2rem;
            &:not(.between) {
                @media (max-width: 370px) {
                    flex-direction: column;
                    align-items: flex-start;
                }
            }
            &.between {
                justify-content: space-between;
            }
            + .select-grade {
                margin-top: 3rem;
            }
            .tag {
                display: flex;
                gap: 0.8rem;
                padding: 1.4rem 1.8rem;
                color:#666666;
                background-color: #F6F7FA;
                border-radius: 5rem;
                font-weight:500;
                strong {
                    color: #4E60FF;
                    font-weight: 600;
                }
            }
            +.table {
                margin-top: 3rem;
            }
        }
        ul.select-grade {
            display: flex;
            // flex-wrap: wrap;
            gap: 2.4rem;
            color: #333;
            text-align: left;
            font-weight: 500;
            @include mix.respond-to(terminal){
                flex-direction: column;
            }
            li {
                flex: 1;
                > .title {
                    color: #333;
                    text-align: left;
                    font-weight: 500;
                }
                > div {
                    padding-top: 1.2rem;
                    select {
                        width: 100%;
                        font-weight:400;
                    }
                }
            }
        }
        +.grade-wrap {
            margin-top: 6rem;
        }
    }
    .section {
        &.basic-info {
            .box-wrap {
                align-items: stretch;
                @include mix.respond-to(terminal2) {
                    flex-direction: column;
                }
                .box {
                    &.round {
                        &:nth-of-type(1) {
                            width: 25.6rem;
                            gap: 1.2rem;
                            @include mix.respond-to(terminal2) {
                                width: 100%;
                            }
                        }
                        .img-wrap {
                            position: relative;
                            border-radius: 50%;
                            overflow: hidden;
                            background-color: #F6F7FA;
                            &.small {
                                width: 4.8rem;
                                height: 4.8rem;
                            }
                            &.large {
                                width: 12rem;
                                height: 12rem;
                            }
                            img {
                                position: absolute;
                                left: 50%;
                                top: 50%;
                                width: 100%;
                                transform: translate(-50%, -50%);
                            }
                        }
                        .txt-wrap {
                            .age {
                                color: #999;
                                @include mix.txt-body4;
                                font-weight: 400;
                            }
                            .name {
                                margin-top: 0.4rem;
                                color: #333;
                                font-weight: 600;
                                @include mix.txt-body4;
                                @include mix.text-overflow(1);
                            }
                        }
                    }
                    +.box {
                        width: calc(100% - 25.6rem - 2.4rem);
                        padding-left: 0;
                        padding-right: 0;
                        @include mix.respond-to(terminal2) {
                            width: 100%;
                        }
                        @include mix.respond-to(terminal) {
                            flex-direction: column;
                        }
                        .input-box {
                            display: flex;
                            flex: 1;
                            position: relative;
                            flex-direction: column;
                            margin-top: 0;
                            padding: 0 3rem;
                            .title {
                                display: inline-block;
                                @include mix.txt-body4;
                                color: #999;
                                font-weight: 400;
                                &.ess {
                                    &::after {
                                        content: '*';
                                        display: inline-block;
                                        margin-left: 0.5rem;
                                        color: #EA4F4F;
                                    }
                                }
                            }
                            > .input-wrap {
                                display: flex;
                                gap: 1rem;
                                align-items: center;
                                height: auto;
                                border: 0 none;
                                &.text {
                                    height: auto;
                                    @include mix.txt-body4;
                                    > strong {
                                        margin-top:.4rem;
                                        font-weight: 600;
                                        @include mix.text-overflow(1);
                                        color: #333;
                                    }
                                }
                                > .input-field {
                                    height: 6.4rem;
                                    padding: 0 1.6rem;
                                    font-weight:400;
                                    &:first-of-type {
                                        width: calc(100% - 10rem);
                                    }
                                    &::placeholder {
                                        color: #999999;
                                    }
                                }   
                                > .input-round {
                                    display: flex;
                                    width: 7rem;
                                    height: 100%;
                                    align-items: center;
                                    position: relative;
                                    margin: 0;
                                    padding: 0 1.6rem;
                                    border: .1rem solid #eee;
                                    border-radius: 0.8rem;
                                    background-color: #fff;
                                    .input-field {
                                        width: 100%;
                                        border: 0 none;
                                        padding: 0;
                                    }
                                    .text {
                                        position: absolute;
                                        right: 1.6rem;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        display: inline-block;
                                        @include mix.txt-cap2;
                                        color: #999;
                                    }
                                    + .input-field {
                                        width: calc(100% - 25rem);
                                    }
                                }
                            }
                            .info-text {
                                margin-top: 1rem;
                                font-weight: 400;
                            }
                            + .input-box {
                                @include mix.respond-to(terminal) {
                                    margin-top:  3rem;
                                    padding-top: 3rem;
                                }
                                &::before {
                                    content: '';
                                    display: inline-flex;
                                    position: absolute;
                                    left: 0;
                                    top: 0;
                                    width: .1rem;
                                    height: 100%;
                                    background-color: #eee;
                                    @include mix.respond-to(terminal) {
                                        left: 3rem;
                                        right: 3rem;
                                        width: calc(100% - 6rem);
                                        height: .1rem;
                                    }
                                }
                            }
                        }
                    }
                }   
            }
        }
        &.subject-interest {
            .box-wrap {
                align-items: stretch;
                @include mix.respond-to(terminal) {
                    flex-direction: column;
                }
                .title-wrap {
                    display: flex;
                    align-items: center;
                    padding-bottom: 3rem;
                    &.between {
                        justify-content: space-between;
                    }
                    @include mix.respond-to(mobile) {
                        padding-bottom: 2rem;
                    }
                }
                .box {
                    flex: 1;
                    flex-direction: column;
                    .ranking-wrap {
                        dl {
                            dt {
                                display: inline-block;
                                width:3.7rem;
                                margin-right: 1.6rem;
                                color: #999;
                            }
                            dd {
                                display: inline-block;
                                color: #333;
                            }
                            + dl {
                                margin-top: 1.2rem;
                            }
                        }
                    }
                }
            }
        }
    }
    .bottom-wrap {
        display: flex;
        justify-content: center;
        gap: 1.6rem;
        margin-top: 6rem;
        @include mix.respond-to(mobile) {
            margin-top: 4rem;
            .btn {
                &.large {
                    max-width: 16rem;
                    min-width: 0;
                    width: 50%;
                }
            }
        }
    }
    &.edit {
        .section {
            &.basic-info {
                .box-wrap {
                    .box {
                        &.round {
                            &:nth-of-type(1) {
                                flex-direction: column;
                                align-items: center;
                                .btn {
                                    margin-top: 1.2rem;
                                }
                            }
                            .thumbnail {
                                width: 12rem;
                                height: 12rem;
                            }
                        }
                        + .box {
                            .input-box {
                                flex: auto;
                                width: 50%;
                                margin-top: 0;
                                @include mix.respond-to(terminal){
                                    width: 100%;
                                    padding-top: 0;
                                    &:nth-of-type(2) {
                                        margin-top: 3rem;
                                    }
                                }
                                &:nth-of-type(3) {
                                    margin-top: 3rem;
                                }
                                &.school-info{
                                    margin-top: 3rem;
                                    @include mix.respond-to(terminal){
                                        .input-wrap {
                                            flex-wrap: wrap;
                                            gap: normal normal;
                                            > .input-round {
                                                width: calc(50% - .5rem);
                                                height: 6.4rem;
                                                margin-bottom: 1rem;
                                                + .input-round {
                                                    margin-left: 1rem;
                                                }
                                                + .input-field {
                                                    width: calc(100% - 10rem);
                                                    + .btn {
                                                        margin-left: 1rem;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                .title {
                                    color: #333;
                                    margin-bottom: 1.2rem;
                                }
                                .select-wrap {
                                    display: flex;
                                    gap: 1rem;
                                    select[name="region"] {
                                        flex: 0.5;
                                    }
                                }
                                + .input-box {
                                    &::before {
                                        display: none;
                                    }
                                }
                            }
                        }
                    }
                }           
            }
        }
    }
    &:not(.edit) {
        .section {
            &.basic-info {
                .box-wrap {
                    .box {
                        + .box {
                            .input-box {
                                .title {
                                    margin-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .btn {
        &.type-1 {
            background-color: #4E60FF;
            color: #fff;
        }
        &.type-2 {
            border: .1rem solid #4E60FF;
            color: #4E60FF;
        }
        &.type-3 {
            border: .1rem solid #ccc;
            color: #999;
        }
        &.small {
            padding: 0 1.2rem;
            height: 3.8rem;
            @include mix.txt-cap2;
            line-height: 3.8rem;
            border-radius: 0.8rem;
            &.type-3 {
                line-height: 3.6rem;
            }
        }
        &.medium {
            padding: 0 2rem;
            height: 5rem;
            @include mix.txt-cap1;
            line-height: 5rem;
            border-radius: 10rem;
        }
        &.large  {
            min-width: 16rem;
            padding: 0 2.4rem;
            height: 6.4rem;
            @include mix.txt-body4;
            line-height: 6.4rem;
            border-radius: 5rem;
            &.square {
                min-width: 9rem;
                padding: 0;
                border-radius: 0.8rem;
            }
        }
    }
    .input-field {
        border: .1rem solid #eee;
        background-color: #fff;
        padding: 0 1.6rem;
        @include mix.txt-body4;
        height: 6.4rem;
        line-height: 6.2rem;
        border-radius: 0.8rem;
    }
    .info-text {
        @include mix.txt-cap2;
        color: #999;
    }
}