@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.onestop, .classic-wrap {
    .container {
        &__top {
            padding:4rem 0;
            @include mix.respond-to(terminal){
                padding:2rem 0;
            }
            &+.container__body {
                &.inner {
                    @include mix.respond-to(mobile){
                        padding:0;
                        margin-top: 0;
                    }
                }
            }
        }
        &__body {
            margin-top:5rem;
            &.inner {
                @include mix.respond-to(mobile){
                    padding:0;
                    margin-top: 2rem;
                }
            }
        }
    }
    ol.step {
        display:flex;
        margin:0 auto;
        padding: 3rem 0;
        li {
            position:relative;
            width:25%;
            color:#C5CBE8;
            @include mix.txt-body3;
            font-weight:500;
            text-align:center;
            &::before {
                content:'';
                display:flex;
                align-items:center;
                justify-content: center;
                width:2.8rem;
                height:2.8rem;
                margin:0 auto 1.1rem;
                color:#FFFFFF;
                @include mix.txt-cap1;
                background: #C5CBE8;
                font-weight:700;
                border-radius:1.4rem;
            }
            &::after {
                content:'';
                position:absolute;
                top:1.4rem;
                left: calc(50% + 3.1rem);
                width: calc(100% - 6.2rem);
                border-bottom: 2px dashed #E1E5F7;
            }
            &:nth-child(1) {
                &::before {
                    content:'1';
                }
            }
            &:nth-child(2) {
                &::before {
                    content:'2';
                }
            }
            &:nth-child(3) {
                &::before {
                    content:'3';
                }
            }
            &:nth-child(4) {
                &::before {
                    content:'4';
                }
                &::after {
                    display:none;
                }
            }
            &.completed {
                color:#4E60FF;
                // color:var.$color-primary;
                &::before {
                    content:'';
                    border: 2px solid #4E60FF;
                    background:transparent url('../images/icon/ico_step_check.svg') center center no-repeat;
                    background-size:1.34rem 1.005rem;
                }
                &::after {
                    border-bottom: 2px dashed #4E60FF;
                }
            }
            &.active {
                color:#4E60FF;
                font-weight:600;
                // color:var.$color-primary;
                &::before {
                    background-color:#4E60FF;
                }
            }
            @include mix.respond-to(mobile){
                @include mix.txt-cap1;
                &::before {
                    width:2.6rem;
                    height:2.6rem;
                }
                &::after {
                    left:calc(50% + 2.5rem);
                    width:calc(100% - 5rem);
                }
                &.active {
                    font-weight:500;
                }
            }
        }
        @include mix.respond-to(terminal){
            padding:2.4rem 0;
        }
    }
    h4.title {
        display:flex;
        align-items:center;
        @include mix.txt-tit1;
        font-weight:700;
        @include mix.respond-to(mobile){
            padding:0 2rem;
            @include mix.txt-tit3;
        }
        .round-box {
            padding:1.4rem 1.8rem;
            @include mix.txt-body4;
            font-weight:500;
            margin-left: 2rem;
            > span {
                display:flex;
                align-items:center;
                margin-left:.5rem;
                @include mix.txt-cap2;
                font-weight:600;
                > em {
                    display:block;
                    margin-left:.5rem;
                    @include mix.txt-body4;
                }
                > strong {
                    display:block;
                    margin-right:.5rem;
                    color:#4E60FF;
                    @include mix.txt-body4;
                }
            }
            @include mix.respond-to(mobile){
                padding:0;
                background:none;
            }
        }
    }
    h5.title {
        display:flex;
        align-items:center;
        @include mix.txt-tit4;
        font-weight:700;
        flex-wrap:wrap;
        .sub {
            display:flex;
            align-items:center;
            margin-left:1.6rem;
            color:#4E60FF;
            font-weight:600;
            &::before {
                content:'';
                width:.1rem;
                height:1.6rem;
                margin-right:1.6rem;
                background:#CCCCCC;
            }
        }
        @include mix.respond-to(mobile){
            flex-direction: column;
            align-items:flex-start;
            gap:1rem;
            padding:0 2rem;
            @include mix.txt-body2;
            .sub {
                margin-left:0;
                &::before {
                    display:none;
                }
            }
        }
    }
    .box-wrap {
        display:flex;
        align-items:center;
        justify-content: center;
        margin-top: 2.4rem;
        gap:2.4rem;
        @include mix.txt-body4;
        @include mix.respond-to(terminal){
            flex-direction: column;
            .box {
                width: 100%;
            }
        }
        .box {
            flex:1;
            padding:3.4rem 3rem;
            border:1px solid #EEEEEE;
            &.round {
                border-radius:1.6rem;
                &-bg {
                    width: 100%;
                    padding:4rem 3rem;
                    border-radius: 2rem;
                    background-color: #F6F7FA;
                    border: none !important;
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    .studying-subject {
                        color:#4E60FF;
                        font-weight:600;
                        line-height:1.9rem;
                        &+h5 {
                            margin-top:1.8rem;
                        }
                    }
                    > h5{
                        @include mix.txt-body1;
                        font-weight: 600;
                        color: #333;
                        text-align:center;
                    }
                    > span{
                        @include mix.txt-body4;
                        font-weight: 400;
                        color: #666;
                        margin-top: 1.2rem;
                        text-align:center;
                        > strong {
                            font-weight:600;
                        }
                        em {
                            font-weight:500;
                        }
                    }
                    em {
                        margin-left:.5rem;
                        &.point {
                            color:#EA4F4F;
                        }
                    }
                    > .tag-box{
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 1.2rem;
                        > span{
                            @include mix.txt-body4;
                            font-weight: 500;
                            color: #666;
                            padding-left: 1.3rem;
                            position: relative;
                            display: block;
                            &::before{
                                content: '#';
                                @include mix.txt-body4;
                                font-weight: 500;
                                color: #666;
                                position: absolute;
                                top: 50%;
                                transform: translateY(-50%);
                                left: 0;
                            }
                            & + span{
                                margin-left: 0.6rem;
                            }
                        }
                    }
                    .btn-wrap {
                        width:auto;
                        margin-top: 2.8rem;
                        > button{
                            width: 16rem;
                            padding:2rem 0;
                            color:#fff;
                            @include mix.txt-body4;
                            background-color:#4E60FF;
                            border-radius:5rem;
                            border:1px solid #4E60FF;
                            &.border {
                                color:#4E60FF;
                                @include mix.txt-body4;
                                font-weight:600;
                                background:transparent;
                                border:1px solid #4E60FF;
                            }
                        }
                        &:has(button + button) {
                            button {
                                width:21rem;
                                &+button {
                                    margin-left:1.6rem;
                                }
                            }
                        }
                        @include mix.respond-to(mobile){
                            display:block;
                            &:has(button + button) {
                                button {
                                    width:100%;
                                    &+button {
                                        margin:1.2rem 0 0;
                                    }
                                }
                            }
                        }
                    }
                }
                .table-info{
                    width: 100%;
                    text-align: center;
                    padding-bottom: 4rem;
                    > h5{
                        @include mix.txt-body1;
                        font-weight: 600;
                        color: #333;
                    }
                    em{
                        @include mix.txt-body4;
                        font-weight: 400;
                        color: #666;
                        padding-top: 1.2rem;
                        display: inline-block;
                        > strong{
                            margin-left:.5rem;
                            font-weight: 600;
                        }
                    }
                }
            }
            &.banner{
                padding: 4rem;
                border-radius: 3.2rem;
                display: flex;
                flex-direction: column;
                position: relative;
                &::after{
                    content: '';
                    width: 14rem;
                    height: 14rem;
                    display: inline-block;
                    position: absolute;
                    bottom: 4rem;
                    right: 4rem;
                }
                > h4{
                    @include mix.txt-tit5;
                    font-weight: 700;
                    color: #333;
                }
                > span{
                    @include mix.txt-body4;
                    font-weight: 400;
                    color: #666;
                    margin-top: 1.6rem;
                }
                .btn-wrap {
                    width:auto;
                    justify-content: flex-start;
                }
                button{
                    width: fit-content;
                    padding:1.4rem 2rem;
                    color:#4E60FF;
                    @include mix.txt-cap1;
                    background-color:#E7EAFF;
                    border-radius:10rem;
                    margin-top: 7.2rem;
                }
                &:nth-of-type(1){
                    &::after {
                        background: url('../images/img/img_final-result_bn01_img.svg') right bottom no-repeat;
                    }
                }
                &:nth-of-type(2) {
                    &::after{
                        background: url('../images/img/img_final-result_bn02_img.svg') right bottom no-repeat;
                    }
                }
                &:hover {
                    padding:3.9rem;
                    border:2px solid #4E60FF;
                    button{
                        color: #fff;
                        background-color: #4E60FF;
                    }
                    &::after {
                        right:3.9rem;
                        bottom:3.9rem;
                    }
                }
                @include mix.respond-to(mobile){
                    button {
                        width:100%;
                        margin-top: 17rem;
                    }
                    &::after {
                        bottom:10rem;
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                    }
                    &:hover {
                        &::after {
                            bottom:9.9rem;
                            right:auto;
                        }
                    }
                }
            }
            .cont {
                position:relative;
                display: flex;
                align-items:center;
                justify-content: space-between;
                > span {
                    padding-left:7.6rem;
                    color:#666666;
                    font-weight:500;
                    line-height:1.6rem;
                    strong {
                        display:block;
                        color:#333333;
                        @include mix.txt-body3;
                        font-weight:600;
                        > em{
                            @include mix.txt-body2;
                            font-weight: 600;
                            color: #999;
                        }
                        &[class^='ico-'] {
                            display: flex;
                            align-items: center;
                            cursor: pointer;
                            &::after {
                                content: '';
                                width: 2.8rem;
                                height: 2.8rem;
                                display: inline-block;
                                margin-left: 0.8rem;
                                background-size:100% 100%;
                                background-repeat:no-repeat;
                                @include mix.respond-to(mobile){
                                    width:2.4rem;
                                    height:2.4rem;
                                }
                            }
                        }
                        &.ico-mark{
                            position: relative;
                            &::after{
                                background-image: url('../images/icon/ico_mark.svg');
                            }
                            @include mix.respond-to(mobile){
                                .tooltip-box{
                                    min-width: auto;
                                    width: 22rem;
                                }
                            }
                        }
                    }
                    br {
                        display: none;
                    }
                    &::before {
                        content:'';
                        display:block;
                        position:absolute;
                        top:50%;
                        left:0;
                        width:5.6rem;
                        height:5.6rem;
                        background:#F6F7FA url('../images/icon/ico_book2.svg') center center no-repeat;
                        border-radius:2.8rem;
                        background-size:3.2rem 3.2rem;
                        transform: translateY(-50%);
                    }
                }
                .btn {
                    padding:1.4rem 2rem;
                    color:#FFFFFF;
                    @include mix.txt-cap1;
                    background-color:#4E60FF;
                    border-radius:5rem;
                    font-weight:600;
                    border:1px solid #4E60FF;
                    &.border {
                        color:#4E60FF;
                        @include mix.txt-cap1;
                        font-weight:600;
                        background:transparent;
                        border:1px solid #4E60FF;
                    }
                    &+.btn {
                        margin-left:1.2rem;
                    }
                }
                @include mix.respond-to(mobile){
                    flex-direction: column;
                    > span {
                        padding:0;
                        line-height:2.4rem;
                        text-align:center;
                        font-weight:400;
                        &::before {
                            display:none;
                        }
                        br {
                            display:block;
                        }
                    }
                    .btn {
                        margin-top: 2.4rem;
                        padding:1.6rem 2.4rem;
                        @include mix.txt-body4;
                    }
                }
            }
            .ico-question {
                display:flex;
                align-items:center;
                cursor: pointer;
                &::after {
                    content: '';
                    width: 2rem;
                    height: 2rem;
                    display: inline-block;
                    margin-left: 0.4rem;
                    background-size:100% 100%;
                    background-repeat:no-repeat;
                    @include mix.respond-to(mobile){
                        width:2.4rem;
                        height:2.4rem;
                    }
                }
                &::after {
                    background-image:url('../images/icon/ico_question.svg');
                }
            }
            .tit {
                display:flex;
                align-items:center;
                color:#666666;
                font-weight:600;
                line-height:1.9rem;
                & + .cont {
                    margin-top:3.2rem;
                    &:has(span+span) {
                        &::before {
                            content:'';
                            display:block;
                            position:absolute;
                            top:0;
                            left:50%;
                            width:.1rem;
                            height:100%;
                            background:#EEEEEE;
                            transform: translateX(-50%);
                        }

                    }
                    > span {
                        flex:1;
                        padding:0 2.8rem 0 0;
                        color:#999999;
                        &::before {
                            display:none;
                        }
                        strong {
                            margin:1rem 0 0 0;
                            @include mix.txt-tit3;
                            font-weight:600;
                        }
                        &+span {
                            padding:0 0 0 2.8rem;
                        }
                        @include mix.respond-to(terminal2){
                            @include mix.txt-cap1;
                            strong {
                                @include mix.txt-body1;
                            }
                            &+span {
                                padding:0 0 0 1rem;
                            }
                        }
                    }
                }
                &::before {
                    content:'';
                    display:block;
                    width:2.4rem;
                    height:2.4rem;
                    margin-right:.8rem;
                    background-repeat:no-repeat;
                    background-size: 100% 100%;
                }
                &.ico-book {
                    &::before {
                        background-image:url('../images/icon/ico_book.svg');
                    }
                }
                &.ico-file {
                    &::before {
                        background-image:url('../images/icon/ico_file2.svg');
                    }
                }
                &.ico-clock {
                    &::before {
                        background-image:url('../images/icon/ico_clock2.svg');
                    }
                }
                @include mix.respond-to(mobile){
                    & + .cont {
                        flex-direction: row;
                        > span {
                            text-align:left;
                            line-height:1.671rem;
                            font-weight:500;
                        }
                    }
                }
            }
            &:has(.tit) {
                padding: 3rem;
            }
        }
        @include mix.respond-to(terminal){
            padding:2.4rem 0;
        }
        @include mix.respond-to(mobile){
            padding: 0 2rem;
            margin-top: 3.2rem;
            .box {
                padding:3.2rem;
                &:has(.tit) {
                    padding:2.4rem;
                }
                .cont {
                    > span {
                        strong {
                            margin-bottom:1.2rem;
                        }
                    }
                }
            }
        }
    }
    .round-box {
        display:flex;
        align-items:center;
        color:#666666;
        background-color:#F6F7FA;
        border-radius:2.4rem;
    }
    .status-wrap {
        display:flex;
        align-items:center;
        flex-wrap:wrap;
        gap:1.2rem;
        margin-top:2.4rem;
        @include mix.respond-to(mobile){
            margin-top:3.2rem;
            padding: 0 2rem;
        }
        .round-box {
            flex:0 0 auto;
            padding:1.2rem 1.6rem;
            @include mix.txt-cap1;
            font-weight:500;
            &::before {
                content:'#';
                margin-right:.3rem;
            }
        }
    }
    .func {
        position:relative;
        display: flex;
        align-items:center;
        justify-content: space-between;
        margin-top:4.8rem;
        z-index:1;
        > span {
            display: flex;
        }
        @include mix.respond-to(mobile){
            margin-top:3.2rem;
            padding:0 2rem;
        }
    }
    .tit-wrap {
        @include mix.respond-to(mobile){
            position:relative;
            .round-box {
                position:absolute;
                bottom:0;
                right:2rem;
            }
        }
    }
    .table-cont {
        position:relative;
        margin-top:2.4rem;
        &:has(.evaluate){
            margin-top: 0;
        }
        .desc {
            width:100%;
            position:absolute;
            top:-4.7rem;
            right:0;
            display:flex;
            align-items:center;
            justify-content: flex-end;
            color:#999999;
            font-weight:500;
            &::before {
                content:'';
                display:block;
                width:.4rem;
                height:.4rem;
                margin-right:.6rem;
                background:#999999;
                border-radius:.2rem;
            }
        }
        .btn {
            flex:0 0 auto;
            padding:1rem 1.6rem;
            color:#999999;
            font-weight:600;
            line-height:1.9rem;
            border-radius:.8rem;
            &.btn-primary {
                color: #A034FF;
                background: #F9ECFF;
            }
            &.btn-primary2 {
                color: #4E60FF;
                background: #E7EAFF;
            }
            &.btn-grey {
                background: #F6F7FA;
            }
        }
        .table {
            table {
                tbody {
                    tr {
                        td {
                            strong {
                                display: block;
                                font-weight: 600;
                            }
                            &.subject {
                                display: flex;
                                align-items:center;
                                justify-content: center;
                                padding:2rem 3rem;
                                .tit {
                                    text-align:center;
                                    strong {
                                        margin-top: .6rem;
                                        color: #333333;
                                        @include mix.txt-body3;
                                    }
                                }
                                &:has(.btn) {
                                    justify-content: space-between;
                                    .tit {
                                        padding-right:2rem;
                                        text-align:left;
                                    }
                                }
                            }
                            &.grade {
                                color:#EA4F4F;
                                font-weight:600;
                                line-height:1.9rem;
                                &.o {
                                    color:#4E60FF;
                                }
                            }
                        }
                    }
                }
            }
            &.evaluate{
                width:90rem;
                margin:0 auto;
                table{
                    thead{
                        th{
                            color: #999;
                            text-align: left;
                            padding-left: 2.95rem;
                            &:first-child {
                                font-weight:400;
                            }
                            &.font-primary{
                                color: #4E60FF;
                                text-align: left;
                            }
                        }
                    }
                    tbody{
                        td{
                            color: #999;
                            text-align: left;
                            padding-left: 2.95rem;
                            &:nth-of-type(2){
                                color: #666;
                                font-weight:600;
                            }
                        }
                    }
                }
                @include mix.respond-to(terminal){
                    width:auto;
                }
            }
        }
        @include mix.respond-to(mobile){
            position:static;
            .desc {
                position:static;
                align-items:flex-start;
                justify-content: flex-start;
                padding:0 2rem;
                @include mix.txt-cap1;
                &::before {
                    margin-top:.8rem;
                }
            }
        }
    }
    .evaluate-btn{
        width: 100%;
        text-align: center;
        padding-top: 1.6rem;
        > button{
            min-width: 16rem;
            padding:2rem;
            color:#fff;
            @include mix.txt-body4;
            background-color:#4E60FF;
            border-radius:5rem;
        }
    }
    .lecture-wrap {
        position:relative;
        margin-top:14rem;
        .latest-learn {
            position:static;
            padding:0;
            margin:0;
            border:0;
            overflow:visible;
            .curriculum {
                width: 100%;
                margin-top:4.8rem;
                padding:0;
                overflow:hidden;
                @include mix.respond-to(mobile){
                    margin-top:3.2rem;
                    padding:0 2rem;
                }
                &::before {
                    display:none;
                }
                &::after {
                    display:none;
                }
                &__item {
                    height:auto;
                    .category-wrap {
                        display:flex;
                        align-items:center;
                        justify-content: space-between;
                        gap:.8rem;
                        .open-sum{
                            @include mix.txt-body4;
                            color: #666;
                            font-weight: 500;
                            display: flex;
                            align-items: center;
                            &::after{
                                content: '';
                                width: 1.6rem;
                                height: 1.6rem;
                                display: inline-block;
                                mask-image: url('../images/icon/ico_exam-sum.svg');
                                background-color: #aaa;
                                margin-left: 0.4rem;
                            }
                        }
                    }
                    .curriculum-tit {
                        margin-top: 3.2rem;
                    }
                    .teacher-wrap {
                        display:flex;
                        align-items:center;
                        margin-top:4.8rem;
                        em {
                            display:flex;
                            align-items:center;
                            color:#999999;
                            font-weight:500;
                            line-height:1.9rem;
                            position: relative;
                            & + em {
                                flex:1;
                                color:#666666;
                                @include mix.text-overflow(1);
                                padding-left: 0.8rem;
                                margin-left: 0.8rem;
                                &::after {
                                    content:'';
                                    display:block;
                                    width:.1rem;
                                    height:1.2rem;
                                    background:#999999;
                                    position: absolute;
                                    left: 0;
                                    top: 50%;
                                    transform: translateY(-50%);
                                }
                            }
                        }
                    }
                }
            }
            .swiper-btn-wrap {
                position:absolute;
                top:0;
                bottom: auto;
                left:auto;
                right:0;
                @include mix.respond-to(mobile){
                    display:none;
                }
            }
        }
        @include mix.respond-to(mobile){
            margin-top:10rem;
        }
        &.curriculum-wrap {
            margin-top:9rem;
            .latest-learn {
                margin-top:3.6rem;
                .curriculum {
                    margin-top:0;
                }
            }
            @include mix.respond-to(mobile){
                margin-top: 3rem;
                .latest-learn {
                    margin-top:1.2rem;
                }
            }
        }
    }
    .current-wrap{
        > .box-wrap{
            .box{
                &.round-box{
                    flex-direction: row;
                }
            }
        }
    }
    .grade-list {
        position:relative;
        margin-top:14rem;
        @include mix.respond-to(mobile){
            margin-top:10rem;
        }
    }
    .deep-learning {
        .tab-mini {
            margin-top:4.8rem;
            padding-top:2.8rem;
            border-top: 1px solid #EEEEEE;
            @include mix.respond-to(mobile){
                margin-top:3rem;
            }
        }
    }
    .current-wrap {
        .box-wrap{
            .box{
                &.round-bg{
                    flex-direction: row;
                    @include mix.respond-to(terminal){
                        flex-direction: column;
                    }
                }
            }
        }
        .grade-wrap{
            width: 56%;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            > select{
                min-width: 22rem;
                & + select{
                    margin-left: 1rem;
                }
            }
            @media (max-width: 1280px) {
                > select{
                    min-width: auto;
                }
            }
            @include mix.respond-to(terminal){
                width: 100%;
                flex-direction: column;
                > select{
                    width: 100%;
                    & + select{
                        margin-left: 0;
                        margin-top: 1rem;
                    }
                }
            }
        }
        .episode{
            display: flex;
            align-items: center;
            padding-left: 4rem;
            flex:1 0 auto;
            > h5{
                @include mix.txt-body2;
                font-weight: 600;
                margin-right: 1rem;
            }
            @include mix.respond-to(terminal){
                width: 100%;
                padding-left: 0;
                margin-top: 2rem;
                flex-direction: column;
                > h5{
                    width: 100%;
                    margin-right: 0;
                    margin-bottom: 1rem;
                }
                select{
                    width: 100%;
                }
            }
        }
    }
    .retest-wrap{
        width: 100%;
        background: #F6F7FA;
        padding: 3rem 0;
        position: absolute;
        bottom: 0;
        left: 0;
        .inner{
            max-width: 128rem;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
        }
        &__select{
            display: flex;
            align-items: center;
            flex-wrap:wrap;
            > h5{
                @include mix.txt-body1;
                font-weight: 600;
                color: #333;
                margin-right:4rem;
            }
            .select-wrap {
                display:flex;
                gap:1rem;
            }
            select{
                min-width:16.6rem;
            }
        }
        .solve{
            @include mix.button_line(2rem 3rem, #4E60FF, 1.6rem, #4E60FF, 5rem);
            line-height: normal;
            height: 5.9rem;
            min-width: 16rem;
        }
        @media (max-width: 1280px) {
            .inner{
                padding: 0 2rem;
            }
        }
        @media (max-width: 940px) {
            .inner{
                align-items: flex-end;
            }
            &__select{
                > h5{
                    width: 100%;
                    flex: auto;
                    display: block;
                    margin-bottom: 1rem;
                }
            }
            .solve{
                margin-left: 2rem;
            }
        }
        @include mix.respond-to(mobile){
            .inner{
                width: 100%;
                flex-direction: column;
                align-items: normal;
            }
            &__select{
                display: inline-block;
                flex-direction: column;
                select{
                    width: 100%;
                    & + select{
                        margin-left: 0;
                    }
                }
            }
            .solve{
                width:100%;
                margin-left: 0;
                margin-top: 2rem;
            }
        }
        @media (max-width: 600px) {
            .select-wrap {
                display:block;
            }
            select {
                margin-top: 1rem;
            }
        }
    }
    &:has(.retest-wrap){
        padding-bottom: 26.5rem;
        @include mix.respond-to(mobile){
            padding-bottom: 48rem;
        }
    }
}

