@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body {
    &.dark-mode {
        .classic-wrap {
            .inner{
                > .tab-round{
                    .tab-wrap{
                        border: none;
                    }
                }
            }
            .comm-bot{
                background-color: #242424;
                > p{
                    color: #fff;
                }
                > button{
                    background-color: #444;
                }
            }
            .classic-status{
                &__cont{
                    .classic-tit{
                        color: #fff;
                    }
                }
            }
            .keyword-cont{
                .table{
                    .classic-tit{
                        color: #fff;
                    }
                }
            }
            &__study{
                .box-gray, .box-line{
                    background-color: #242424;
                    border:none;
                }
                .box-line{
                    > .title, > .cont{
                        color: #fff
                    }
                    .table-cont{
                        .table{
                            table{
                                thead{
                                    tr{
                                        th{
                                            background-color: #333;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .study-info{
                    color: #fff;
                }
            }
            .study-tit{
                color: #fff;
            }
        }
    }
}