@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body {
    &.dark-mode {
        .onestop, .classic-wrap {
            .viewer {
                &-wrap {

                }
                &__inner {

                }
                &__top {
                    .list {
                        &-item {
                            color:#FFFFFF;
                        }
                    }
                    .count {
                        color:#999999;
                    }
                    .box-wrap {
                        .box {
                            .btn-wrap {
                                .btn {
                                    &.border {
                                        color:#999999;
                                        background-color:#333333;
                                        border-color:#333333;
                                    }
                                }
                            }
                        }
                    }
                    .exam {
                        &-info {
                            border-color:#333333;
                            background-color:#333333;
                        }
                        &-time {
                            color:#FFFFFF;
                            &::before {
                                background-color:#FFFFFF;
                            }
                        }
                        &-count {
                            color:#FFFFFF;
                        }
                    }
                }
                &__body {
                    .btn-wrap {
                        .btn {
                            &.btn-prev, &.btn-next {
                                color:#FFFFFF;
                                border-color:#FFFFFF;
                                &::before, &::after {
                                    background-color:#FFFFFF;
                                }
                            }
                            &:disabled{
                                color:#999;
                                background-color:#333;
                                border: none;
                                &::before, &::after {
                                    background-color:#999;
                                }
                            }
                        }
                    }
                }
            }
            .retest-wrap {
                background-color:#242424;
                &__select {
                    > h5 {
                        color:#FFFFFF;
                    }
                }
                .solve {
                    color:#FFFFFF;
                    border-color:#333333;
                    background-color:#333333;
                }
            }
        }
    }
}