@use 'abstracts/variables' as var;

.tooltip {
    display: none; /*   */
    position: absolute;
    top: 100%; /*    */
    left: 50%;
    transform: translateX(-50%);
    padding: .8rem;
    background-color: #333;
    color: #fff;
    font-size: 1.1rem;
    text-align: left;
    border-radius:.4rem;
    white-space: nowrap;
    z-index: 10;
}

/*     */
.highlight:hover .tooltip {
    display: block;
}