@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

body {
    &.overflow-hidden {
        overflow:hidden;
    }
}
.onestop, .classic-wrap{
    width: 100%;
    position: relative;
    padding-bottom: 11.5rem;
    @include mix.respond-to(terminal){
        padding-top:6.1rem;
    }
    &:not(.latest-learn){
        .learning{
            margin-top: 14rem;
        }
    }
    &:has(.latest-learn){
        .learning{
            margin-top: 0;
        }
    }
    .inner{
        position: relative;
        max-width: 128rem;
        margin: 0 auto;
        padding:0 2rem;
        @include mix.txt-body4;
        &:has(.latest-learn) {
            @include mix.respond-to(mobile){
                padding-right:0;
                .curriculum {
                    padding-right:2rem;
                }
            }
        }
    }
    &.sub-main {
        .inner {
            &:has(.latest-learn) {
                overflow: hidden;
                @include mix.respond-to(mobile){
                    padding-right:0;
                    .curriculum {
                        padding-right:2rem;
                    }
                }
            }
            > .latest-learn {
                @include mix.respond-to(mobile){
                    margin: 10rem auto 0;
                }
            }
        }
        &:not(:has(.latest-learn)) {
            .learning {
                margin-top:14rem;
                @media (max-width: 869px) {
                    margin-top:10rem;
                }
            }
        }
    }
    .step-learn{
        width: 100%;
        padding-bottom: 12rem;
        background: linear-gradient(180deg, #FFF 30.4%, #98C8FF 100%);
        position: relative;
        overflow: hidden;
        &::before{
            content: '';
            width: 35.5rem;
            height: 35.5rem;
            display: inline-block;
            background: url('../images/img/bg_onestop_main_left.svg') 0 0 no-repeat;
            position: absolute;
            bottom: 17rem;
            left: -2rem;
            @include mix.respond-to(mobile){
                bottom: 39rem;
                left: -13rem
            }
        }
        &::after{
            content: '';
            width: 19.3rem;
            height: 27.2rem;
            display: inline-block;
            background: url('../images/img/bg_onestop_main_right.svg') 0 0 / 100% 100% no-repeat;
            position: absolute;
            bottom: 17.2rem;
            right: 13.7rem;
            z-index: 1;
            @include mix.respond-to(mobile){
                bottom: 9.2rem;
                right: -8.3rem;
            }
        }
        .txt-wrap{
            text-align: center;
            padding-top: 12rem;
            position: relative;
            h3{
                font-size: 6rem;
                font-weight: 700;
                line-height: 7rem;
                letter-spacing: -0.12rem;
                color: #333;
                > br{
                    display: none
                }
            }
            span{
                @include mix.txt-body2;
                font-weight: 400;
                color: #666;
                margin-top: 1.6rem;
                display: inline-block;
                > strong{
                    margin-left:.5rem;
                    font-weight: 600;
                }
            }
            @include mix.respond-to(mobile){
                padding-top: 8rem;
                h3{
                    font-size: 4.2rem;
                    font-weight: 700;
                    line-height: 5.2rem;
                    > br{
                        display: block;
                    }
                }
                span{
                    @include mix.txt-cap1;
                    font-weight: 400;
                    margin-top: 1.2rem;
                }
            }
        }
        .selection{
            width: 100%;
            position: relative;
            margin-top: 6rem;
            border: 1px solid rgba(255, 255, 255, 0.50);
            background: linear-gradient(93deg, rgba(255, 255, 255, 0.50) 9.96%, rgba(255, 255, 255, 0.10) 90.52%);
            backdrop-filter: blur(5px);
            border-radius: 2.8rem;
            overflow: hidden;
            z-index: 5;
            &__year{
                width: 100%;
                height: 7.2rem;
                // background: linear-gradient(93deg, rgba(255, 255, 255, 0.50) 9.96%, rgba(255, 255, 255, 0.10) 90.52%);
                // backdrop-filter: blur(5px);
                border-bottom: 1px solid rgba(255, 255, 255, 0.50);
                .list{
                    height: 7.2rem;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .item{
                    @include mix.txt-body2;
                    color: #bbb;
                    position: relative;
                    .active{
                        color: #222;
                        font-weight: 600;
                    }
                    & + .item{
                        margin-left: 2rem;
                        padding-left: 2rem;
                        &::after{
                            content: '';
                            width: .1rem;
                            height: 1.2rem;
                            display: inline-block;
                            background-color: #D3E0EE;
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            left: 0;
                        }
                    }
                }
                @include mix.respond-to(mobile){
                    .item{
                        @include mix.txt-body3;
                    }
                }
            }
            &__subject{
                width: 100%;
                padding: 4rem 2rem;
                .subject-box{
                    width: 88rem;
                    display: flex;
                    align-items: center;
                    margin: 0 auto;
                    .title{
                        width: 7rem;
                        @include mix.txt-body3;
                        font-weight: 600;
                        color: #555;
                    }
                    & + .subject-box{
                        margin-top: 2rem;
                    }
                    @include mix.respond-to(terminal){
                        width: 100%;
                        flex-direction: column;
                        .title{
                            width: 100%;
                            flex: auto;
                            margin-bottom: 0.6rem;
                            margin-left: 1rem;
                        }
                    }
                }
                .list{
                    width: 88rem;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    margin-left: -0.6rem;
                    @include mix.respond-to(terminal){
                        width: 100%;
                        flex-wrap: wrap;
                        justify-content: flex-start;
                        margin-left: -1rem;
                        margin-top: -1rem;
                    }
                }
                .item{
                    margin-left: 0.6rem;
                    &:nth-of-type(1){
                        margin-left: 0;
                    }
                    > button{
                        width: 13rem;
                        font-weight: 500;
                        line-height: normal;
                        @include mix.button_line(1rem 1.6rem, #ccc, 1.6rem, #999, 0.8rem);
                        background-color: #fff;
                        &.active{
                            background-color: #4E60FF;
                            color: #fff;
                            font-weight: 600 ;
                            border: none;
                        }
                    }
                    @include mix.respond-to(terminal){
                        margin-left: 1rem;
                        margin-top: 1rem;
                        &:nth-of-type(1){
                            margin-left: 1rem;
                        }
                    }
                }
            }
            @include mix.respond-to(mobile){
                margin-top: 10rem;
                &__subject{
                    .item{
                        width: calc(50% - 1rem);
                        margin-left: 1rem;
                        > button{
                            width: 100%;
                        }
                    }
                }
            }
        }
        .info-txt{
            @include mix.txt-body4;
            font-weight: 600;
            color: #fff;
            margin-top: 1.2rem;
            display: inline-block;
        }
        .danchoo-onestop{
            width: 18rem;
            height: 20rem;
            position: absolute;
            bottom: 16.8rem;
            right: 0;
            z-index: 10;
            > img{
                width: 100%;
            }
            @include mix.respond-to(terminal){
                display: none;
            }
            &__top{
                width: 7.23rem;
                height: 6.02rem;
                position: absolute;
                bottom: -6.4rem;
                left: 7rem;
                z-index: 10;
                display: none;
                > img{
                    width: 100%;
                }
                @include mix.respond-to(terminal){
                    bottom: -6.7rem;
                    left: 3.4rem;
                    display: block;
                }
                @include mix.respond-to(mobile){
                    bottom: -10.8rem;
                }
            }
        }
    }
    .quick{
        width: 100%;
        height: 11.3rem;
        background: #333;
        display: flex;
        align-items: center;
        .tag-wrap{
            display: flex;
            align-items: center;
        }
        > .inner{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
        }
        h3{
            @include mix.txt-tit2;
            font-weight: 700;
            color: #fff;
        }
        &__tag{
            padding: 1.2rem 3.2rem;
            border: 1px solid #fff;
            border-radius: 0.8rem;
            display: flex;
            margin: 0 2rem;
            > span{
                margin-right: 1.2rem;
                @include mix.txt-body2;
                font-weight: 500;
                color: #fff;
                &:last-child {
                    margin-right:0;
                }
                &.disabled{
                    color: #999;
                }
            }
        }
        button.start{
            padding:1.6rem 5rem;
            color:#fff;
            @include mix.txt-body3;
            background-color:#4E60FF;
            border-radius:5rem;
            &:disabled{
                background-color: #666;
                color: #999;
            }
        }
        button.modify{
            @include mix.button_line(1.6rem 5rem, #666, 1.8rem, #CCC, 5rem);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 2rem;
            @media (max-width: 1280px) {
                right: 2rem;
            }
        }
        &.move-up{
            position: absolute;
            bottom: -14rem;
        }
        @include mix.respond-to(terminal2){
            button.start,
            button.modify {
                padding:1.6rem 4.3rem;
            }
        }
        @include mix.respond-to(terminal){
            > .inner{
                justify-content: flex-start;
            }
            button.start,
            button.modify {
                padding:1.6rem 5rem;
            }
        }
        @media (max-width: 900px) {
            button.start,
            button.modify {
                padding:1.6rem 3rem;
            }
        }
        @media (max-width: 820px) {
            height: auto;
            padding: 3rem 0;
            .inner{
                flex-wrap: wrap;
                justify-content: center;
                .tag-wrap{
                    width: 100%;
                    flex: auto;
                    justify-content: center;
                    margin-bottom: 2rem;
                    .quick__tag{
                        flex: 1;
                        flex-wrap:wrap;
                        margin: 0 0 0 2rem;
                        > span {
                            line-height:2rem;
                        }
                    }
                }
                button.modify{
                    position: initial;
                    transform: initial;
                }
                button.start{
                    width: 100%;
                }
                button.modify{
                    width: calc(50% - 0.5rem);
                    margin-left: 1rem;
                }
                &:has(.modify){
                    button.start{
                        width: calc(50% - 0.5rem);
                    }
                }
            }
        }
        @include mix.respond-to(mobile){
            padding: 3rem 0;
            h3{
                @include mix.txt-body1;
                font-weight: 700;
            }
            .inner {
                .tag-wrap {
                    display:block;
                    h3 {
                        text-align:center;
                    }
                    .quick__tag{
                        margin:1rem 0 0;
                        padding: 0.8rem 1.6rem;
                        border-radius: 0.8rem;
                        justify-content: center;
                        > span{
                            @include mix.txt-cap1;
                        }
                    }
                }

            }
            button.start,
            button.modify{
                padding: 1.4rem 0;
            }
        }
    }
    .status{
        margin-top: 14rem;
        display: flex;
        align-items: center;
        &__graph{
            width: 64%;
            height: 50.4rem;
            position: relative;
            border: 1px solid #EEE;
            border-radius: 2.8rem;
            padding: 4rem;
            > h4{
                @include mix.txt-body1;
                font-weight: 600;
                color: #333;
            }
            .chart-container{
                margin-top: 2rem;
            }
            .description{
                display: none;
            }
            &.first{
                overflow: hidden;
                position: relative;
                &::after{
                    content: '';
                    width: 100%;
                    height: 100%;
                    display: inline-block;
                    background: url('../images/img/img_status-graph_first.svg') 0 0 / 105% auto no-repeat;
                    position: absolute;
                    top: -0.1rem;
                    left: -0.1rem;
                }
                .description{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50% , -50%);
                    z-index: 10;
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    > img{
                        width: 11.4rem;
                        height: 11.4rem;
                    }
                    > p{
                        @include mix.txt-body1;
                        font-weight: 500;
                        color: #fff;
                        padding-top: 1.6rem;
                        text-align: center;
                    }
                }
            }
            @include mix.respond-to(mobile){
                width: 100%;
                height: 49.2rem;
                overflow-x: auto;
                padding: 2.4rem;
                position: relative;
                h4{
                    @include mix.txt-body3;
                    font-weight: 600;
                    position: sticky;
                    left: 0;
                    top: 0;
                }
                .chart-container{
                    width: 82rem;
                    margin-top: 0;
                }
            }
        }
        &__banner{
            width: calc(37% - 2rem);
            height: 50.4rem;
            margin-left: 2rem;
            border-radius: 2.8rem;
            background: #A0C0FF;
            padding: 4rem;
            position: relative;
            @media (max-width: 952px) {
                padding: 4rem 3rem;
            }
            > em{
                @include mix.txt-body3;
                font-weight: 500;
                color: #EDF3FF;
            }
            > h4{
                @include mix.txt-tit2;
                font-weight: 600;
                color: #fff;
                margin-top: 1.6rem;
                @media (max-width: 1333px) {
                    br{
                        display: none;
                    }
                }
                @media (max-width: 1190px) {
                    @include mix.txt-tit3;
                }
                @media (max-width: 1090px) {
                    @include mix.txt-tit4;
                }
                @media (max-width: 1024px) {
                    @include mix.txt-tit2;
                }
            }
            > img{
                width: 24rem;
                height: 19.2rem;
                margin: 0 auto;
                display: block;
                padding-top: 2.5rem;
                @media (max-width: 1024px) {
                    width: 75%;
                    height: auto;
                    // padding-top: 0;
                }
            }
            .btn-wrap{
                display: flex;
                padding-top: 4.5rem;
                .diagnosis{
                    padding:1.6rem 2.4rem;
                    color:#4E60FF;
                    @include mix.txt-body4;
                    background-color:#fff;
                    border-radius:5rem;
                    @media (max-width: 1190px) {
                        padding: 1.6rem 1rem;
                    }
                    @media (max-width: 1001px) {
                        @include mix.txt-cap1;
                    }
                }
                .retest{
                    padding:1.6rem 2.4rem;
                    color:#fff;
                    @include mix.txt-body4;
                    background-color:#4E60FF;
                    border-radius:5rem;
                    @media (max-width: 1190px) {
                        padding: 1.6rem 1rem;
                    }
                    @media (max-width: 1001px) {
                        @include mix.txt-cap1;
                    }
                }
                button{
                    flex: 1;
                    font-weight: 600;
                    & + button{
                        margin-left: 1.7rem;
                        @media (max-width: 1001px) {
                            margin-left: 1rem;
                        }
                    }
                }
            }
            &.first{
                background-color: #FAB96F;
                > em{
                    color: #FFF5E9;
                }
                > img{
                    position: absolute;
                    bottom: 4rem;
                    right: 4rem;
                }
            }
        }
        @media (max-width: 869px) {
            flex-direction: column;
            margin: 10rem auto 0;
            &__graph{
                width: 100%;
            }
            &__banner{
                width: 100%;
                height: auto;
                margin-left: 0;
                margin-top: 2rem;
                h4{
                    br{
                        display: block;
                    }
                }
                img{
                    width: 23rem;
                    position: absolute;
                    top: 2rem;
                    right: 4rem;
                }
                .btn-wrap{
                    padding-top: 7rem;
                }
            }
        }
        @include mix.respond-to(mobile){
            &__banner{
                padding: 3.2rem;
                > em{
                    @include mix.txt-cap1;
                    font-weight: 500;
                }
                h4{
                    @include mix.txt-body2;
                    margin-top: 1.6rem;
                }
                img{
                    width: 80%;
                    position: inherit;
                    top: inherit;
                    right: inherit;
                    margin-top: 2.6rem;
                }
                .btn-wrap{
                    padding-top: 4.8rem;
                    flex-direction: column;
                    .diagnosis, .retest{
                        width: 100%;
                        margin-left: 0;
                    }
                    .retest{
                        margin-top: 1rem;
                    }
                }
            }
        }
    }
    .latest-learn{
        width: 100%;
        max-width: 128rem;
        position: relative;
        border: 1px solid #EEE;
        border-radius: 2.8rem;
        margin: 14rem auto;
        padding: 4rem;
        display: flex;
        justify-content: space-between;
        overflow: hidden;
        > h4{
            width: 30%;
            @include mix.txt-tit1;
            font-weight: 700;
            letter-spacing: 0.072rem;
            color: #333;
            z-index: 10;
            background: linear-gradient(-270deg, #FFF 81.27%, rgba(255, 255, 255, 0.00) 100%);
            position: relative;
            &::before{
                content: '';
                width: 5rem;
                height: 100%;
                display: inline-block;
                background: #fff;
                position: absolute;
                top: 0;
                left: -5rem;
                z-index: 10;
                transition:opacity .5s;
            }
        }
        .curriculum{
            width: 70%;
            transition:padding .5s;
            overflow: visible;
            &::before{
                content: '';
                width: 5rem;
                height: 100%;
                display: inline-block;
                background: #fff;
                position: absolute;
                top: 0;
                right: -5rem;
                z-index: 10;
                transition:opacity .5s;
            }
            &::after{
                content: '';
                width: 11rem;
                height: 100%;
                display: inline-block;
                background: linear-gradient(270deg, #FFF 7.27%, rgba(255, 255, 255, 0.00) 100%);
                position: absolute;
                top: 0;
                right: 0;
                z-index: 10;
            }
            &__list{
                position: relative;
                display: flex;
                align-items: center;
            }
            &__item{
                height: 31rem;
                border: 1px solid #EEE;
                padding: 3.2rem;
                border-radius: 1.6rem;
                .category{
                    width: fit-content;
                    padding: 0.6rem 1rem;
                    border-radius: 0.8rem;
                    background-color: #F6F7FA;
                    display: flex;
                    align-items: center;
                    > em{
                        @include mix.txt-cap1;
                        font-weight: 600;
                        color: #999;
                        position: relative;
                        & + em{
                            margin-left: 0.6rem;
                            padding-left: 0.6rem;
                            &::before{
                                content: '';
                                width: .1rem;
                                height: 1rem;
                                display: inline-block;
                                background-color: #ccc;
                                position: absolute;
                                top: 50%;
                                transform: translateY(-50%);
                                left: 0;
                            }
                        }
                    }
                }
                .curriculum-tit{
                    margin-top: 4rem;
                    .sub-tit{
                        @include mix.txt-body4;
                        font-weight:600;
                        color: #666;
                        @include mix.text-overflow(1);
                    }
                    h5{
                        height:6rem;
                        margin-top: 1rem;
                        @include mix.txt-body1;
                        font-weight:600;
                        @include mix.text-overflow(2);
                    }
                }
                .go-study{
                    width:100%;
                    margin-top:2.4rem;
                    padding:2.2rem 0;
                    color:#4E60FF;
                    @include mix.txt-body4;
                    background-color:#E7EAFF;
                    border-radius:5rem;
                    font-weight:600;
                    &:focus {
                        outline:none;
                    }

                }
                &:hover{
                    padding:3.1rem;
                    border:2px solid #4E60FF;
                    .category{
                        > em{
                            color: #4E60FF;
                        }
                    }
                    .go-study{
                        background-color: #384BEC;
                        color: #fff;
                    }
                }
            }
        }
        .swiper-btn-wrap{
            position: absolute;
            display: flex;
            align-items: center;
            bottom: 4rem;
            left: 4rem;
            .swiper-button-next{
                width: 4rem;
                height: 4rem;
                border-radius: 50%;
                background-color: #f6f7fa;
                left: initial;
                right: initial;
                top: initial;
                bottom: initial;
                position: relative;
                margin-top: initial;
                &::after{
                    content: '';
                    width: 1.8rem;
                    height: 1.8rem;
                    display: inline-block;
                    background: url('../images/icon/ico_arr_right.svg') 0 0 no-repeat;
                }
                margin-left: 0.8rem;
                &.swiper-button-disabled{
                    opacity: 0.7;
                }
            }
            .swiper-button-prev{
                width: 4rem;
                height: 4rem;
                border-radius: 50%;
                background-color: #f6f7fa;
                left: initial;
                right: initial;
                top: initial;
                bottom: initial;
                position: relative;
                margin-top: initial;
                &::after{
                    content: '';
                    width: 1.8rem;
                    height: 1.8rem;
                    display: inline-block;
                    background: url('../images/icon/ico_arr_right.svg') 0 0 no-repeat;
                    transform: rotate(-180deg)
                }
                &.swiper-button-disabled{
                    opacity: 0.7;
                }
            }
        }
        @include mix.respond-to(mobile){
            margin: 0 auto;
            border: none;
            flex-direction: column;
            padding: 0;
            overflow: visible;
            h4{
                width: 100%;
                @include mix.txt-tit3;
                font-weight: 700;
                > br{
                    display: none;
                }
            }
            .curriculum{
                width: 100%;
                margin-top:3.2rem;
                padding:0;
                &::before,
                &::after{
                    display: none;
                }
            }
            .swiper-btn-wrap{
                display: none;
            }
        }
        // &:has(.swiper-button-prev.swiper-button-disabled) {

        //         h4{
        //             &::after{
        //                 display:none;
        //             }
        //         }

        // }
        &:has(.swiper-button-next.swiper-button-disabled) {
            .curriculum {
                &::after{
                    display:none;
                }
                &::before{
                    display:none;
                }
            }
        }
    }
    .learning{
        &__top{
            display: flex;
            h4{
                @include mix.txt-tit1;
                font-weight: 700;
                color: #333;
            }
            .round-box {
                width: fit-content;
                padding:1.4rem 1.8rem;
                @include mix.txt-body4;
                font-weight:500;
                margin-left: 2rem;
                > span {
                    display:flex;
                    align-items:center;
                    margin-left:.5rem;
                    @include mix.txt-body4;
                    font-weight:500;
                    > em {
                        display:block;
                        margin-left:.8rem;
                        @include mix.txt-body4;
                        color: #333;
                        font-weight: 600;
                    }
                    > strong {
                        display:block;
                        margin-left:.8rem;
                        color:#4E60FF;
                        @include mix.txt-body4;
                        font-weight: 600;
                    }
                    & + span{
                        margin-left: 1.2rem;
                        padding-left: 1.2rem;
                        position: relative;
                        &::after{
                            content: '';
                            width: .1rem;
                            height: 1rem;
                            display: inline-block;
                            background-color: #ccc;
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            left: 0;
                        }
                    }
                }
                @include mix.respond-to(mobile){
                    position:static;
                    padding:0;
                    background:none;
                }
            }
        }
        &__subject{
            width: 100%;
            border: 1px solid #EEE;
            border-radius: 2.8rem;
            padding: 3.2rem 0;
            margin-top: 4.8rem;
            .list{
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .item{
                margin-left: 3rem;
                padding-right: 3rem;
                position: relative;
                > button{
                    @include mix.txt-body2;
                    font-weight: 400;
                    color: #999;
                    &.active{
                        color: #333;
                        font-weight: 600;
                    }
                }
                &::after{
                    content: '';
                    width: .1rem;
                    height: 1.4rem;
                    display: inline-block;
                    background-color: #EEE;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0;
                }
                &:last-of-type{
                    &::after{
                        display:none;
                        margin-right: 0;
                    }
                }
            }
            .table-cont{
                border: 1px
            }
        }
        .unit-list{
            width: 100%;
            border: 1px solid #EEE;
            border-radius: 2.8rem;
            padding: 4rem;
            margin-top: 2rem;
            > .table-cont{
                margin-top: 0;
                > .table{
                    margin-top:2.8rem;
                    padding-bottom: 0;
                    .btn {
                        width:11rem;
                    }
                }
                .func{
                    margin-top:0;
                    .tag{
                        .list{
                            display: flex;
                            align-items: center;
                            margin-left: -0.6rem;
                        }
                        .item{
                            margin-left: 0.6rem;
                            > button{
                                padding: 1rem 2rem;
                                @include mix.txt-body4;
                                font-weight: 400;
                                color: #999;
                                border: 1px solid #eee;
                                border-radius: 5rem;
                                &.active{
                                    background-color: #4E60FF;
                                    color: #fff;
                                    font-weight: 500;
                                }
                            }
                        }
                    }
                }
            }
        }
        @include mix.respond-to(mobile){
            width: 100%;
            margin-top: 10rem;
            &__top{
                flex-direction: column;
                h4{
                    width: 100%;
                    @include mix.txt-tit3;
                }
                .round-box{
                    margin-left: 0;
                    margin-top: 4rem;
                }
            }
            &__subject{
                margin-top: 1rem;
                padding:3rem 0;
                .list{
                    flex-wrap: wrap;
                }
                .item{
                    width:calc(33.33% - 1rem);
                    margin:0 0 1.6rem;
                    padding:0;
                    line-height:2.5rem;
                    text-align:center;
                    &:nth-child(3) {
                        &::after {
                            display:none;
                        }
                    }
                    &:nth-child(4),
                    &:nth-child(5),
                    &:nth-child(6) {
                        margin-bottom:0;
                    }
                }
            }
            .unit-list{
                padding: 2.4rem;
                .table-cont{
                    .func{
                        flex-direction: column;
                        padding: 0;
                        align-items: flex-start;
                        > span{
                            margin-top: 2rem;
                        }

                        .tag{
                            .list{
                                flex-wrap: wrap;
                                margin-top: -1rem;
                                margin-left: -1rem;
                            }
                            .item{
                                margin-top: 1rem;
                                margin-left: 1rem;
                                button{
                                    padding: 1rem 2rem;
                                    @include mix.txt-body4;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .grade-list {
        width: 100%;
        max-width: 128rem;
        position:relative;
        margin:14.3rem auto 0;
    }
    &.move-up{
        margin-bottom: 14rem;
    }
}
.state{
    padding: 0.6rem 1rem;
    border-radius: 0.8rem;
    @include mix.txt-cap1;
    font-weight: 500;
    &.studying{
        color: #FA7A16;
        background-color: #FFEFDE;
    }
    &.complete{
        color: #4D91E5;
        background-color: #E7EEFF;
    }
}
.accuracy-wrap{
    margin-top: 14rem;
    @include mix.respond-to(mobile){
        margin-top: 10rem;
        > h4.title{
            .info{
                .tooltip-box{
                    top: -12rem;
                }
            }
        }
    }
}
.subject-wrap{
    margin-top: 14rem;
    @include mix.respond-to(mobile){
        margin-top: 10rem;
    }
}
.chart-wrap {
    > h4.title{
        display: flex;
        align-items: center;
        .info {
            position: relative;
            width: 2.8rem;
            height: 2.8rem;
            background: url('../images/icon/ico_mark.svg') 0 0 /100% 100% no-repeat;
            margin-left: 0.8rem;
            cursor: pointer;
            > .tooltip-box{
                top: auto;
                bottom:3.8rem;
                left: -0.5rem;
                &::before{
                    transform: rotate(-180deg);
                    top: 100%;
                }
            }
        }
    }
    @include mix.respond-to(mobile){
        >h4.title {
            .info{
                .tooltip-box{
                    width: 15rem;
                    left: -16.5rem;
                    &::before{
                        left: 17.4rem;
                    }
                }
            }
            p{
                > br{
                    display: none;
                }
            }
        }
        > .box-wrap{
            .box{
                &.round{
                    overflow-x: auto;
                }
            }
        }
    }
}