@use "../abstracts/variables" as var;
@use "../abstracts/variables" as mix;

.viewer-wrap {
    .viewer {
        &__inner {
            display:flex;
            align-items:flex-start;
            justify-content: space-between;
            @include mix.respond-to(mobile){
                padding:0;
                gap:2rem;
                flex-direction: column;
                justify-content: flex-start;
                align-items:baseline;
            }
        }
        &__top {
            .list {
                width:100%;
                &-item {
                    color:#666666;
                    @include mix.txt-body4;
                    strong {
                        color:#333333;
                        @include mix.txt-body2;
                        font-weight:600;
                    }
                    span {
                        display:block;
                        font-weight:500;
                        line-height:1.9rem;
                        &+strong {
                            display:block;
                            margin-top:.8rem;
                        }
                    }
                }
                &-wrap {
                    display:flex;
                    margin-top:1.2rem;
                    flex-wrap:wrap;
                    @include mix.respond-to(terminal){
                        gap:1.2rem;
                        flex-direction: column;
                        justify-content: flex-start;
                        align-items:baseline;
                    }
                    .list-item {
                        display:flex;
                        align-items:center;
                        &+.list-item {
                            &::before {
                                content:'';
                                width:.1rem;
                                height:1.2rem;
                                margin:0 1.6rem;
                                background:#CCCCCC;
                            }
                        }
                        @include mix.respond-to(terminal){
                            align-items:flex-start;
                            &+.list-item {
                                &::before {
                                    display:none;
                                }
                            }
                        }
                    }
                }
            }
            .page-title {
                width:auto;
                padding: 0 3rem;
                h3 {
                    flex:1 0 auto;
                }
            }
            .count {
                display:flex;
                align-items:center;
                justify-content: flex-end;
                flex:1 0 auto;
                padding: 0 3rem;
                color:#666666;
                @include mix.txt-body4;
                font-weight:400;
                em {
                    margin:0 .1rem 0 .3rem;
                    font-weight:600;
                }
                .count-now {
                    display:flex;
                    align-items:center;
                    &::after {
                        content:'/';
                        display:block;
                        margin:0 .6rem;
                        @include mix.txt-cap1;
                        font-weight:600;
                    }
                }
                .count-today {
                    position:relative;
                    display:flex;
                    align-items:center;
                    em {
                        color:#4E60FF;
                    }
                    .tooltip-box {
                        left:auto;
                        right:-1rem;
                        &::before {
                            left:auto;
                            right:1.2rem;

                        }
                    }
                }
                .btn-tooltip {
                    width:1.6rem;
                    height:1.6rem;
                    margin-left:.4rem;
                    background:url('../images/icon/ico_question.svg') no-repeat;
                    background-size: 100% 100%;
                    text-indent:-9999em;
                    overflow:hidden;
                }
                @include mix.respond-to(mobile){
                    position:relative;
                    width:100%;
                    justify-content: flex-start;
                    .count-today {
                        position:static;
                        .tooltip-box {
                            right:0;
                            left:50%;
                            width:100%;
                            transform:translateX(-50%);
                            &::before {
                                left:1rem;
                                right:auto;
                            }
                        }
                    }
                }
            }
            .box-wrap {
                margin-top:2rem !important;
                @include mix.respond-to(terminal){
                    padding:0;
                }
                .box {
                    padding:3rem;
                    justify-content: space-between;
                    flex-direction: row;
                    .btn-wrap {
                        margin-top:0;
                        .btn {
                            width:9rem;
                            @include mix.txt-cap1;
                            font-size:1.4rem !important;
                            padding:1.4rem 0;
                            line-height:2.2rem !important;
                        }
                    }
                    @include mix.respond-to(mobile){
                        gap:2rem;
                        flex-direction: column;
                        .btn-wrap {
                            width:100%;
                            .btn {
                                width:100%;
                            }
                        }
                    }
                }
            }
            .exam {
                &-info {
                    display:flex;
                    align-items:center;
                    justify-content:flex-end;
                    gap:2rem;
                    margin-top:2rem;
                    padding:3rem;
                    color:#666666;
                    @include mix.txt-body4;
                    font-weight:400;
                    border:1px solid #EEEEEE;
                    border-radius:1.6rem;
                    @include mix.respond-to(terminal){
                        flex-direction: column;
                        gap:1rem;
                    }
                    @include mix.respond-to(mobile){
                        padding:2rem;
                    }
                    &:has(.exam-time) {
                        &:has(.exam-count) {
                            flex-wrap:wrap;
                            justify-content:space-between;
                            gap:1rem;
                        }
                    }
                    &:not(:has(.exam-time)){
                        justify-content:flex-end;
                    }
                    &:not(:has(.exam-count)){
                        justify-content:space-between;
                        &::after {
                            content:'';
                            display:block;
                        }
                    }
                }
                &-time {
                    display:flex;
                    align-items:center;
                    font-weight:500;
                    em {
                        margin-left:.8rem;
                        color:#4E60FF;
                        font-weight:600;
                    }
                    &::before {
                        content:'';
                        display:block;
                        width:2.4rem;
                        height:2.4rem;
                        margin-right:.4rem;
                        background-color:#666666;
                        mask-image:url('../images/icon/ico_clock3.svg');
                        mask-repeat:no-repeat;
                        mask-position:center center;
                        mask-size:100% 100%;
                    }
                    @include mix.respond-to(terminal){
                        width: 100%;
                    }
                }
                &-level {
                    display:flex;
                    align-items:center;
                    em {
                        display:block;
                        color:#999999;
                        @include mix.txt-cap1;
                        font-weight:500;
                    }
                    progress {
                        display:block;
                        width:50rem;
                        height:1rem;
                        margin:0 2rem;
                        border:0;
                        border-radius:.5rem;
                        overflow:hidden;
                        transition:width .5s;
                        &::-webkit-progress-bar {
                            background: #EDEFF7;
                        }
                        &::-webkit-progress-value {
                            background: linear-gradient(90deg, #51C5FF 0%, #4E60FF 100%);
                            border-radius:.5rem;
                            transition:width .5s;
                        }
                        @media (max-width: 1280px) {
                            width:32rem;
                        }
                        @media (max-width: 1100px) {
                            width:24rem;
                        }
                    }
                    @include mix.respond-to(terminal){
                        width:100%;
                        justify-content:space-between;
                        progress {
                            flex:1;
                        }
                    }
                }
                &-count {
                    span {
                        margin-left:.5rem;
                        font-weight:600;
                    }
                    em {
                        margin-left:.5rem;
                        color:#4E60FF;
                        font-weight:600;
                    }
                    @include mix.respond-to(terminal){
                        width: 100%;
                    }
                }
            }
            @include mix.respond-to(mobile){
                padding:2rem;
                .page-title,
                .count,
                .box-wrap {
                    padding:0;
                }
            }
        }
        &__body {
            margin-top: 6rem;
            .viewer {
                background:rgba(234, 79, 79, .5);
                @include mix.respond-to(mobile){
                    padding:0 2rem;
                }
            }
            .btn-wrap {
                position:relative;
                margin-top:8rem;
                .btn {
                    display:flex;
                    align-items:center;
                    justify-content: center;
                    gap:.6rem;
                    width:16rem;
                    padding:2rem 0;
                    @include mix.txt-body4;
                    color:#FFFFFF;
                    background-color:#4E60FF;
                    border-radius:5rem;
                    font-weight:600;
                    &::after,
                    &::before {
                        width:1.8rem;
                        height:1.8rem;
                        mask-repeat:no-repeat;
                        mask-position:center center;
                        mask-size:100% 100%;
                    }
                    &.border {
                        color:#4E60FF;
                        border:.1rem solid #4E60FF;
                        background-color:#FFFFFF;
                    }
                    &.btn-prev {
                        justify-content: flex-start;
                        padding-left:4.8rem;
                        color:#4E60FF;
                        border:.1rem solid #4E60FF;
                        background-color:transparent;
                        &::before {
                            content:'';
                            margin-right:.6rem;
                            mask-image:url('../images/icon/ico_arr_left_blue.svg');
                            background-color:#4E60FF;
                        }
                    }
                    &.btn-next {
                        justify-content: flex-start;
                        padding-left:5.9rem;
                        color:#4E60FF;
                        border:.1rem solid #4E60FF;
                        background-color:transparent;
                        &::after {
                            content:'';
                            margin-left:.6rem;
                            mask-image:url('../images/icon/ico_arr_left_blue.svg');
                            background-color:#4E60FF;
                            transform: rotate(-180deg);
                        }
                    }
                    &:disabled{
                        color:#999999;
                        background-color:#EEEEEE;
                        border: none;
                        &::before,
                        &::after {
                            background-color:#999999;
                        }
                    }
                }
                .btn__inner {
                    display:flex;
                    gap:1.6rem;
                    justify-content: center;
                }
                > button {
                    position:absolute;
                    bottom:0;
                    right:0;
                }
                @include mix.respond-to(mobile){
                    display:block;
                    padding:0 2rem;
                    .btn {
                        &.border,
                        &.btn-prev,
                        &.btn-next {
                            width:50%;
                            padding-left:0;
                            justify-content: center;
                        }
                    }
                    > button {
                        &.btn {
                            width:100%;
                            position:static;
                            margin-top:2rem;
                        }
                    }
                }
            }
        }
    }
    .status-wrap {
        display:flex;
        align-items:center;
        flex-wrap:wrap;
        gap:1.2rem;
        margin-top:2.4rem;
        @include mix.respond-to(mobile){
            margin-top:3.2rem;
            padding: 0 2rem;
        }
        .round-box {
            flex:0 0 auto;
            padding:1.2rem 1.6rem;
            @include mix.txt-cap1;
            font-weight:500;
            &::before {
                content:'#';
                margin-right:.3rem;
            }
        }
    }
}

